 какие разделы?': [{'Водопровод.': [true, false], 'канализация.': [true, false], 'Отопление.': [true, false], 'Вентиляция и кондиционирование воздуха.': [true, false], 'Слаботочные устройства.': [false, false], 'Приобретение и монтаж санитарнотехнического оборудования.': [false, false]}, false, false]}, {'Что понимается под понятием «объектные сметы»?': [{'Объединяют в своем составе на объект в целом данные из локальных смет.': [true, false], 'Составляются на основе сметных расчетов на отдельные виды затрат.': [false, false], 'Являются первичными сметными документами и составляются на отдельные виды работ и затрат по зданиям и сооружениям или по общеплощадочным работам на основе объемов, определившихся при разработке рабочей документации (рд), рабочих чертежей.': [false, false], 'Специальный документ, объединяющий в себе количественно-материальные данные по одному или нескольким объектам, отраженные в локальных сметах. И являются базовыми для определения договорной цены на конкретные сооружения или этапы работ.': [true, false], 'Это затраты по одному или нескольким сооружениям, видам работ.': [true, false], 'Это своя смета затрат каждого предприятие даже если они в составе жилого помещения.': [true, false]}, false, false]}, {'ФЕР предназначены для определения стоимости каких работ?': [{'Пусконаладочные работы.': [true, false], 'Ремонтных работ.': [false, false], 'Ремонтно-реставрационных работ.': [false, false], 'Монтажных работ.': [false, false], 'Пусконаладочных работ по электротехническим устройствам.': [true, false], 'Монтажных работ по электротехническим устройствам.': [false, false]}, false, false]}, {'В каких ценах составляется сметная документация?': [{'В сметных ценах, сложившихся ко времени составления сметной документации.': [true, false], 'В прогнозных ценах на дату окончания строительства объекта.': [false, false], 'В базисных ценах.': [false, false], 'С применением базисного уровня цен.': [true, false], 'С применением ресурсного метода определения стоимости строительства.': [true, false], 'Только с применением ресурсного метода определения стоимости строительства.': [false, false]}, false, false]}, {'Для каких целей используются общеотраслевые нормативы сметной прибыли?': [{'Для технико-экономического обоснования проекта.': [true, false], 'Для разработки инвесторских смет.': [false, false], 'Определения начальной (стартовой) цены предмета конкурса при проведении подрядных торгов.': [true, false], 'Для конкретной строительно-монтажной организации индивидуальным расчетом.': [false, false], 'По согласованию между техническим заказчиком и/или застройщиком и подрядчиком указанные нормативы сметной прибыли могут применяться на стадии разработки рабочей документации и расчетах за выполненные работы.': [true, false], 'По видам строительных и монтажных работ.': [false, false]}, false, false]}, {'В главу двенадцатую сводного сметного расчета «проектные и изыскательские работы, авторский надзор» что включается?': [{'Средства на выполнение проектных и изыскательских работ.': [true, false], 'Средства на содержание службы технического надзора.': [false, false], 'Средства на разборку временных зданий и сооружений.': [false, false], 'Средства на испытание свай.': [true, false], 'Средства на проведение авторского надзора.': [true, false], 'Средства на проведение экспертизы предпроектной и проектной документации.': [true, false]}, false, false]}, {'Применение каких лесоматериалов предусмотрено сборниками ФЕР?': [{'В сборниках фер предусмотрено выполнение работ с применением лесоматериалов мягких пород (сосны, ели, пихты и т.п.).': [true, false], 'В сборниках фер предусмотрено выполнение работ с применением лесоматериалов твердых пород (дуба, граба, бука и т.п.).': [false, false], 'В сборниках фер предусмотрено выполнение работ с применением лесоматериалов разных пород деревьев.': [false, false], 'В сборниках фер предусмотрено выполнение работ с применением лесоматериалов мягких пород с, а для других следует применять следующие коэффициенты: для лесоматериалов из лиственницы, березы - 1,1; для лесоматериалов из дуба, бука, граба, ясеня - 1,2.': [true, false], 'В сборниках фер предусмотрено выполнение работ с применением лесоматериалов мягких пород, а для других следует применять следующие коэффициенты: для лесоматериалов из лиственницы, березы - 1,2; для лесоматериалов из дуба, бука, граба, ясеня - 1,3.': [false, false], 'В сборниках фер предусмотрено выполнение работ с применением лесоматериалов мягких пород, а для других следует применять следующие коэффициенты: для лесоматериалов из лиственницы, березы - 1,2; для лесоматериалов из дуба, бука, граба, ясеня - 1,1.': [false, false]}, false, false]}, {'Что служит основой для определения стоимости строительства базисно-индексным методом?': [{'Сметные расчеты (сметы), составленные в базисном уровне цен с использованием нормативов действующей сметнонормативной базы и системы текущих (прогнозных) индексов.': [true, false], 'Система текущих и прогнозных индексов по отношению к стоимости, определенной в базисном уровне.': [false, false], 'Стоимость работ в базисном уровне цен и пересчет их в текущий (прогнозный) уровень цен с использованием системы текущих (прогнозных) индексов.': [true, false], 'Сочетание базисного метода с системой индексов на ресурс.': [false, false], 'Текущие цены.': [false, false], 'Прогнозные цены.': [false, false]}, false, false]}, {'Что представляет собой базисный уровень стоимости?': [{'Уровень ценовых показателей сметно-нормативной базы, используемой для составления сметной документации.': [true, false], 'Уровень стоимости, определяемый на основе сметных цен, зафиксированных на конкретную дату строительства.': [false, false], 'Уровень стоимости, определяемый на основе цен, которые прогнозируются на будущий период, когда намечается осуществлять расчеты за строительную продукцию.': [false, false], 'Текущий уровень цен.': [false, false], 'Уровень стоимости, определенный на основе сметных цен, зафиксированный на определенную дату.': [true, false], 'Прогнозируемый уровень цен.': [false, false]}, false, false]}, {'По какому нормативному правому акту расценивается вид работ «установка стропил»?': [{'ФЭР 10.': [true, false], 'ФЭР 10-01-002-01.': [true, false], 'ФЭР 11.': [false, false], 'ФЭР 15.': [false, false], 'ФЭР 09.': [false, false], 'ФЭР 13.': [false, false]}, false, false]}, {'В каких размерах определяется резерв средств на непредвиденные работы и затраты при подготовке сметной документации для объектов производственного назначения?': [{'Резерв средств на непредвиденные работы и затраты определяется для объектов производственного назначения не более 3%.': [true, false], 'Резерв средств на непредвиденные работы и затраты определяется для объектов производственного назначения не более 1,5%.': [false, false], 'Резерв средств на непредвиденные работы и затраты определяется для объектов производственного назначения не более 2%.': [false, false], 'Резерв средств на непредвиденные работы и затраты определяется для объектов производственного назначения 3%.': [true, false], 'Резерв средств на непредвиденные работы и затраты определяется для объектов производственного назначения 1,5%.': [true, false], 'Резерв средств на непредвиденные работы и затраты определяется для объектов производственного назначения 2%.': [true, false]}, false, false]}, {'На какие виды подразделяются сметные нормативы?': [{'Государственные, территориальные.': [true, false], 'Договорные.': [false, false], 'Рыночные.': [false, false], 'Отраслевые.': [true, false], 'Фирменные.': [true, false], 'Индивидуальные.': [true, false]}, false, false]}, {'Что не учитывается при составлении сметной документации на пусконаладочные работы?': [{'Дополнительные затраты при производстве работ в зимнее время.': [true, false], 'Накладные расходы.': [false, false], 'Сметная прибыль.': [false, false], 'Затраты по первоначальной очистке от снега площадки застройки объектов строительства (с учетом организации рабочей зоны).': [true, false], 'Затраты на составление технического отчета.': [true, false], 'Затраты по электрической проверке и настройке центрального управляющего устройства.': [true, false]}, false, false]}, {'Что такое «сметная стоимость»?': [{'Расчетная стоимость,  подлежащая определению на этапе архитектурно-строительного проектирования или подготовки сметы на снос объекта капитального строительства.': [true, false], 'Основа для определения размеров капитальных вложений.': [true, false], 'Сумма денежных средств, необходимых для планирования капитальных вложений, формирования договорных цен, расчетов за выполненные подрядные работы.': [true, false], 'Прямые затраты плюс накладные расходы и сметная прибыль.': [true, false], 'Прямые затраты плюс накладные расходы.': [false, false], 'Себестоимость и сметная прибыль.': [false, false]}, false, false]}, {'Что входит в состав сметной документации?': [{'Локальные сметы и расчеты, обьектные сметы и расчеты, сводный сметный расчет, сводка затрат.': [true, false], 'Локальные сметы и расчеты, обьектные сметы и расчеты.': [true, false], 'Элементные сметные нормы, локальные сметы и расчеты.': [false, false], 'Элементные сметные нормы, ЕНиР.': [false, false], 'Элементные сметные нормы, локальные сметы и расчеты, обьектные сметы и расчеты и сводка затрат.': [false, false], 'Элементные сметные нормы, ЕНиР, локальные сметы и расчеты, сводный сметный расчет, обьектные сметы и сводка затрат.': [false, false]}, false, false]}, {'Что такое «локальная смета»?': [{'Первичные сметные документы, которые составляются на отдельные виды работ по зданиям и сооружениям, инженерным сетям и благоустройству на основе обьектов, определенных при разработке рабочих чертежей. Локальные сметы составляются по рекомендуемым госстроем россии формам.': [true, false], 'Первичные сметные документы, составляемые по рекомендуемым госстроем россии формам.': [true, false], 'Первичные сметные документы, которые составляются на здания и сооружения, сети и благоустройство на основе норм снип и пос.': [false, false], 'Первичные сметные документы, которые составляются на отдельные виды работ по зданиям и сооружениям.': [false, false], 'Первичные сметные документы, которые составляются на отдельные виды работ по зданиям и сооружениям, инженерным сетям.': [false, false], 'Первичные сметные документы, которые составляются на основе обьектов, определенных при разработке рабочих чертежей.': [false, false]}, false, false]}, {'Что включается в «локальную смету»?': [{'Прямые затраты, накладные расходы и сметную прибыль(плановые накопления).': [true, false], 'Прямые затраты, накладные расходы и сметную прибыль.': [true, false], 'Заработная плата.': [false, false], 'Эксплуатация машин и стоимость материалов.': [false, false], 'Заработная плата, эксплуатация машин и стоимость материалов.': [false, false], 'Прибыль(плановые накопления).': [false, false]}, false, false]}, {'Каков порядок группировки затрат в локальных сметах (расчетах) на общестроительные работы?': [{'Земляные работы, фундаменты, стены, каркас, перекрытия, перегородки, полы, покрытие и кровля, заполнение проемов, лестницы и площадки, отделочные работы, разные работы(крыльца, отмостки, каналы и др.). Допускается разделение на подземную и надземную часть.': [true, false], 'Подземная и надземная часть.': [true, false], 'Земляные работы, стены.': [false, false], 'Фундаменты, стены, каркас.': [false, false], 'Стены, перекрытия, каркас, кровля, полы, отделочные работы,.': [false, false], 'Земляные работы, фундаменты, стены, отделочные работы.': [false, false]}, false, false]}, {'Что такое «объектная смета»?': [{'Суммирует данные локальных смет по соответствующему обьекту по графам "строительные работы", "монтажные работы", "стоимость оборудования, мебели и инвентаря",и "прочие затраты".': [true, false], 'Суммирует данные локальных смет по соответствующему обьекту по графам.': [true, false], 'Суммирует данные локальных смет по соответствующему обьекту по основной заработной плате, материалам, оборудованию и прочим затратам.': [false, false], 'Суммирует данные локальных смет по основной заработной плате, материалам.': [false, false], 'Суммирует данные локальных смет по графам "строительные работы", "монтажные работы".': [false, false], 'Суммирует данные локальных смет.': [false, false]}, false, false]}, {'Какие лимитированные затраты включаются в «объектные сметы»?': [{'Группа затрат, определяемых по установленным сметным нормативам в процентах от того или иного элемента сметной стоимости.': [true, false], 'Зимние удорожания, средства на возведение временных зданий и сооружений, резерв на непредвиденные работы и затраты, связанные с подвижным и разъездным характером работ, затраты, связанные с командированием работников строительно-монтажных организаций, затраты по возмещению затрат по перевозке работников строительно-монтажных организаций автомобильным транспортом, разница в стоимости электроэнергии и ряд других.': [true, false], 'Затраты на временные здания и сооружения; удорожание работ, выполняемых в зимнее время.': [false, false], 'Затраты на временные здания и сооружения, удорожание работ в зимнее время, часть резерва на непредвиденные работы и затраты.': [false, false], 'Затраты на временные здания и сооружения; удорожание работ, выполняемых в зимнее время; дополнительный транспорт материалов; разница в стоимости электроэнергии; часть резерва на непредвиденные работы и затраты.': [false, false], 'Затраты на временные здания и сооружения; удорожание работ, выполняемых в зимнее время; дополнительный транспорт материалов,перевозка рабочих, разница в стоимости электроэнергии, часть резерва на непредвиденные работы и затраты.': [false, false]}, false, false]}, {'Подсчет объемов работ в локальных сметах осуществляется на основе чего?': [{'Чертежей, спецификаций, пояснительных записок и ведомостей обьемов.': [true, false], 'Параметров зданий, сооружений, их частей и конструктивных элементов, принятых в проектных решениях; объемов работ, принятых из ведомостей строительных и монтажных работ и определяемых по проектным материалам; номенклатуры и количества оборудования, мебели и инвентаря, принятых из заказных спецификаций, ведомостей и других проектных материалов; действующих сметных нормативов и показателей на виды работ, конструктивные элементы, а также рыночных цен и тарифов на продукцию производственно-технического назначения и услуги.': [true, false], 'Чертежей и пояснительных записок, объемов работ, принятых из ведомостей строительных и монтажных работ и определяемых по проектным материалам.': [false, false], 'Ведомостей обьемов работ и спецификаций и пояснительных записок.': [false, false], 'Действующих сметных нормативов и показателей на виды работ, конструктивные элементы, а также рыночных цен и тарифов на продукцию производственно-технического назначения и услуги.': [false, false], 'Объемов работ, принятых из ведомостей строительных и монтажных работ и определяемых по проектным материалам.': [false, false]}, false, false]}, {'По каким конструктивным элементам зданий жилищно-гражданского назначения ведется подсчет объемов работ в локальной смете?': [{'Подземная часть здания, надземная часть здания, внутренние специальные строительные работы.': [true, false], 'Подземная часть здания (земляные работы, фундаменты, стены подвала, перекрытия, перегородки, окна, двери, полы, лестницы, внутренняя отделка); надземная часть здания (стены, перекрытия, крыша, перегородки, полы, лестницы, окна, двери, балконы и крыльца, отделка внутренняя и наружная, прочие работы); внутренние специальные строительные работы (отопление, вентиляция и кондиционирование, водопровод, газоснабжение, электроосвещений. Ввод телефона, радио, мусоропроводы).': [true, false], 'Подземная часть здания, надземная часть здания.': [false, false], 'Земляные работы, фундаменты, стены, перекрытия, крыша, перегородки.': [false, false], 'Земляные работы, фундаменты, стены, перекрытия, крыша, перегородки, отопление, вентиляция, водопровод, газоснабжение, электроосвещение.': [false, false], 'Фундаменты, стены, перекрытия, крыша, перегородки, отопление, вентиляция.': [false, false]}, false, false]}, {'По каким конструктивным элементам зданий производственного назначения ведется подсчет объемов работ в локальной смете?': [{'Подземная часть здания, надземная часть здания.': [true, false], 'Подземная часть здания (земляные работы, основания под фундаменты, фундаменты, стены подвалов, перекрытия, перегородки, окна и двери(проемы), полы, лестницы, отделка внутренняя и наружная); надземная часть здания (каркас, стены, перекрытия, крыша, перегородки, проемы, полы, лестницы, отделка внутренняя и наружная, прочие работы).': [true, false], 'Земляные работы, фундаменты, стены подвала, перекрытия, стены, крыша, полы, лестницы.': [false, false], 'Земляные работы, фундаменты, стены подвала, перекрытия, стены.': [false, false], 'Подземная часть здания, каркас, стены, перекрытия, крыша.': [false, false], 'Земляные работы, основания под фундаменты, фундаменты, стены подвалов, перекрытия, перегородки, окна и двери(проемы), полы, лестницы, отделка внутренняя и наружная); надземная часть здания.': [false, false]}, false, false]}, {'Сметная стоимость включает затраты на что?': [{'Строительные работы, монтажные работы, приобретение (изготовление) оборудования, мебели и инвентаря, прочие затраты.': [true, false], 'Прямые затраты плюс накладные расходы и сметная прибыль.': [true, false], 'Строительно-монтажные работы, оборудование и прочие затраты.': [false, false], 'Основную заработную плату, материалы и эксплуатацию машин.': [false, false], 'Прямые затраты плюс накладные расходы.': [false, false], 'Основная зарплата рабочих, сметная стоимость материалов, стоимость эсм.': [false, false]}, false, false]}, {'Что содержит сводный сметный расчет?': [{'Двенадцать глав.': [true, false], 'Девять глав.': [true, false], 'Тринадцать глав.': [false, false], 'Десять глав.': [false, false], 'Восемь глав.': [false, false], 'Четырнадцать глав.': [false, false]}, false, false]}, {'Какие затраты определяются в главе первой сводного сметного расчета «подготовка территории строительства»?': [{'Затраты на отвод земельного участка, разбивку основных осей зданий, освобождение территории строительства от имеющихся на ней строений, лесонасаждений, перенос инженерных сетей, компенсация стоимости сносимых сооружений и насаждений, возмещение потерь сельскохозяйственного производства и др. Затраты, связанные с освоением застраиваемой территории.': [true, false], 'Затраты, связанные с отводом и оформлением земельного участка, разбивочными работами, выдачей исходных данных и технических условий для проектирования, согласованием проектных решений; плата за землю при изъятии (выкупе) земельного участка и его аренда на период проектирования и строительства; средства на компенсации потерь прежним землепользователям от изъятия земель под строительство; затраты на работы, связанные с неблагоприятными гидрогеологическими условиями территории строительства и необходимого устройства объездов для городского транспорта; сметная стоимость строительно-монтажных работ по освоению территории строительства.': [true, false], 'На отвод земельного участка, разбивку основных осей зданий, освобождение территории строительства от имеющихся на ней строений, лесонасаждений, устройство временных дорог и сетей, перенос инженерных сет на отвод земельного участка ей, компенсация стоимости сносимых сооружений и насаждений, возмещение потерь сельскохозяйственного производства.': [false, false], 'На отвод земельного участка, устройство временных дорог и сетей.': [false, false], 'На отвод земельного участка, освобождение территории строительства от имеющихся на ней строений, лесонасаждений.': [false, false], 'На отвод земельного участка и др. Затраты, связанные с освоением застраиваемой территории.': [false, false]}, false, false]}, {'В главу вторую сводного сметного расчета «основные объекты строительства» что включается?': [{'Сметная стоимость зданий и сооружений основного производственного назначения.': [true, false], 'Сметная стоимость зданий, сооружений и видов работ, определяющих основное назначение стройки: цеха промышленного предприятия, жилые дома, школы и др.': [true, false], 'Сметная стоимость зданий и сооружений и инженерных сетей к ним.': [false, false], 'Цеха промышленного предприятия.': [false, false], 'Жилые дома, школы и др.': [false, false], 'Обьекты подсобного и обслуживающего назначения.': [false, false]}, false, false]}, {'В главу третью сводного сметного расчета «объекты подсобного и обслуживающего назначения» что включается?': [{'Сметная стоимость зданий ремонтных мастерских, складов, заводоуправления, галерей, эстакады, хозяйственные корпуса, проходные, а также стоимость зданий культурно-бытового назначения, расположенных в пределах территории, отведенной для строительства предприятий.': [true, false], 'В промышленном строительстве - здания заводоуправлений, лабораторные корпуса, проходные, ремонтные цеха и мастерские, склады, эстакады, галереи и др.; в жилищно-гражданском строительстве - хозяйственные корпуса, проходные и теплицы в больницах и научных городках, мусоросборники и др.': [true, false], 'Сметная стоимость зданий ремонтных мастерских, складов, заводоуправления, галереи, эстакады, корпуса, проходные, гаражи, насосные станции и здания культурно-бытового назначения, расположенные в пределах территории, отведенной для строительства предприятий.': [false, false], 'Сметная стоимость гаражей, стоянок для машин и сети радиофикации и телефонизации.': [false, false], 'Сметная стоимость зданий ремонтных мастерских, складов.': [false, false], 'Сметная стоимость зданий ремонтных мастерских, складов, заводоуправления.': [false, false]}, false, false]}, {'В главу четвертую сводного сметного расчета «объекты энергетического хозяйства» что включается?': [{'Обьекты энергетического хозяйства - линии электропередачи, дизельные, трансформаторные подстанции.': [true, false], 'Сметная стоимость зданий электроподстанций, трансформаторных подстанций и киосков, линий электроснабжения.': [true, false], 'Линии электропередачи.': [false, false], 'Трансформаторные подстанции.': [false, false], 'Дизельные подстанции.': [false, false], 'Сети и сооружения водопровода, канализации и теплоснабжения.': [false, false]}, false, false]}, {'В главу пятую сводного сметного расчета «объекты транспортного хозяйства и связи» что включается?': [{'Сметная стоимость строительства автомобильных дорог, гаражей, стоянок автомашин, железнодорожных и подъездных путей к предприятиям, внутризаводских путей, депо, мастерских, складов, резервуаров, морских и речных причалов (при сооружении в составе предприятий) и др., также стоимость устройства всех видов связи (абонентской, диспетчерской и др.) - здания для размещения устройств связи и наружные кабельные сети.': [true, false], 'Сметная стоимость объектов транспорта и связи - гаражи, телефонные станции, сети телефонизации и радиофикации, подъездные автодороги.': [true, false], 'Сметная стоимость строительства автомобильных дорог.': [false, false], 'Стоимость устройства всех видов связи.': [false, false], 'Сметная стоимость депо, мастерских, складов, резервуаров, морских и речных причалов.': [false, false], 'Сметная стоимость обьектов вспомогательного назначения-проходные, котельные, теплицы и т.д.': [false, false]}, false, false]}, {'В главу шестую сводного сметного расчета «наружные сети и сооружения водопровода, канализации, теплоснабжения и газоснабжения» что включается?': [{'Сметная стоимость наружных сетей и сооружений водоснабжения, канализации, теплоснабжения и газоснабжения.': [true, false], 'Сметная стоимость прокладки наружных инженерных сетей и возведения зданий котельных, насосных станций, центрального теплового пункта, очистных сооружений и др.': [true, false], 'Сметная стоимость прокладки наружных инженерных сетей.': [false, false], 'Сметная стоимость возведения зданий котельных, насосных станций.': [false, false], 'Сметная стоимость центрального теплового пункта, очистных сооружений.': [false, false], 'Сметная стоимость водоснабжения, канализации, теплоснабжения.': [false, false]}, false, false]}, {'В главу седьмую сводного сметного расчета «благоустройство и озеленение территории» что включается?': [{'Сметная стоимость благоустройства и озеленения территории.': [true, false], 'Сметная стоимость работ по вертикальной планировке территории (предприятия, жилого дома, квартала, городских улиц и площадей), устройству дорожек, спортивных и игровых площадок, малых архитектурных форм, ограждения и освещения, озеленения территории.': [true, false], 'Сметная стоимость благоустройства, озеленения территории, подьездные дороги и линии электропередачи.': [false, false], 'Сметная стоимость работ по устройству дорожек, спортивных и игровых площадок.': [false, false], 'Сметная стоимость работ по созданию малых архитектурных форм, ограждения и освещения.': [false, false], 'Сметная стоимость объектов транспорта и связи.': [false, false]}, false, false]}, {'В главу восьмую сводного сметного расчета «временные здания и сооружения» что включается?': [{'Использование временных зданий и сооружений, возводимых на строительных площадках на период строительства объектов.': [true, false], 'Титульные временные здания и сооружения, возводимые на территории строительной площадки; временные здания и сооружения, возводимые за пределами территории строительной площадки; устройства временного характера, необходимые для выполнения отдельных видов работ на объектах; нетитульные временные здания и сооружения.': [true, false], 'Титульные временные здания и сооружения, возводимые на территории строительной площадки.': [false, false], 'Нетитульные временные здания и сооружения.': [false, false], 'Временные здания и сооружения, возводимые за пределами территории строительной площадки.': [false, false], 'Прочие работы и затраты.': [false, false]}, false, false]}, {'Что включается в главу девятую сводного сметного расчета «прочие работы и затраты»?': [{'Дополнительные затраты при производстве смр в зимнее время, затраты по: содержанию и восстановлению после окончания строительства действующих постоянных автомобильных дорог, перевозке рабочих, содержанию вахтовых поселков, содержанию всо, командированием работников подрядных организаций и организованным набором рабочих и др., не учтенные действующими сметными нормами.': [true, false], 'Средства на основные виды прочих работ и затрат в текущем уровне цен. В начале главы 9 учитываются относимые к лимитированным затратам дополнительные затраты при производстве строительно-монтажных работ в зимнее время.': [true, false], 'Дополнительные затраты при производстве работ в зимнее время, по перевозке рабочих содержанию вахтовых поселков, содержанию всо, и др., не учтенные действующими сметными нормами.': [false, false], 'Временные здания, содержание дирекции, содержание и восстановление после окончания строительства постоянных автомобильных дорог.': [false, false], 'Командировки работников подрядных организаций и организованный набор рабочих.': [false, false], 'Дополнительные затраты по перевозке рабочих, содержанию вахтовых поселков, содержанию всо.': [false, false]}, false, false]}, {'В главу десятую сводного сметного расчета «содержание службы заказчика-застройщика (строительного контроля) строящегося предприятия» что включается?': [{'Содержание дирекции или строительного контроля строящегося предприятия.': [true, false], 'Средства на содержание аппарата заказчика-застройщика (единого заказчика, дирекции строящегося предприятия) и строительного контроля как для строительства, так и при выполнении ремонтных и реставрационных работ. В отдельных случаях, при соответствующих расчетных обоснованиях, допускается установление индивидуальных нормативов для конкретной стройки или службы заказчика-застройщика, согласованных в установленном порядке.': [true, false], 'Проектно-изыскательские работы.': [false, false], 'Прочие затраты.': [false, false], 'Средства на содержание аппарата заказчика-застройщика.': [false, false], 'Дополнительные затраты при производстве работ в зимнее время.': [false, false]}, false, false]}, {'В главу одиннадцатую сводного сметного расчета «подготовка эксплуатационных кадров» что включается?': [{'Затраты на подготовку эксплуатационных кадров для строящегося предприятия.': [true, false], 'Средства на подготовку эксплуатационных кадров для вновь строящихся и реконструируемых предприятий, определяемые расчетами исходя из: количества и квалификационного состава рабочих, обучение которых намечается осуществить в учебных центрах, учебно-курсовых комбинатах, технических школах, учебных полигонах, непосредственно на предприятиях с аналогичными производствами и т.д.; сроков обучения; расходов на теоретическое и производственное обучение рабочих кадров; заработной платы (стипендии) обучающихся рабочих с начислениями к ней; стоимости проезда обучаемых до места обучения (стажировки) и обратно; прочих расходов, связанных с подготовкой указанных кадров.': [true, false], 'Содержание дирекции (технического надзора) строящегося предприятия.': [false, false], 'Средства на подготовку эксплуатационных кадров для вновь строящихся и реконструируемых предприятий, определяемые расчетами исходя из: количества и квалификационного состава рабочих.': [false, false], 'Средства на подготовку эксплуатационных кадров для вновь строящихся и реконструируемых предприятий, определяемые расчетами исходя из: сроков обучения; расходов на теоретическое и производственное обучение рабочих кадров; заработной платы (стипендии) обучающихся рабочих с начислениями к ней.': [false, false], 'Средства на подготовку эксплуатационных кадров для вновь строящихся и реконструируемых предприятий, определяемые расчетами исходя из: заработной платы (стипендии) обучающихся рабочих с начислениями к ней.': [false, false]}, false, false]}, {'В главу двенадцатую сводного сметного расчета «проектные и изыскательские работы, авторский надзор» что включается?': [{'Средства на проектно-изыскательские работы, на проведение экспертизы проектно-сметной документации, публичный и ценовой аудит.': [true, false], 'Средства на выполнение проектно-изыскательских работ (услуг) - раздельно на проектные и изыскательские; проведение авторского надзора проектных организаций за строительством; проведение экспертизы предпроектной и проектной документации; испытание свай, проводимое подрядной строительно-монтажной организацией в период разработки проектной документации по техническому заданию заказчика строительства; подготовку тендерной документации.': [true, false], 'Средства на проектно-изыскательские работы, авторский и технический надзор, на проведение экспертизы проектно-сметной документации.': [false, false], 'Средства на проведение экспертизы проектно-сметной документации.': [false, false], 'Средства на подготовку тендерной документации.': [false, false], 'Средства на проектно-изыскательские работы.': [false, false]}, false, false]}, {'Что такое резерв средств на непредвиденные работы и затраты?': [{'В сводный сметный расчет стоимости строительства включается резерв средств на непредвиденные работы и затраты, предназначенный для компенсации дополнительных затрат, связанных с: - уточнением объемов работ по рабочим чертежам, разработанным после утверждения проекта (рабочего проекта); ошибками в сметах, включая арифметические, выявленных после утверждения проектной документации; изменениями проектных решений в рабочей документации Резерв средств может определяться в размере не более 2% для объектов социальной сферы и не более 3% - для объектов производственного назначения. По уникальным и особо сложным объектам строительства размер средств на непредвиденные работы и затраты может быть установлен в размере до 10% по согласованию с соответствующим уполномоченным федеральным органом исполнительной власти в области строительства. При составлении сметных расчетов по объектам-аналогам и другим укрупненным нормативам на предпроектной стадии резерв средств на непредвиденные работы и затраты возможно принимать в размере до 10%.': [true, false], 'В сводный сметный расчет стоимости строительства включается резерв средств на непредвиденные работы и затраты, предназначенный для компенсации дополнительных затрат, связанных с: уточнением объемов работ по рабочим чертежам, разработанным после утверждения проекта (рабочего проекта); ошибками в сметах, включая арифметические, выявленных после утверждения проектной документации; изменениями проектных решений в рабочей документации.': [true, false], 'Резерв средств на непредвиденные работы и затраты предназначен для компенсации дополнительных затрат.': [false, false], 'Резерв средств на непредвиденные работы и затраты предназначен для компенсации дополнительных затрат, связанных с уточнением объемов работ по рабочим чертежам.': [false, false], 'Резерв средств на непредвиденные работы и затраты предназначен для компенсации дополнительных затрат, связанных с уточнением объемов работ по рабочим чертежам, разработанным после утверждения проекта.': [false, false], 'Резерв средств на непредвиденные работы и затраты предназначен для компенсации дополнительных затрат, связанных с выявленными ошибками.': [false, false]}, false, false]}, {'Как определяются затраты на командировки рабочих в сметной документации?': [{'На основании данных пос о численности рабочих и времени их работы на объекте.': [true, false], 'По сметной трудоемкости, определенной в сметной документации.': [true, false], 'В процентах от смр в текущих ценах.': [false, false], 'В процентах от накладных расходов.': [false, false], 'Расчетом на основании фактически представленных затрат.': [false, false], 'Если точно известны сроки командировки и примерно известна плановая численность - то по производственному календарю.': [false, false]}, false, false]}, {'Что такое «твердая договорная цена»?': [{'Цена не подлежащая изменению ни в сторону увеличения, ни в сторону уменьшения вне зависимости от фактического объема выполненных работ и понесенных подрядчиком расходов, какая бы из сторон договора ни предъявила это требование.': [true, false], 'В госконтракте цена работ должна быть твердой и не может изменяться в течение срока его исполнения, кроме как по соглашению сторон в установленных законом случаях.': [true, false], 'Цена, определенная в базисном уровне цен с индексацией на момент заключения договора с корректировкой в связи с изменением цен.': [false, false], 'Скорректированная цена на дату представления счета за выполненные работы без компенсационных выплат.': [false, false], 'Значение твердой цены состоит в обеспечении защиты законных интересов обеих сторон (и подрядчика, и заказчика).': [false, false], 'Твердая цена может быть предусмотрена практически любым договором, в т. Ч. Подряда, оказания услуг, госконтрактом, договором аренды.': [false, false]}, false, false]}, {'Как определяется стоимость проектных работ?': [{'Стоимость работ определяется согласно действующему "сборнику цен на проектные работы".': [true, false], 'Стоимость строительного проектирования всегда рассчитывается с учетом конкретных условий, а также возможностей и пожеланий заказчика. Отправной точкой для него является "справочник базовых цен на проектные работы для строительства" (сбц), данные которого индексируются с учетом коэффициента инфляции.': [true, false], 'В процентах от стоимости строительно-монтажных работ.': [false, false], 'Путем составления сметы на проектные работы.': [false, false], 'В процентах от стоимости строительства.': [false, false], 'Примерную стоимость проекта можно определить только после обсуждения всех деталей и изучения исходной документации.': [false, false]}, false, false]}, {'Затраты на отвод земельного участка учитываются в составе сводного сметного расчета в какой главе?': [{'Затраты, связанные с отводом земельного участка, учитываются в главе 1. "подготовка территории строительства".': [true, false], 'Затраты, связанные с отводом и оформлением земельного участка, разбивочными работами, выдачей исходных данных и технических условий для проектирования, согласованием проектных решений учитываются в главе 1. "подготовка территории строительства".': [true, false], 'Прочие работы и затраты.': [false, false], 'Проектные и изыскательские работы.': [false, false], 'Объекты подсобного и обслуживающего назначения.': [false, false], 'Временные здания и сооружения.': [false, false]}, false, false]}, {'Что относится к титульным временным зданиям и сооружениям?': [{'Временные здания и сооружения для обеспечения нужд строительства в целом - временное приспособление вновь построенных постоянных зданий для обслуживания работников строительства, аренда существующих помещений, перемещение конструкций складских, жилых и вспомогательных сборно-разборных зданий, устройство основания под них, амортизационные отчисления, временные склады, конторы, трансформаторные, котельные, насосные, бетонорастворные узлы, стенды для укрупнительной сборки конструкций, временные дороги и площадки, сети электроснабжения, водоснабжения, теплоснабжения и связи на строительной площадке и т.д.': [true, false], 'К титульным временным зданиям и сооружениям относятся временные здания и сооружения для обеспечения нужд строительства в целом.': [true, false], 'Аренда существующих помещений.': [false, false], 'Временные склады, конторы, трансформаторные, котельные узлы.': [false, false], 'Временные дороги и площадки, сети электроснабжения.': [false, false], 'Временное приспособление вновь построенных постоянных зданий для обслуживания работников строительства, аренда и приспособление существующих помещений, пути под башенные краны, дороги и площадки на территории строительства и т.д.': [false, false]}, false, false]}, {'Что относится к прямым затратам?': [{'Затраты на материалы.': [true, false], 'Затраты на основную заработную плату.': [true, false], 'Затраты на материалы, основную заработную плату, затраты на эксплуатацию строительных машин и механизмов.': [true, false], 'Затраты на эксплуатацию строительных машин и механизмов.': [true, false], 'Накладные расходы для целей составления строительной сметы.': [false, false], 'Затраты на страхование строительных работ.': [false, false]}, false, false]}, {'В составе накладных расходов имеются какие виды затрат?': [{'На основную зарплату.': [false, false], 'На материалы.': [false, false], 'На административно-хозяйственные расходы.': [true, false], 'На организацию и обслуживание производства".': [true, false], 'На обучение рабочих.': [false, false], 'На аренду и ремонт цеха.': [false, false]}, false, false]}, {'Локальная смета составляется в каких случаях?': [{'На объект.': [false, false], 'На застройку.': [false, false], 'На отдельные работы и затраты по зданиям и сооружениям.': [true, false], 'На здание.': [true, false], 'На техническую документацию на оборудование.': [false, false], 'На кровельные работы.': [false, false]}, false, false]}, {'Что такое прибыль от строительно-монтажных работ?': [{'Выручка от реализации строительной продукции.': [false, false], 'Себестоимость строительно-монтажных работ.': [false, false], 'Разница между объектом от реализованной строительной продукции в стоимостном выражении и ее себестоимости.': [true, false], 'Доход от предпринимательской деятельности.': [false, false], 'Сметная, плановая и фактическая прибыль строительного предприятия.': [false, false], 'Разница между договорной ценой и фактической себестоимостью строительно-монтажных работ.': [true, false]}, false, false]}, {'Что входит в понятие "сметные нормы"?': [{'в понятие "сметные нормы" входит совокупность количественных показателей материалов, изделий, конструкций и оборудования': [true, false], 'в понятие "сметные нормы" входит совокупность количественных показателей затрат труда работников в строительстве': [true, false], 'в понятие "сметные нормы" входит совокупность количественных показателей стоимости материалов, изделий и конструкций': [false, false], 'в понятие "сметные нормы" входит совокупность количественных пока