ановление всех видов строительных конструкций (за исключением несущих строительных конструкций)': [true, false], 'если капитальный ремонт включает замену и (или) восстановление всех строительных конструкций (за исключением несущих строительных конструкций) в совокупности с заменой отдельных элементов несущих строительных конструкций на аналогичные или иные улучшающие показатели таких конструкций элементы и (или) восстановление указанных элементов': [true, false], 'если капитальный ремонт включает замену и (или) восстановление  несущих строительных конструкций': [false, false], 'если капитальный ремонт включает изменение параметров линейных объектов или их участков (частей), которое влечет за собой изменение класса, категории и (или) первоначально установленных показателей функционирования': [false, false], 'если капитальный ремонт включает замену и (или) восстановление всех видов систем инженерно-технического обеспечения или всех видов сетей инженерно-технического обеспечения': [true, false], 'если капитальный ремонт включает изменение всех параметров линейного объекта, которое не влечет за собой изменение класса, категории и (или) первоначально установленных показателей функционирования такого объекта и при котором не требуется изменение границ полосы отвода и (или) охранной зоны такого объекта': [true, false]}, false, false]}, {'Каким образом может осуществляться проверка достоверности определения сметной стоимости?': [{'одновременно с проведением государственной экспертизы проектной документации и результатов инженерных изысканий в случае, если проведение государственной экспертизы проектной документации и результатов инженерных изысканий является обязательным': [true, false], 'после проведения государственной экспертизы проектной документации и результатов инженерных изысканий - если орган (организация), который проводил указанную экспертизу, и орган (организация), уполномоченный на проведение проверки сметной стоимости, не совпадают в случаях, указанных в подпунктах "а" и "в" пункта 3 постановления Правительства Российской Федерации от 23 сентября 2013 г. № 840': [true, false], 'после проведения государственной экспертизы проектной документации и результатов инженерных изысканий - если имеется положительное заключение государственной экспертизы проектной документации, выданное до принятия решения о финансировании строительства объекта капитального строительства за счет средств бюджетов бюджетной системы Российской Федерации': [true, false], 'без проведения государственной экспертизы проектной документации и результатов инженерных изысканий - если подготовка проектной документации и ее государственная экспертиза не являются обязательными': [true, false], 'одновременно с проведением государственной экологической экспертизой проектной документации объектов, строительство, реконструкцию которых предполагается осуществлять в исключительной экономической зоне': [false, false], 'одновременно с проведением государственной историко-культурной экспертизой': [false, false]}, false, false]}, {'Какие документы представляются для проведения проверки достоверности определения сметной стоимости строительства, реконструкции объектов капитального строительства?': [{'договор с заказчиком на выполнение проектных работ по объекту капитального строительства': [false, false], 'заявление о проведении проверки сметной стоимости, которое подписывается заявителем': [true, false], 'заявление о проведении проверки сметной стоимости, которое подписывается руководителем главного распорядителя средств федерального бюджета': [false, false], 'ведомости объемов работ, учтенных в сметных расчетах': [true, false], 'задание на проектирование\n': [true, false], 'решение по объекту капитального строительства - в отношении объектов капитального строительства государственной собственности Российской Федерации ': [true, false]}, false, false]}, {'Какие документы представляются для проведения проверки достоверности определения сметной стоимости капитального ремонта