х необходимость выполнения работ, расходы на которые включены в сметную документацию': [true, false], 'дополнительные расчетные обоснования предусмотренных в сметной документации затрат, для расчета которых установлены сметные нормы': [false, false]}, false, false]}, {'Какие требования установлены к формату электронных документов, представляемых для проведения проверки достоверности определения сметной стоимости?': [{'электронные документы представляются в формате doc, docx, odt - для документов с текстовым содержанием, не включающим формулы': [true, false], 'электронные документы представляются в формате xls, xlsx, ods - для документов, содержащих сводки затрат, сводного сметного расчета стоимости строительства, объектных сметных расчетов (смет), локальных сметных расчетов (смет), сметных расчетов на отдельные виды затрат': [true, false], 'электронные документы не должны превышать предельного размера в 80 мегабайт\n': [true, false], 'электронные документы представляются в формате pdf - для документов с текстовым содержанием, в том числе включающих формулы и (или) графические изображения, а также документов с графическим содержанием': [true, false], 'представляемые электронные документы подписываются с использованием усиленной квалифицированной электронной подписи лицами, обладающими полномочиями на их подписание в соответствии с законодательством Российской Федерации': [true, false], 'представляемые электронные документы подписываются собственноручно лицами, обладающими полномочиями на их подписание в соответствии с законодательством Российской Федерации': [false, false]}, false, false]}, {'Что является предметом проверки достоверности определения сметной стоимости строительства?': [{'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их соответствия требованиям технических регламентов': [false, false], 'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их соответствия утвержденным сметным нормативам, сведения о которых включены в федеральный реестр сметных нормативов': [true, false], 'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их соответствия физическим объемам работ': [true, false], 'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их соответствия конструктивным, организационно-технологическим и другим решениям, предусмотренным проектной документацией': [true, false], 'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их фирменным сметным нормативам, сведения о которых не включены в федеральный реестр сметных нормативов': [false, false], 'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления непревышения сметной стоимости над укрупненным нормативом цены строительства': [true, false]}, false, false]}, {'Что является предметом проверки достоверности определения сметной стоимости капитального ремонта в случаях, если разработка проектной документации не требуется?': [{'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их соответствия физическим объемам работ, включенным в ведомость объемов работ или акт технического осмотра объекта капитального строительства': [true, false], 'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их соответствия физическим объемам работ, включенным в дефектную ведомость': [true, false], 'предметом проверки сметной стоимости является изучение и оценка расчетов, содержащихся в сметной документации, в целях установления их соответствия первоначально устано