ены для определения состава и потребности в материально-технических и трудовых ресурсах': [true, false], 'ГЭСН используются для определения сметной стоимости выполняемых работ ресурсным методом': [true, false], 'ГЭСН используются для определения сметной стоимости выполняемых работ базисно-индексным методом': [false, false], 'ГЭСН предназначены для разработки укрупненных сметных нормативов накладных расходов': [false, false], 'ГЭСН предназначены для разработки единичных расценок различного назначения (федеральных, территориальных, отраслевых, фирменных) ': [true, false], 'ГЭСН предназначены для разработки укрупненных сметных нормативов': [true, false]}, false, false]}, {'Какие сборники входят в состав государственных элементных сметных норм?': [{'сборники федеральных единичных расценок': [false, false], 'сборники государственных элементных сметных норм на строительные и специальные строительные работы': [true, false], 'сборники государственных элементных сметных норм на ремонтно-строительные работы': [true, false], 'сборники государственных элементных сметных норм на монтаж оборудования\n': [true, false], 'сборники государственных элементных сметных норм на пусконаладочные работы': [true, false], 'сборники сметных цен на материалы, изделия, конструкции и оборудование, применяемые в строительстве ': [false, false]}, false, false]}, {'Что содержат сборники государственных элементных сметных норм?': [{'указания о порядке применения сборников сметных норм, коэффициентов к сметным нормам, учитывающих условия производства работ': [true, false], 'правила исчисления объемов работ': [true, false], 'таблицы сметных норм': [true, false], 'стоимость материальных ресурсов': [false, false], 'стоимость оплаты труда рабочих (строителей, монтажников)': [false, false], 'стоимость эксплуатации машин и механизмов': [false, false]}, false, false]}, {'Какие нормативные показатели содержат таблицы государственных элементных сметных норм на строительные и специальные строительные работы?': 