ых расчетов (смет) округляются до целых рублей': [false, false], 'в объектных сметных расчетах (сметах) итоговые цифры из локальных сметных расчетов (смет) показываются в тысячах рублей с округлением до двух знаков после запятой': [true, false], 'в объектных сметных расчетах (сметах) итоговые цифры из локальных сметных расчетов (смет) показываются в миллионах рублей с округлением до двух знаков после запятой': [false, false], 'в сводных сметных расчетах стоимости строительства или ремонта (сводках затрат) итоговые суммы из объектных сметных расчетов (смет) показываются в тысячах рублей с округлением до двух знаков после запятой': [true, false], 'в локальных сметных расчетах (сметах) построчные и итоговые цифры не округляются': [false, false]}, false, false]}, {'Какие методы могут применяться при составлении сметных расчетов?': [{'рыночный': [false, false], 'ресурсный\n': [true, false], 'ресурсно-индексный\n': [true, false], 'базисно-индексный': [true, false], 'на основе укрупненных сметных нормативов\n': [true, false], 'базисно-компенсационный': [false, false]}, false, false]}, {'Исходя из каких данных составляются локальные сметные расчеты (сметы) на отдельные виды строительных и монтажных работ, а также на стоимость оборудования?': [{'параметров зданий, сооружений, их частей и конструктивных элементов, принятых в проектных решениях': [true, false], 'действующих сметных нормативов и показателей на виды работ, конструктивные элементы': [true, false], 'требований технических регламентов': [false, false], 'средств бюджетов бюджетной системы Российской Федерации': [false, false], 'объемов работ, принятых из ведомостей строительных и монтажных работ и определяемых по проектным материалам': [true, false], 'номенклатуры и количества оборудования, мебели и инвентаря, принятых из заказных спецификаций, ведомостей и других проектных материалов': [true, false]}, false, false]}, {'Что из перечисленного включает в себя стоимость, определяемая локальными сметными расчетами (смет