 сметных норм сборника ГЭСН 81-02-46... "Работы при реконструкции зданий и сооружений" при отсутствии необходимых сметных норм, включенных в сборники ГЭСНр': [false, false], 'не допускается корректировка сметных норм в зависимости от способа производства работ, за исключением случаев, предусмотренных Методикой применения сметных норм, а также техническими частями соответствующих сборников сметных норм': [true, false], 'не допускается применение коэффициентов к сметным нормам при определении затрат на демонтаж (разборку) строительных конструкций': [false, false]}, false, false]}, {'Что включает в себя раздел "Исчисление объемов работ" технической части сборников сметных норм?': [{'правила': [true, false], 'ведомости объемов работ': [false, false], 'формулы': [true, false], 'спецификации': [false, false], 'чертежи': [false, false], 'примеры расчетов': [true, false]}, false, false]}, {'Что содержат таблицы сметных норм?': [{'шифр': [true, false], 'наименование': [true, false], 'состав работ': [true, false], 'коэффициенты, учитываещие условия производства работ': [false, false], 'измеритель': [true, false], 'нормативные показатели среднего разряда рабочих и расхода ресурсов по элементам затрат': [true, false]}, false, false]}, {'Как расшифровывается шифр таблиц сметных норм (XX-XX-XXX-XX)?': [{'1-й и 2-й - знаки - номер сборника\n': [true, false], '3-й и 4-й знаки - номер раздела в составе сборника\n': [true, false], '1-й и 2-й - знаки - номер главы сводного сметного расчета стоимости строительства': [false, false], '8-й и 9-й знаки - порядковый номер сметной нормы в составе таблицы': [true, false], '5-й, 6-й и 7-й знаки - дата утверждения сметной нормы': [false, false], '5-й, 6-й и 7-й знаки - порядковый номер таблицы сметной нормы в составе раздела сборника': [true, false]}, false, false]}, {'В каких случаях сметные нормы не подлежат корректировке?': [{'в случаях когда проектной документацией предусмотрено использование строительных машин и механизмов, не учтенных в с