с технической документацией на его изготовление и поставку)': [true, false], 'габаритное оборудование поставляется на объект в собранном виде с защитным покрытием, на постоянных прокладках': [true, false], 'негабаритное оборудование поставляется на объект в разобранном виде, максимально укрупненными узлами или блоками, не требующими при монтаже подгоночных операций, с ответными фланцами на штуцерах, а также с крепежными деталями и анкерными болтами': [true, false], 'перед началом монтажа оборудования на объекте капитального строительства выполняются работы по подготовке площадок, мест установки или выведенных на проектные отметки фундаментов (с засыпанным вокруг них котлованом), а также оснований под оборудование и черные полы': [true, false], 'в стоимость габаритного оборудования включены затраты на пусконаладочные работы': [false, false]}, false, false]}, {'Какие затраты учтены в сметных нормах на ремонтно-строительные работы?': [{'затраты по вертикальному и горизонтальному перемещению материалов от приобъектного склада к месту производства работ': [true, false], 'затраты на горизонтальное перемещение мусора и материалов от разборки конструкций в зданиях и сооружениях на расстояние до 80 м': [true, false], 'затраты на непредвиденные работы': [false, false], 'затраты на согласование ремонтно-строительных работ с собственниками объекта капитального строительства': [false, false], 'затраты на вертикальное перемещение мусора и материалов от разборки при условии опускания через окно в лотках': [true, false], 'затраты на горизонтальное перемещение мусора и материалов от разборки конструкций до места их складирования в пределах строительной площадки на расстояние до 50 м от объекта капитального строительства': [true, false]}, false, false]}, {'Какие коэффициенты применяются к сметным нормам при определении затрат на демонтаж (разборку) строительных конструкций при отсутствии сметных норм на работы по демонтажу (разборке) строительных конструкций, элементов систем и сетей инженерно-технического обеспечения в сборниках Государственных элементных сметных норм и Государственных элементных сметных норм на ремонтно-строительные работы?': [{'Сборные бетонные и железобетонные строительные конструкции - 0,8': [true, false], 'Сборные деревянные конструкции - 0,8': [true, false], 'Металлические конструкции - 0,9': [false, false], 'Сборные бетонные и железобетонные строительные конструкции - 0,7': [false, false], 'Металлические конструкции - 0,7': [true, false], 'Сети инженерно-технического обеспечения - 0,6': [true, false]}, false, false]}, {'Какие коэффициенты установлены к сметным нормам при определении затрат на демонтаж оборудования при отсутствии сметных норм на работы по демонтажу (разборке) оборудования?': [{'Оборудование, пригодное для дальнейшего использования, со снятием с места установки, необходимой (частичной) разборкой и консервированием с целью длительного или кратковременного хранения - 0,7': [true, false], 'Оборудование, пригодное для дальнейшего использования, с полной разборкой на детали с целью длительного хранения - 0,9': [false, false], 'Оборудование, пригодное для дальнейшего использования, для передачи заказчику на баланс - 1,1': [false, false], 'Оборудование, пригодное для дальнейшего использования, со снятием с места установки, необходимой (частичной) разборкой без надобности хранения (перемещается на другое место установки и т.п.) - 0,6': [true, false], 'Оборудование, не пригодное для дальнейшего использования, (предназначено в лом) с разборкой и резкой на части - 0,5': [true, false], 'Оборудование, не пригодное для дальнейшего использования, (предназначено в лом) без разборки и резки - 0,3': [true, false]}, false, false]}, {'В случае отсутствия в проектной документации необходимых данных о массе разбираемых строительных конструкций какой объемный вес строительного мусора установлен сметными нормативами?': [{'при разборке бетонных конструкций - 2400 кг/м3': [true, false], 'при разборке железобетонных конструкций - 2500 кг/м3': [true, false], 'при разборке конструкций из кирпича, камня, отбивке штукатурки и облицовочной плитки - 1800 кг/м3': [true, false], 'при разборке конструкций деревянных и каркасно-засыпных \n- 600 кг/м3': [true, false], 'при разборке бетонных конструкций - 2000 кг/м3': [false, false], 'при выполнении прочих работ по разборке (кроме работ по разборке металлоконструкций и оборудования) - 1200 кг/м3': [true, false]}, false, false]}, {'Какие из указанных наименований сборников Государственных элементных сметных норм на строительные и специальные строительные работы верны?': [{'Сборник 1. Земляные работы': [true, false], 'Сборник 4. Скважины': [true, false], 'Сборник 11. Полы': [true, false], 'Сборник 27. Автомобильные дороги': [true, false], 'Сборник 30. Бетонные работы': [false, false], 'Сборник 46. Работы при реконструкции зданий и сооружений': [true, false]}, false, false]}, {'Какие из указанных наименований сборников Государственных элементных сметных норм на монтаж оборудования верны?': [{'Сборник 8. Электротехнические установки': [true, false], 'Сборник 10. Оборудование связи': [true, false], 'Сборник 11. Приборы, средства автоматизации и вычислительной техники': [true, false], 'Сборник 12. Теплоизоляционные работы': [false, false], 'Сборник 34. Оборудование пунктов пропуска платных автомобильных дорог': [false, false], 'Сборник 37. Оборудование общего назначения': [true, false]}, false, false]}, {'Какие затраты не включают при применении Государственных элементных сметных норм на пусконаладочные работы?': [{'затраты на устранение дефектов оборудования и дефектов его монтажа, недоделок строительно-монтажных работ': [true, false], 'затраты на корректировку и доработку прикладного программного обеспечения и загрузку оборудования программным обеспечением': [true, false], 'затраты на монтаж временных схем и приспособлений, обеспечивающих проведение промывок, продувок и индивидуальных испытаний технологического оборудования и трубопроводов': [true, false], 'затраты на все виды очисток (промывки, продувки и др.) трубопроводов и аппаратов': [true, false], 'затраты на индивидуальные испытания оборудования и трубопроводов (кроме индивидуальных испытаний электротехнических устройств)': [true, false], 'затраты на разработку принципиальных монтажных схем и чертежей, внесение изменений в монтажные схемы': [true, false]}, false, false]}, {'Какие затраты не включают при применении Государственных элементных сметных норм на пусконаладочные работы?': [{'затраты на частичный или полный перемонтаж электрических шкафов, панелей, пультов': [true, false], 'затраты на обучение эксплуатационного персонала': [true, false], 'затраты на составление паспортов на технологическое оборудование': [true, false], 'затраты на техническое обслуживание оборудования в период пусконаладочных работ': [true, false], 'затраты на согласование выполненных работ с надзорными органами': [true, false], 'затраты на техническое обслуживание и периодические проверки оборудования в период его эксплуатации': [true, false]}, false, false]}, {'Какие из перечисленных положений учитывают при разработке сметных норм и единичных расценок?': [{'единичные расценки учитывают оптимальные технологические и организационные схемы производства работ\n\n': [true, false], 'единичные расценки разработаны на основе сметных норм по принципу усреднения с определением необходимого и достаточного нормативного количества строительных ресурсов, для выполнения соответствующего вида работ': [true, false], 'единичные расценки разработаны на основе статистических данных и рыночных цен на материальные ресурсы': [false, false], 'сметные нормы разработаны на основе принципа усреднения с минимизацией расходов всех необходимых ресурсов и в сторону уменьшения не корректируются': [true, false], 'единичные расценки учитывают отраслевой уровень строительного производства линейных объектов капитального строительства': [false, false], 'сметные нормы учитывают стоимость материальных ресурсов необходимых и достаточных для выполнения соответствующего вида работ': [false, false]}, false, false]}, {'Какие сборники относятся к единичным расценкам?': [{'федеральные единичные расценки на строительные, специальные строительные работы (ФЕР)': [true, false], 'федеральные единичные расценки на эксплуатацию и текущее содержание (ФЕРэт)': [false, false], 'федеральные единичные расценки на ремонтно-строительные работы (ФЕРр)': [true, false], 'федеральные единичные расценки на монтаж оборудования (ФЕРм)': [true, false], 'федеральные единичные расценки на пусконаладочные работы (ФЕРп)': [true, false], 'федеральные единичные расценки на проектно-изыскательские работы (ФЕРпр)': [false, false]}, false, false]}, {'Как расшифровывается структура обозначения сборников единичных расценок (ФЕР(x) 81-ТН-НС-XXXX)?': [{'ФЕР(x) - сокращенное обозначение единичных расценок на соответствующие виды работ (ФЕР, ФЕРм, ФЕРп или ФЕРр)': [true, false], 'ТН - текущий юридический статус документа': [false, false], 'ТН - код типа сметного норматива': [true, false], 'НС - номер сборника': [true, false], 'XXXX - присвоенный год выпуска единичных расценок': [true, false], 'XXXX - уровень цен в сборнике единичных расценок': [false, false]}, false, false]}, {'Что содержат сборники единичных расценок на строительные, специальные строительные и ремонтно-строительные работы?': [{'техническую часть': [true, false], 'вводную историческую часть': [false, false], 'таблицы единичных расценок': [true, false], 'таблицы сметных норм': [false, false], 'приложения': [true, false], 'ответы на часто задаваемые вопросы': [false, false]}, false, false]}, {'Какие показатели приведены в составе сборников единичных расценок?': [{'оплата труда рабочих, руб., принятая в соответствии со сметными нормами, на основании которых разработаны соответствующие единичные расценки': [true, false], 'затраты труда рабочих, чел.-ч., принятые в соответствии со сметными нормами, на основании которых разработаны соответствующие единичные расценки': [true, false], 'затраты на эксплуатацию строительных машин и механизмов, в том числе оплата труда машинистов, руб., рассчитанные в соответствии со сборником сметных цен на эксплуатацию машин и механизмов и затраты труда машинистов, принятые в соответствии со сметными нормами, на основе которых разработаны единичные расценки': [true, false], 'стоимость материалов, изделий и конструкций, руб., рассчитанная в соответствии со сборником сметных цен на материалы, изделия и конструкции, и расход неучтенных материалов, изделий и конструкций в физических (натуральных) единицах измерения, принятый в соответствии со сметными нормами, на основании которых разработаны соответствующие единичные расценки': [true, false], 'затраты на непредвиденные работы и затраты, руб., рассчитанные в соответствии с техническими частями сборников единичных расценок': [false, false], 'затраты на транспортные расходы по доставке материалов, изделий и конструкций, руб., принятые в соответствии со сметными нормами, на основании которых разработаны соответствующие единичные расценки': [false, false]}, false, false]}, {'Как расшифровывается обозначение единичной расценки (шифр) XX-XX-XXX-XX?': [{'1-й и 2-й знаки - номер сборника': [true, false], '1-й и 2-й знаки - две последние цифры года выпуска сборника': [false, false], '3-й и 4-й знаки - код из классификатора строительных ресурсов': [false, false], '3-й и 4-й знаки - номер раздела (отдела) в составе сборника': [true, false], '5-й, 6-й и 7-й знаки - порядковый номер таблицы в составе раздела': [true, false], '8-й и 9-й знаки - порядковый номер единичной расценки в составе таблицы': [true, false]}, false, false]}, {'На какие виды работ не распространяется действие единичных расценок, включенных в сборники?': [{'отдельные виды работ, нормы на которые отсутствуют в сборниках ГЭСН': [true, false], 'работы, технология производства которых и расход ресурсов в конкретных условиях их выполнения существенно отличаются от технологии и расхода ресурсов, принятых в сметных нормах, включенных в сборники ГЭСН': [true, false], 'работы по строительству объекта капитального строительства,  финансирование которого планируется осуществлять за счет средств федерального бюджета': [false, false], 'работы с применением машин и строительных материалов, в том числе импортных, не имеющих аналогов отечественного производства, существенно изменяющих технологию их производства и расход ресурсов по сравнению со сметными нормами, включенными в сборники ГЭСН': [true, false], 'отдельные виды конструкций и виды работ, к капитальности, классу точности которых предъявляются повышенные требования правил организации строительства': [true, false], 'работы, выполняемые в высокогорных районах на высоте более 3500 м': [true, false]}, false, false]}, {'Допускается ли внесение изменений в единичные расценки, при применении единичных расценок для определения сметной стоимости?': [{'не допускается, в том числе в случаях, когда проектной документацией предусмотрено использование строительных машин и механизмов, не учтенных в сметных нормах, на основе которых разработаны соответствующие единичные расценки, но при этом принципиально не меняются технологические и организационные схемы производства работ': [true, false], 'не допускается, в том числе в случаях, когда проектной документацией предусмотрено  использование в соответствии с ПОС машин и механизмов, технические характеристики которых отличаются от учтенных сметными нормами, на основе которых разработаны соответствующие единичные расценки, но при этом принципиально не меняются технологические и организационные схемы производства работ': [true, false], 'не допускается, в том числе в случаях, когда проектной документацией предусмотрено выполнение работ вручную и (или) с использованием средств малой механизации. При этом сметными нормами, на основе которых разработаны соответствующие единичные расценки, учтено применение машин и механизмов или иных технических средств': [true, false], 'не допускается, 