ом числе по возведению новых элементов конструктивных решений), с применением коэффициентов 1,15 - к затратам труда и оплате труда рабочих и 1,25 - к затратам на эксплуатацию строительных машин и механизмов, затратам труда машинистов': [true, false], 'затраты на ремонтно-строительные работы и работы по реконструкции объектов капитального строительства могут быть определены по единичным расценкам сборника ФЕР 81-02-46-... "Работы при реконструкции зданий и сооружений", с применением коэффициентов 1,15 - к затратам труда и оплате труда рабочих и 1,25 - к затратам на эксплуатацию строительных машин и механизмов, затратам труда машинистов': [false, false], 'затраты на ремонтно-строительные работы и работы по реконструкции объектов капитального строительства могут быть определены  по единичным расценкам, включенным в сборники единичных расценок на строительные и специальные строительные работы (аналогичные технологическим процессам в новом строительстве, в том числе по возведению новых элементов конструктивных решений), с применением коэффициентов 1,5 - к затратам труда и оплате труда рабочих и 1,2 - к затратам на эксплуатацию строительных машин и механизмов, затратам труда машинистов': [false, false], 'затраты на ремонтно-строительные работы и работы по реконструкции объектов капитального строительства могут быть определены ФЕР 81-02-09-... "Строительные металлические конструкции" с применением коэффициента 2,0': [false, false]}, false, false]}, {'На какие единичные расценки не распространяются коэффициенты 1,15 - к затратам труда и оплате труда рабочих и 1,25 - к затратам на эксплуатацию строительных машин и механизмов, затратам труда машинистов при определении затрат на ремонтно-строительные работы и работы по реконструкции по единичным расценкам, включенным в сборники единичных расценок на строительные и специальные строительные работы?': [{'на строительные и специальные строительные работы, используемые при определении сметных затрат по разборке (демонтажу) строительных конструкций, систем и сетей инженерно-технического обеспечения с применением понижающих коэффициентов, установленных соответствующими методическими рекомедациями': [true, false], 'сборников единичных расценок на ремонтно-строительные работы, на монтаж оборудования, на пусконаладочные работы ФЕРм, ФЕРр, ФЕРп\n': [true, false], 'на строительные и специальные строительные работы по разборке (демонтажу) строительных конструкций, систем и сетей инженерно-технического обеспечения, включенные в сборники ФЕР\n': [true, false], 'на единичные расценки сборника ФЕР 81-02-15-… "Отделочные работы"': [false, false], 'на единичные расценки сборника ФЕР 81-02-16-… "Трубопроводы внутренние"': [false, false], 'на единичные расценки сборника ФЕР 81-02-46-... "Работы при реконструкции зданий и сооружений"': [true, false]}, false, false]}, {'Какие из указанных наименований сборников федеральных единичных расценок на строительные и специальные строительные работы верны?': [{'Сборник 10. Деревянные конструкции': [true, false], 'Сборник 12. Кровли': [true, false], 'Сборник 15. Отделочные работы': [true, false], 'Сборник 22. Кирпичные конструкции': [false, false], 'Сборник 27. Автомобильные дороги': [true, false], 'Сборник 46. Работы при реконструкции зданий и сооружений': [true, false]}, false, false]}, {'Для каких условий производства работ по строительству объектов капитального строительства сметными нормативами установлено применение повышающих коэффициентов?': [{'производство работ по возведению конструктивных элементов встраиваемых помещений внутри строящегося объекта капитального строительства (при возведенных несущих конструктивных элементах), что в соответствии с требованиями технической безопасности, приводит к ограничению действий рабочих по производству работ': [true, false], 'производство работ осуществляется на территории действующего предприятия с наличием в зоне производства работ одного или нескольких из перечисленных ниже факторов:\nразветвленной сети транспортных и инженерных коммуникаций;\nстесненных условий для складирования материалов;\nдействующего технологического оборудования;\nдвижения технологического транспорта': [true, false], 'производство работ осуществляется в охранной зоне действующей воздушной линии электропередачи, вблизи объектов, находящихся под напряжением, внутри объектов капитального строительства, внутренняя проводка в которых не обесточена, если это приведет к ограничению действий рабочих в соответствии с требованиями техники безопасности': [true, false], 'производство работ осуществляется в стесненных условиях застроенной части населенных пунктов\n': [true, false], 'производство работ осуществляется в эксплуатируемых тоннелях метрополитенов в ночное время "в окно"\n': [true, false], 'производство работ осуществляется на территории не действующего предприятия с наличием в зоне производства работ разветвленной сети транспортных и инженерных коммуникаций': [false, false]}, false, false]}, {'Влияние каких условий производства работ следует учитывать при определении сметной стоимости пусконаладочных работ путем применения повышающих коэффициентов, установленных сметными нормативами?': [{'производство работ осуществляется в помещениях эксплуатируемого объекта капитального строительства без остановки рабочего процесса предприятия, при этом в зоне производства работ имеются действующее технологическое или лабораторное оборудование, мебель и иные загромождающие помещения предметы\n': [true, false], 'производство работ в электроустановках, находящихся под напряжением, с оформлением при этом наряда-допуска или распоряжения': [true, false], 'производство работ в горной местности на высоте до 1500 метров над уровне моря\n': [false, false], 'производство работ на электрооборудовании, защищенном от воздействия окружающей среды по конструктивному исполнению (пыле-, взрыво-, брызго-, водозащищенном, герметичном, защищенном от агрессивной среды)': [true, false], 'производство работ на предприятиях, где в силу режима секретности применяются специальный допуск, специальный пропуск и другие ограничения для пусконаладочного персонала': [true, false], 'производство работ при температуре воздуха на рабочем месте ниже 0 °C': [true, false]}, false, false]}, {'Влияние каких условий производства работ следует учитывать при определении сметной стоимости пусконаладочных работ путем применения повышающих коэффициентов, установленных сметными нормативами?': [{'производство работ в закрытых сооружениях (помещениях), верхняя отметка перекрытия которых находится ниже 3 м от поверхности земли, за исключением подземных сооружений специального назначения': [true, false], 'производство работ в горной местности на высоте от 1500 до 2500 метров над уровне моря\n': [true, false], 'производство работ в горной местности на высоте до 1000 метров над уровне моря\n': [false, false], 'производство работ осуществляется в охранной зоне действующей воздушной линии электропередачи, вблизи объектов, находящихся под напряжением, внутри объектов капитального строительства, внутренняя проводка в которых не обесточена, если это приведет к ограничению действий рабочих в соответствии с требованиями техники безопасности': [true, false], 'производство работ в помещениях категории А и Б по пожаро-взрывоопасности, на взрывоопасных блоках 1-й, 2-й и 3-й категорий взрывоопасности': [true, false], 'производство работ осуществляется в помещениях объекта капитального строительства с остановкой рабочего процесса предприятия, при этом в зоне производства работ имеются действующее технологическое или лабораторное оборудование, мебель и иные загромождающие помещения предметы': [true, false]}, false, false]}, {'Для каких условий производства работ по капитальному ремонту объектов капитального строительства сметными нормативами установлено применение повышающих коэффициентов?': [{'в случае, если рабочим основного производства установлен сокращенный рабочий день, а рабочие имеют рабочий день нормальной продолжительности': [true, false], '