ьствование работ, скрываемых последующими работами, и промежуточная приемка возведенных строительных конструкций, влияющих на безопасность объекта капитального строительства, участков сетей инженерно-технического обеспечения': [false, false], 'приемка законченных видов (этапов) работ': [true, false], 'проверка соответствия законченного строительством объекта требованиям проектной и подготовленной на ее основе рабочей документации, заключению государственной экспертизы': [false, false]}, false, false]}, {'Какие контрольные мероприятия включает в себя строительный контроль, осуществляемый заказчиком?': [{'проверка полноты и соблюдения установленных сроков выполнения подрядчиком входного контроля и достоверности документирования его результатов': [true, false], 'проверка выполнения подрядчиком контрольных мероприятий по соблюдению правил складирования и хранения применяемой продукции и достоверности документирования его результатов': [true, false], 'проверка полноты и соблюдения установленных сроков выполнения подрядчиком контроля последовательности и состава технологических операций по осуществлению строительства объектов капитального строительства и достоверности документирования его результатов': [true, false], 'совместно с подрядчиком освидетельствование скрытых работ и промежуточная приемка возведенных строительных конструкций, влияющих на безопасность объекта капитального строительства, участков сетей инженерно-технического обеспечения': [true, false], 'проверка совместно с подрядчиком соответствия законченного строительством объекта требованиям проектной и подготовленной на ее основе рабочей документации, результатам инженерных изысканий, требованиям градостроительного плана земельного участка, требованиям технических регламентов': [true, false], 'иные мероприятия в целях осуществления строительного контроля, предусмотренные законодательством Российской Федерации и (или) заключенным договором': [true, false]}, false, false]}, {'Что из перечисленного относится к 