едств': [true, false], 'техническое перевооружение, реконструкция, расширение и строительство новых объектов производственного назначения инфраструктуры строительно - монтажной организации': [true, false]}, false, false]}, {'Какие затраты не учитываются в нормативах сметной прибыли?': [{'благотворительные взносы': [true, false], 'затраты на компенсацию стоимости питания в столовых и буфетах': [true, false], 'затраты на оплату проездов в транспорте общего пользования': [true, false], 'затраты на оплату подписки на газеты и журналы': [true, false], 'затраты на платежи за превышение предельно допустимых выбросов загрязняющих веществ': [true, false], 'платежи за превышение предельно допустимых выбросов загрязняющих веществ': [true, false]}, false, false]}, {'Какие затраты не учитываются в нормативах сметной прибыли?': [{'добровольные пожертвования в избирательные фонды': [true, false], 'выделение единовременных пособий работникам, уходящим на пенсию, надбавки к пенсиям': [true, false], 'затраты на страхование персонала строительных организаций (кроме обязательного социального страхования и обеспечения медицинского страхования)': [true, false], 'затраты на покупку акций, облигаций и других ценных бумаг, затраты, связанные с выпуском и распространением ценных бумаг': [true, false], 'затраты на строительство жилых и других объектов непроизводственного назначения': [true, false], 'затраты на содержание аппарата управления объединений, ассоциаций, концернов и других вышестоящих органов управления': [true, false]}, false, false]}, {'Какие коэффициенты применяются к нормативам сметной прибыли при определении сметной стоимости?': [{'0,8 - при определении сметной стоимости строительства, реконструкции, технического перевооружения, капитального и текущего ремонта в текущем уровне цен': [false, false], '0,8 - при определении сметной стоимости строительства, реконструкции, технического перевооружения, капитального и текущего ремонта в текущем уровне цен за исключением следующих вид