лненные работы': [true, false], 'проектными организациями при разработке сметной документации': [true, false], 'производителями и подставщиками материальных ресурсов': [false, false], 'органами исполнительной власти': [false, false], 'всеми перечисленными лицами': [false, false]}, false, false]}, {'На какие виды подразделяются нормы накладных расходов по своему функциональному назначению и масштабу применения?': [{'укрупненные нормативы по основным видам строительства': [true, false], 'нормативы по видам строительных, монтажных и ремонтно-строительных работ': [true, false], 'индивидуальные нормы для конкретной строительно-монтажной или ремонтно-строительной организации': [true, false], 'нормативы установленные в задании на проектирование': [false, false], 'нормативы установленные органами местного самоуправления': [false, false], 'индивидуальные нормативы определенные для конкретного объекта капитального строительства': [false, false]}, false, false]}, {'Что применяется в качестве базы для исчисления накладных расходов?': [{'затраты на оплату труда строителей': [true, false], 'затраты на оплату труда механизаторов': [true, false], 'стоимость материалов, изделий и конструкций': [false, false], 'время эксплуатации машин и механизмов': [false, false], 'стоимость оборудования': [false, false], 'прямые затрат определенные локальным сметным расчетом': [false, false]}, false, false]}, {'На каких стадиях применяются нормативы накладных расходов по видам строительных и монтажных работ?': [{'на стадии разработки рабочей документации': [true, false], 'при расчетах за выполненные работы': [true, false], 'на стадии разработки проектной документации': [true, false], 'на стадии определения бизнес потребности': [false, false], 'на стадии обоснования инвестиций': [false, false], 'на стадии эксплуатации объекта': [false, false]}, false, false]}, {'Какие затраты не учтены в нормах накладных расходов, но относятся к накладным расходам?': [{'затраты, не связанные с командированием рабочих для выполнения строительных, монтажных и специальных строительных работ': [false, false], 'отчисления в резерв на возведение временных (титульных) зданий (сооружений) в тех случаях, когда средства на их возведение предусмотрены в свободной (договорной) цене строительства': [true, false], 'затраты по перевозке работников, проживающих от места работы на расстоянии более 3 километров, к месту работы и обратно автомобильным транспортом (собственным или арендованным), если коммунальный или пригородный транспорт не в состоянии обеспечить их перевозку и нет возможности организовать перевозку путем организации специальных маршрутов городского пассажирского транспорта': [true, false], 'дополнительные затраты, не связанные с осуществлением подрядных работ вахтовым методом': [false, false], 'затраты на перебазирование строительных организаций и их подразделений на другие стройки': [true, false], 'расходы на проведение специальных мероприятий по обеспечению нормальных условий труда (по борьбе с радиоактивностью, силикозом, малярией, гнусом, энцефалитным клещом и др.)': [true, false]}, false, false]}, {'Какие затраты не учтены в нормах накладных расходов, но относятся к накладным расходам?': [{'пособия в связи с потерей трудоспособности, выплачиваемые работникам на основании судебных решений': [false, false], 'налоги, сборы, платежи и другие обязательные отчисления, производимые в соответствии с установленным законодательством порядком (за исключением отчислений на единый социальный налог)': [true, false], 'затраты на платежи (страховые взносы) по добровольному страхованию в соответствии с установленным законодательством Российской Федерации порядком, средств транспорта (водного, воздушного, наземного), имущества гражданской ответственности организаций - источников повышенной опасности, гражданской ответственности перевозчиков, профессиональной ответственности, по добровольному страхованию от несчастных случаев и болезней, а также медицинскому страхованию': [true, false], 'пр