, 'величина накладных расходов может определяться на основе данных объектов аналогов': [false, false], 'величина накладных расходов может определяться на основе нормативов по видам ремонтно-строительных работ': [true, false]}, false, false]}, {'От каких затрат определяются накладные расходы в строительстве, осуществляемом в районах Крайнего Севера и приравненных к ним местностях?': [{'от фонда оплаты труда рабочих-строителей': [true, false], 'от фонда оплаты труда механизаторов': [true, false], 'от стоимости материалов, изделий и конструкций': [false, false], 'от времени эксплуатации машин и механизмов': [false, false], 'от стоимости оборудования': [false, false], 'от прямых затрат': [false, false]}, false, false]}, {'Какие нормативно-правовые акты определяют перечень районов Крайнего Севера и приравненных к ним местностей?': [{'распоряжения Правительства Российской Федерации': [true, false], 'постановления Правительства Российской Федерации': [true, false], 'распоряжения Президента Российской Федерации': [true, false], 'указы Президента Российской Федерации': [true, false], 'распоряжения Правительства субьекта Российской Федерации': [false, false], 'приказ органов местного самоуправления': [false, false]}, false, false]}, {'Какие коэффициенты и в каких случаях применяются к нормативам накладных расходов?': [{'при определении сметной стоимости ремонтных работ в жилых и общественных зданиях, аналогичных технологическим процессам в новом строительстве (в том числе возведение новых конструктивных элементов в ремонтируемом здании), с использованием сборников ТЕР-2001 (ФЕР-2001) нормативы накладных расходов следует применять с коэффициентом 0,9': [true, false], 'при реконструкции объектов метрополитена, а также мостов, путепроводов, искусственных сооружений, относящихся к категории сложных, к нормативам накладных расходов применяется коэффициент 1,2': [true, false], 'при реконструкции и капитальном ремонте действующих атомных электростанций и других объектов с ядерными реакторами к нормативам накладных расходов применяется коэффициент 1,2.': [true, false], 'при определении стоимости работ по капитальному ремонту наружных инженерных сетей улиц и дорог общегородского, районного и местного значения, мостов и путепроводов применяется коэффициент 1,1': [false, false], 'при реконструкции объектов метрополитена, а также мостов, путепроводов, искусственных сооружений, относящихся к категории сложных, к нормативам накладных расходов применяется коэффициент 1,4': [false, false], 'коэффициенты к нормативам накладных расходов в МДС 81-33.2004 не установлены': [false, false]}, false, false]}, {'Какие из перечисленных сборников относятся к сметным нормам?': [{'ФЕР 81-02-01-2001': [false, false], 'ГЭСН 81-02-01-2017': [true, false], 'ФЕР 81-02-30-2001': [false, false], 'ГЭСН 81-02-27-2017': [true, false], 'ГЭСН 81-02-05-2017': [true, false], 'ФССЦ 81-01-2001': [false, false]}, false, false]}, {'Какие из перечисленных методик относятся к сметным нормативам?': [{'методические рекомендации по применению федеральных единичных расценок на строительные, специальные строительные, ремонтно-строительные, монтаж оборудования и пусконаладочные работы, утвержденные приказом Минстроя России от 09.02.2017 № 81/пр': [true, false], 'методика применения сметных норм, утвержденная Приказом Минстроя России от 29.12.2016 № 1028/пр': [true, false], 'методика определения стоимости строительной продукции на территории Российской Федерации, утвержденная постановлением Госстроя России от 05.03.2004 № 15/1 ': [true, false], 'постановление Правительства РФ от 18.05.2009 № 427 "О порядке проведения проверки достоверности определения сметной стоимости строительства, реконструкции, капитального ремонта объектов капитального строительства"': [false, false], 'постановление Правительства РФ от 16.02.2008 № 87 "О составе разделов проектной документации и требованиях к их содержанию"': [false, false], 'методические рекомендации по разработке и оформлению проекта организации строительства, проекта организации работ по сносу (демонтажу), проекта производства работ (МДС 12-46.2008)': [false, false]}, false, false]}, {'Для чего предназначены методические указания по применению справочников базовых цен на проектные работы в строительстве?': [{'для определения стоимости разработки проектной и рабочей документации для строительства новых зданий и сооружений\n \n': [true, false], 'для определения стоимости разработки проектной и рабочей документации на реконструкцию  зданий и сооружений': [true, false], 'для определения стоимости строительства объектов капитального строительства': [false, false], 'для определения стоимости разработки проектной и рабочей документации расширения  и технического перевооружения объектов': [true, false], 'для определения стоимости авторского надзора': [false, false], 'для определения стоимости пуконаладочных работ': [false, false]}, false, false]}, {'От каких показателей могут устанавливаться базовые цены Справочников на проектные работы?': [{'от натуральных показателей   (мощности, протяженности, емкости, площади и др.) объектов проектирования': [true, false], 'от общей стоимости строительства объектов проектирования': [true, false], 'от места расположения объекта ': [false, false], 'от цены контракта на выполнение проектных работ': [false, false], 'от трудоемкости ': [false, false], 'от стоимости оборудования': [false, false]}, false, false]}, {'Какие работы и услуги учтены Справочниками базовых цен на проектные работы?': [{'участие проектной организации совместно с заказчиком в согласовании готовой проектной документации с государственными органами и органами местного самоуправления': [true, false], 'защита проектной документации в экспертных и утверждающих инстанциях': [true, false], 'изготовление демонстрационных материалов (кроме демонстрационных макетов)': [true, false], 'научно-исследовательские и опытно-экспериментальные работы\n': [false, false], 'разработка деталировочных чертежей металлических конструкций (КМД) и технологических трубопроводов заводского изготовления': [false, false], 'служебные командировки': [false, false]}, false, false]}, {'Какие работы и услуги не учтены Справочниками базовых цен на проектные работы?': [{'выполнение обследований и обмерные работы на объектах, подлежащих реконструкции, расширению и техническому перевооружению': [true, false], 'внесение изменений в проектную и рабочую документацию (за исключением исправления ошибок, допущенных проектной организацией)': [true, false], 'защита проектной документации в экспертных и утверждающих инстанциях': [false, false], 'разработку раздела "Мероприятия по гражданской обороне и мероприятия по предупреждению чрезвычайных ситуаций природного и техногенного характера". Базовая цена разработки указанного раздела определяется по соответствующему Справочнику': [true, false], 'участие проектной организации совместно с заказчиком в согласовании готовой проектной документации с государственными органами и органами местного самоуправления': [false, false], 'разработку деталировочных чертежей металлических конструкций (КМД) и технологических трубопроводов заводского изготовления': [true, false]}, false, false]}, {'Стоимость проектирования каких работ и затрат учтена ценами Справочников базовых цен на разработку проектной и рабочей документации предприятий, цехов, зданий и сооружений?': [{'всех внутриплощадочных инженерных сетей в пределах площадки строительства': [true, false], 'коммуникаций в пределах площадки строительства': [true, false], 'стоимость проектирования инженерных сетей и коммуникаций вне площадки строительства': [false, false], 'сооружений и устройств (электроснабжения, водоснабжения, канализации, теплоснабжения и др.) включая присоединение цехов, зданий и сооружений к ним в пределах площадки строительства ': [true, false], 'схемы планировочной организации земельного участка (генерального плана и благоустройства) в пределах площадки строительства': [true, false], 'выполнение обследований и обмерные работы на объектах': [false, false]}, false, false]}, {'Какие коэффициенты могут быть установлены к ценам на проектные работы при определении базовой цены от общей стоимости строительства в случаях, когда объем строительно-монтажных работ по объекту капитального строительства составляет менее 60% от общей стоимости строительства?': [{'до 50% - 0,95': [true, false], 'до 40% - 0,9': [true, false], 'до 30% - 0,8': [true, false], 'до 50% - 1,1': [false, false], 'до 20% - 0,7': [true, false], 'коэффициенты не установлены': [false, false]}, false, false]}, {'Какие сложные условия проектирования учитываются при определении затрат на разработку проектной и рабочей документации?': [{'вечномерзлые, просадочные, набухающие   грунты': [true, false], 'сейсмичность от 7 до 9 баллов': [true, false], 'карстовые и оползневые явления': [true, false], 'сейсмичность 6 баллов': [false, false], 'стесненные условия строительства': [false, false], 'сейсмичность 5 баллов': [false, false]}, false, false]}, {'Какое распределение базовой цены на разработку проектной и рабочей документации?': [{'проектная документация   -   40%': [true, false], 'рабочая документация   -   60%': [true, false], 'предпроектные проработки - 20%': [false, false], 'проектная документация   -   80%': [false, false], 'рабочая документация   -   30%': [false, false], 'дизайнпроект   -   65%': [false, false]}, false, false]}, {'Каким образом определяется цена разработки проектной и рабочей документации, если проектируемый объект имеет значение основного показателя меньше минимального или больше максимального показателей?': [{'если показатель мощности объекта меньше табличного показателя, базовая цена его проектирования определяется по формуле: Ц = a + b x (0,4 x Xmin + 0,6 x Xзад.)': [true, false], 'если показатель мощности объекта меньше табличного показателя, базовая цена его проектирования определяется по формуле: \nЦ = a + b х Хзад. х 0,6': [false, false], 'если показатель мощности объекта больше табличного показателя, базовая цена его проектирования определяется по формуле: Ц = a + b x (0,4 x Xmax + 0,6 x Xзад.)': [true, false], 'если показатель мощности объекта больше табличного показателя, базовая цена его проектирования определяется по формуле: \nЦ = a + b х Хзад.': [false, false], 'если показатель мощности объекта больше табличного показателя, базовая цена его проектирования определяется по формуле: \nЦ = a + b х Хmin': [false, false], 'если показатель мощности объекта меньше табличного показателя, базовая цена его проектирования определяется по формуле: \nЦ = a + b х Хзад.': [false, false]}, false, false]}, {'Какие усложняющие факторы учитываются при определении стоимости разработки проектной и рабочей документации для строительства объектов капитального строительства за границей Российской Федерации?': [{'перевод материалов иностранного заказчика на русский язык': [true, false], 'применение иностранных норм и стандартов на материалы и оборудование': [true, false], 'дополнительные требования к проектной и рабочей документации  при  строительстве объектов на подрядных условиях': [true, false], 'перевод текстовых материалов технической документации, надписей  на чертежах на иностранный язык  ': [true, false], 'защита проектной документации в экспертных и утверждающих инстанциях': [false, false], 'участие проектной организации совместно с заказчиком в согласовании готовой проектной документации с государственными органами и органами местного самоуправления': [false, false]}, false, false]}, {'По какому сборнику единичных расценок определяются затраты на устройство покрытия автомобильных дорог?': [{'ФЕР 81-02-27-2001': [false, false], 'ФЕРм 81-03-27-2001': [false, false], 'Раздел 6. Устройство оснований и покрытий. Сборник 27. Автомобильные дороги\n': [true, false], 'ФЕР 81-02-05-2001': [false, false], 'ФЕР 81-02-28-2001': [false, false]}, false, false]}, {'Какой расход стальных шпунтовых свай установлен в Государственных элементных сметных нормах в зависимости от оборачиваемости (числа оборотов) свай?': [{'0,5 - при 2-х оборотах': [false, false], '0,65 - при 2-х оборотах': [true, false], '0,33 - при 3-х оборотах': [false, false], '0,40 - при 3-х оборотах': [true, false], '0,25 - при 4-х - 5-ти оборотах': [true, false], '0,22 - при количестве оборотов более 5': [true, false]}, false, false]}         
      ],
      generalQuestions: [
      {'Какие характеристики указывают на необходимость отнесения сооружения связи к объектам повышенного уровня ответственности?': [{'Заглубление подземной части ниже планировочной отметки земли более 15 метров.': [false, false], 'Высота до 100 метров.': [true, false], 'Высота от 75 метров.': [true, false], 'Заглубление подземной части ниже планировочной отметки земли от 5 метров.': [true, false], 'Заглубление подземной части ниже планировочной отметки земли до 10 метров.': [true, false], 'Высота более 100 метров.': [false, false]}, false, false]}, {'Какие объекты авиационной инфраструктуры должны быть идентифицированы как объекты повышенного уровня ответственности?': [{'Аэропорты.': [true, false], 'Пункт управления полетами летательных аппаратов.': [true, false], 'Центр для подготовки летного состава.': [true, false], 'Используемая при осуществлении авиационной деятельности техника.': [true, false], 'Объекты хранения авиационной техники.': [true, false], 'Объекты единой системы организации воздушного движения.': [true, false]}, false, false]}, {'Допускается ли предоставлять проектную документацию и (или) результаты инженерных изысканий в организации экспертизы не в электронной форме?': [{'Допускается, в случаях, предусмотренных законодательством Российской Федерации.': [true, false], 'Допускается, в случае, когда проектная документация и (или) результаты инженерных изысканий содержат сведения, составляющие государственную тайну.': [true, false], 'Не допускается.': [false, false], 'Допускается, в случае, когда проектная документация была составлена по специальным техническим условиям (СТУ).': [false, false], 'Не допускается, за исключением случаев, предусмотренных законодательством Российской Федерации. ': [true, false], 'Допускается, в случае предоставление проектной документации и (или) результатов инженерных изысканий в организации негосударственной экспертизы.': [false, false]}, false, false]}, {'Что такое «этап строительства»?': [{'Строительство объекта капитального строительства из числа объектов капитального строительства, планируемых к строительству, если такой объект может быть введен в эксплуатацию и эксплуатироваться автономно.': [false, false], 'Строительство части объекта капитального строительства, которая может быть введена в эксплуатацию и эксплуатироваться автономно.': [true, false], 'Реконструкция объекта капитального строительства из числа объектов капитального строительства, планируемых к реконструкции на одном земельном участке, если такой объект может быть введен в эксплуатацию и эксплуатироваться автономно.': [true, false], 'Реконструкция части объекта капитального строительства, которая может быть введена в эксплуатацию и эксплуатироваться автономно.': [true, false], 'Переустройство (перенос) инженерных коммуникаций при строительстве автомобильных дорог.': [true, false], 'Вырубка леса при строительстве автомобильных дорог.': [true, false]}, false, false]}, {'Что вправе предпринять организация по проведению государственной экспертизы при проведении экспертизы?': [{'Разъяснять бесплатно по запросам заинтересованных лиц порядок проведения государственной экспертизы.': [true, false], 'Разъяснять, в том числе на возмездной основе, по запросам заинтересованных лиц порядок проведения государственной экспертизы.': [false, false], 'Принимать меры по обеспечению сохранности документов, представленных для проведения государственной экспертизы.': [true, false], 'Принимать меры по неразглашению проектных решений и иной конфиденциальной информации, которая стала известна этой организации в связи с проведением государственной экспертизы.': [true, false], 'Информировать органы государственной власти Российской Федерации о выявлении в процессе проведения экспертизы существенных нарушений законодательства Российской Федерации.': [false, false], 'Оказывать, в том числе на возмездной основе, консультирование заявителя по вопросам подготовки проектной документации и (или) выполнения инженерных изысканий. ': [false, false]}, false, false]}, {'Имеет ли право организация по проведению государственной экспертизы участвовать в осуществлении архитектурно-строительного проектирования и (или) инженерных изысканий и в какой форме?': [{'Организация по проведению государственной экспертизы вправе участвовать в осуществлении архитектурно-строительного проектирования и (или) инженерных изысканий в качестве организации по надзору и контролю за осуществлением архитектурно-строительного проектирования и (или) инженерных изысканий.': [false, false], 'Организация по проведению государственной экспертизы вправе участвовать в осуществлении архитектурно-строительного проектирования и (или) инженерных изысканий в качестве юридического лица, на которое возложена, в соответствии с договором, обязанность по техническому аудиту архитектурно-строительного проектирования и (или) инженерных изысканий.': [false, false], 'Организация по проведению государственной экспертизы вправе участвовать в осуществлении архитектурно-строительного проектирования и (или) инженерных изысканий в случае наделения её такими полномочиями органом власти субъекта Российской Федерации.': [false, false], 'Организация по проведению государственной экспертизы не вправе участвовать в осуществлении архитектурно-строительного проектирования и (или) инженерных изысканий, в случае, если подготовка проектной документации и результаты инженерных изысканий выполняются для объекта, строительство которого не финансируется за счёт средств бюджетов бюджетной системы Российской Федерации.': [true, false], 'Организация по проведению государственной экспертизы вправе участвовать в осуществлении архитектурно-строительного проектирования и (или) инженерных изысканий, в том числе, в случае, если подготовка проектной документации и результаты инженерных изысканий выполняются для объекта, строительство которого финансируется за счёт средств бюджетов бюджетной системы Российской Федерации.': [false, false], 'Организация по проведению государственной экспертизы не вправе участвовать в осуществлении архитектурно-строительного проектирования и (или) инженерных изысканий, в случае, если подготовка проектной документации и результаты инженерных изысканий выполняются для объекта, строительство которого финансируется за счёт средств бюджетов бюджетной системы Российской Федерации.': [true, false]}, false, false]}, {'В течение скольких дней со дня получения от заявителя документов организация по проведению государственной экспертизы может осуществлять их проверку?': [{'Организация по проведению государственной экспертизы в течение 3 рабочих дней со дня получения от заявителя требуемых документов, осуществляет их проверку.': [true, false], 'Срок проведения проверки в отношении объектов, указанных в ч. 5.1 ст. 6 Градостроительного кодекса Российской Федерации, не должен превышать 10 рабочих дней.': [true, false], 'Срок проведения проверки в отношении объектов, указанных в ч. 2 ст. 49 Градостроительного кодекса Российской Федерации, не должен превышать 10 рабочих дней.': [false, false], 'Организация по проведению государственной экспертизы в течение 3 дней со дня получения от заявителя требуемых документов, осуществляет их проверку.': [false, false], 'Срок проведения проверки в отношении объектов, указанных в ч. 5.1 ст. 6 Градостроительного кодекса Российской Федерации, не должен превышать 10 дней.': [false, false], 'Организация по проведению государственной экспертизы осуществляет проверку документов, предоставленных заявителем на экспертизу в течение срока, установленного договором.': [false, false]}, false, false]}, {'При наличии каких оснований принимается решение об оставлении без рассмотрения документов, представленных для проведения государственной экспертизы?': [{'Документы предоставлены на бумажном носителе, в случае, когда проектная документация и (или) результаты инженерных изысканий содержат сведения, составляющие государственную тайну.': [false, false], 'Документы предоставлены на бумажном носителе, в случае, когда проектная документация и (или) результаты инженерных изысканий не содержат сведения, составляющие государственную тайну.': [true, false], 'Государственная экспертиза должна осуществляться иной организацией по проведению государственной экспертизы.': [false, false], 'Документы представлены с нарушением требований к формату документов, представляемых в электронной форме.': [true, false], 'Отсутствие в проектной документации разделов, предусмотренных законодательством о градостроительной деятельности.': [false, false], 'Отсутствие положительного заключения государственной экспертизы результатов инженерных изысканий (в случае, если проектная документация направлена на государственную экспертизу после государственной экспертизы результатов инженерных изысканий).': [false, false]}, false, false]}, {'Что из перечисленного является основанием для отказа в принятии проектной документации и (или) результатов инженерных изысканий, представленных на государственную экспертизу?': [{'Документы предоставлены на бумажном носителе, в случае, когда проектная документация и (или) результаты инженерных изысканий содержат сведения, составляющие государственную тайну.': [false, false], 'Документы предоставлены на бумажном носителе, в случае, когда проектная документация и (или) результаты инженерных изысканий не содержат сведения, составляющие государственную тайну.': [false, false], 'Направление проектной документации и (или) результатов инженерных изысканий в орган исполнительной власти, государственное учреждение, если проведение государственной экспертизы таких проектной документации и (или) результатов инженерных изысканий осуществляется иным органом исполнительной власти, иным государственным учреждением.': [true, false], 'Для проведения государственной экспертизы предоставлены проектная документация и (или) результаты инженерных изысканий, обязательная государственная экспертиза которых не предусмотрена законодательством о градостроительной деятельности.': [false, false], 'Несоответствие результатов инженерных изысканий составу и форме, установленным законодательством о градостроительной деятельности.': [true, false], 'Отсутствие положительного заключения государственной экспертизы результатов инженерных изысканий (в случае, если проектная документация направлена на государственную экспертизу после государственной экспертизы результатов инженерных изысканий).': [true, false]}, false, false]}, {'Что из перечисленного является основанием для отказа в принятии проектной документации и (или) результатов инженерных изысканий, представленных на государственную экспертизу?': [{'Отсутствие в составе проектной документации разделов, которые подлежат включению в состав такой документации в соответствии с требованиями, установленными Правительством Российской Федерации.': [true, false], 'Непредоставление выписки из реестра членов саморегулируемой организации в области архитектурно-строительного проектирования и (или) инженерных изысканий, членом которой является исполнитель работ по подготовке проектной документации и (или) выполнению инженерных изысканий, действительной на дату заключения договора о подготовке проектной документации и (или) о выполнении инженерных изысканий с застройщиком (техническим заказчиком), в случаях, предусмотренных законодательством о градостроительной деятельности.': [false, false], 'Непредоставление выписки из реестра членов саморегулируемой организации в области архитектурно-строительного проектирования и (или) инженерных изысканий, членом которой является исполнитель работ по подготовке проектной документации и (или) выполнению инженерных изысканий, действительной на дату передачи проектной документации и (или) результатов инженерных изысканий застройщику (техническому заказчику), в случаях, предусмотренных законодательством о градостроительной деятельности.': [true, false], 'Непредоставление документов, подтверждающих полномочия заявителя действовать от имени застройщика, технического заказчика (в случае, если заявитель не является техническим заказчиком и (или) застройщиком), в которых полномочия на заключение, изменение, исполнение, расторжение договора о проведении государственной экспертизы должны быть оговорены специально.': [true, false], 'Направление не подлежащих государственной экспертизе проектной документации и (или) результатов инженерных изысканий.': [false, false], 'Непредоставление ведомости объемов работ, учтенных в сметных расчетах, в случаях предусмотренных законодательством о градостроительной деятельности.': [true, false]}, false, false]}, {'Что из перечисленного входит в предмет государственной экспертизы проектной документации?': [{'Оценка соответствия проектной документации заданию застройщика или технического заказчика на проектирование.': [true, false], 'Оценка соответствия проектной документации санитарно-эпидемиологическим требованиям.': [true, false], 'Оценка соответствия проектной документации требованиям в области гражданской обороны.': [false, false], 'Оценка соответствия проектной документации требованиям к безопасному использованию атомной энергии.': [true, false], 'Оценка соответствия проектной документации требованиям антитеррористической защищенности объекта.': [true, false], 'Оценка достоверности определения сметной стоимости строительства, финансируемого с привлечением средств юридических лиц, созданных Российской Федерацией.': [true, false]}, false, false]}, {'Что из перечисленного входит в предмет государственной экспертизы проектной документации?': [{'Оценка соответствия проектной документации требованиям промышленной безопасности.': [true, false], 'Оценка соответствия проектной документации требованиям государственной охраны объектов культурного наследия.': [true, false], 'Оценка соответствия проектной документации требованиям в области пожарной безопасности.': [false, false], 'Оценка соответствия проектной документации требованиям профессиональных стандартов.': [false, false], 'Оценка достоверности определения сметной стоимости строительства, финансируемого с привлечением бюджетных средств.': [true, false], 'Оценка достоверности определения сметной стоимости строительства, финансируемого с привлечением средств юридических лиц, доля в уставных (складочных) капиталах которых Российской Федерации, субъектов Российской Федерации, муниципальных образований составляет более 20 процентов.': [false, false]}, false, false]}, {'Что из перечисленного входит в предмет государственной экспертизы проектной документации?': [{'Оценка соответствия проектной документации требованиям в области охраны окружающей среды.': [true, false], 'Оценка достоверности определения сметной стоимости строительства, финансируемого с привлечением средств юридических лиц, доля в уставных (складочных) капиталах которых Российской Федерации, субъектов Российской Федерации, муниципальных образований равен 50 процентов и более.': [false, false], 'Оценка соответствия проектной документации требованиям энергетической эффективности.': [false, false], 'Оценка соответствия проектной документации требованиям к обеспечению надежности и безопасности электроэнергетических систем и объектов электроэнергетики.': [true, false], 'Оценка соответствия проектной документации требованиям радиационной безопасности.': [false, false], 'Оценка соответствия проектной документации результатам инженерных изысканий.': [true, false]}, false, false]}, {'Каков срок проведения государственной экспертизы?': [{'Срок проведения государственной экспертизы определяется сложностью объекта капитального строительства, но не должен превышать сорок два рабочих дня.': [true, false], 'Срок проведения государственной экспертизы может составлять шестьдесят два рабочих дня, в случае получения заявления от застройщика или технического заказчика о его продлении.': [true, false], 'Срок проведения государственной экспертизы проектной документации или проектной документации и результатов инженерных изысканий в отношении объектов капитального строительства, строительство, реконструкция и (или) капитальный ремонт которых будут осуществляться в особых экономических зонах не должен превышать сорок пять дней.': [false, false], 'Срок проведения государственной экспертизы проектной документации или проектной документации и результатов инженерных изысканий не должен превышать шестьдесят дней.': [false, false], 'Срок проведения государственной экспертизы результатов инженерных изысканий, которые направлены на государственную экспертизу до направления на эту экспертизу проектной документации не должен превышать сорок пять дней.': [false, false], 'Cрок проведения государственной экспертизы может быть продлен по заявлению застройщика или технического заказчика не более чем на тридцать дней.': [false, false]}, false, false]}, {'Какие действия вправе предпринимать при проведении государственной экспертизы организация по проведению государственной экспертизы?': [{'Привлекать на договорной основе к проведению государственной экспертизы государственные организации.': [true, false], 'Привлекать на договорной основе к проведению государственной экспертизы негосударственные организации.': [true, false], 'Привлекать на договорной основе к проведению государственной экспертизы специалистов.': [true, false], 'Истребовать от органов государственной власти сведения и документы, необходимые для проведения государственной экспертизы.': [true, false], 'Истребовать от органов местного самоуправления сведения и документы, необходимые для проведения государственной экспертизы.': [true, false], 'Истребовать от организаций сведения и документы, необходимые для проведения государственной экспертизы.': [true, false]}, false, false]}, {'Что является результатом государственной экспертизы?': [{'Результатом государственной экспертизы является заключение, содержащее выводы о соответствии (положительное заключение) или несоответствии (отрицательное заключение) проектной документации результатам инженерных изысканий, получившим положительное заключение государственной экспертизы, требованиям технических регламентов, в том числе санитарно-эпидемиологическим, экологическим требованиям, требованиям государственной охраны объектов культурного наследия, требованиям пожарной, промышленной, ядерной, радиационной и иной безопасности, и требованиям к составу разделов проектной документации - в случае, если государственная экспертиза проектной документации осуществлялась после проведения государственной экспертизы результатов инженерных изысканий.': [false, false], 'Результатом государственной экспертизы является заключение, содержащее выводы о соответствии (положительное заключение) или несоответствии (отрицательное заключение) результатов инженерных изысканий требованиям технических регламентов - в случае, если осуществлялась государственная экспертиза результатов инженерных изысканий.': [true, false], 'Результатом государственной экспертизы является заключение, содержащее выводы о соответствии (положительное заключение) или несоответствии (отрицательное заключение) проектной документации результатам инженерных изысканий, требованиям технических регламентов, в том числе санитарно-эпидемиологическим требованиям, требованиям охраны окружающей среды, требованиям государственной охраны объектов культурного наследия, требованиям пожарной, промышленной, ядерной, радиационной и иной безопасности, и требованиям к содержанию разделов проектной документации, а также результатов инженерных изысканий требованиям технических регламентов - в случае, если осуществлялась государственная экспертиза одновременно этих проектной документации и результатов инженерных изысканий.': [false, false], 'Результатом государственной экспертизы является заключение, содержащее выводы о соответствии (положительное заключение) или несоответствии (отрицательное заключение) проектной документации результатам инженерных изысканий,  заданию на проектирование, требованиям, предусмотренным пунктом 1 части 5 статьи 49 Градостроительного кодекса Российской Федерации - в случае, если государственная экспертиза проектной документации осуществлялась после проведения государственной экспертизы результатов инженерных изысканий.': [true, false], 'Результатом государственной экспертизы является заключение, содержащее выводы о соответствии (положительное заключение) или несоответствии (отрицательное заключение)проектной документации результатам инженерных изысканий, требованиям технических регламентов, в том числе санитарно-эпидемиологическим, экологическим требованиям, требованиям государственной охраны объектов культурного наследия, требованиям пожарной, промышленной, ядерной, радиационной и иной безопасности, и требованиям к составу разделов проектной документации, а также результатов инженерных изысканий требованиям технических регламентов - в случае, если осуществлялась государственная экспертиза одновременно этих проектной документации и результатов инженерных изысканий.': [false, false], 'Результатом государственной экспертизы является заключение, содержащее выводы о соответствии (положительное заключение) или несоответствии (отрицательное заключение) результатов инженерных изысканий требованиям технических регламентов и требованиям задания на инженерные изыскания - в случае, если осуществлялась государственная экспертиза результатов инженерных изысканий.': [false, false]}, false, false]}, {'Какие сведения указываются в реестре выданных заключений?': [{'Идентификационные сведения об исполнителях работ.': [true, false], 'Дата выдачи и реквизиты заключения.': [true, false], 'Идентификационные сведения о техническом заказчике.': [true, false], 'Идентификационные сведения о застройщике.': [true, false], 'Сведения о результате государственной экспертизы (отрицательное или положительное заключение).': [true, false], 'Сведения о документации по планировке территории, на основании которых была осуществлена подготовка проектной документации.': [false, false]}, false, false]}, {'Какие сведения указываются в реестре выданных заключений?': [{'Сведения об экспертах, подготовивших заключение экспертизы. ': [false, false], 'Сведения о результате государственной экспертизы (отрицательное или положительное заключение).': [true, false], 'Идентификационные сведения об объекте капитального строительства, проектная документация и (или) результаты инженерных изысканий в отношении которого представлены на государственную экспертизу.': [true, false], 'Дата и номер договора об оказании услуг по проведению государственной экспертизы.': [false, false], 'Кадастровый номер земельного участка в границах архитектурно-строительного проектирования.': [false, false], 'Дата и номер квалификационного аттестата экспертов, проводивших экспертизу.': [false, false]}, false, false]}, {'Какая проектная документация для строительства, реконструкции и (или) капитального ремонта объектов капитального строительства не подлежит обязательной государственной экспертизе?': [{'Проектная документация для строительства, реконструкции и (или) капитального ремонта объектов индивидуального жилищного строительства.': [true, false], 'Проектная документация для строительства или реконструкции отдельно стоящих объектов капитального строительства с количеством этажей не более чем два, общая площадь которых составляет не более чем 1500 квадратных метров и которые не предназначены для проживания граждан и осуществления производственной деятельности, за исключением объектов, которые в соответствии со статьей 48.1 Градостроительного кодекса Российской Федерации являются особо опасными, технически сложными или уникальными объектами.': [true, false], 'Проектная документация для строительства или реконструкции отдельно стоящих объектов капитального строительства с количеством этажей не более чем два, общая площадь которых составляет не более чем 1500 квадратных метров и которые не предназначены для проживания граждан и осуществления производственной деятельности, но являются особо опасными, технически сложными или уникальными объектами.': [false, false], 'В случае, если строительство, реконструкцию объектов капитального строительства планируется осуществлять в границах охранных зон трубопроводов.': [false, false], 'Проектная документация работ по сохранению объектов культурного наследия в случае, если при проведении таких работ затрагиваются конструктивные и другие характеристики надежности и безопасности объектов культурного наследия.': [false, false], 'Проектная документация работ по сохранению объектов культурного наследия в случае, если при проведении таких работ не затрагиваются конструктивные и другие характеристики надежности и безопасности объектов культурного наследия.': [true, false]}, false, false]}, {'Каким требованиям, из перечисленных, должны соответствовать электронные документы, предоставляемые на экспертизу в форматах doc, docx, odt, pdf?': [{'Электронные документы, представляемые в форматах doc, docx, odt, pdf должны формироваться способом, не предусматривающим сканирование документа на бумажном носителе.': [true, false], 'Электронные документы, представляемые в форматах doc, docx, odt, pdf должны состоять из одного или нескольких файлов, каждый из которых содержит текстовую и (или) графическую информацию.': [true, false], 'Электронные документы, представляемые в форматах doc, docx, odt, pdf должны обеспечивать возможность поиска по текстовому содержанию документа и возможность копирования текста (за исключением случаев, когда текст является частью графического изображения).': [true, false], 'Электронные документы, представляемые в форматах doc, docx, odt, pdf должны содержать оглавление (для документов, содержащих структурированные по частям, главам, разделам (подразделам) данные) и закладки, обеспечивающие переходы по оглавлению.': [true, false], 'Электронные документы, представляемые в форматах doc, docx, odt, pdf должны содержать закладки, обеспечивающие переходы к содержащимся в тексте рисункам и таблицам.': [true, false], 'Электронные документы, представляемые в форматах doc, docx, odt, pdf должны не превышать предельного размера в 75 мегабайт (в случае превышения предельного размера, документ делится на несколько, название каждого файла дополняется словом "Фрагмент" и порядковым номером файла, полученного в результате деления).': [false, false]}, false, false]}, {'Какие сведения должно содержать заключение в отношении подписавших его экспертов?': [{'Фамилия, имя, отчество (при наличии) каждого эксперта.': [true, false], 'Должность каждого эксперта.': [true, false], 'Направление деятельности каждого эксперта.': [true, false], 'Номер квалификационного аттестата эксперта.': [false, false], 'Срок действия квалификационного аттестата эксперта.': [false, false], 'Наименование и реквизиты документа, являющегося основанием для привлечения эксперта к подготовке заключения экспертизы.': [true, false]}, false, false]}, {'Какие систематизированные сведения включаются в Единый государственный реестр заключений экспертизы проектной документации объектов капитального строительства?': [{'Сведения о заключениях экспертизы.': [true, false], 'Сведения о представленной для проведения экспертизы проектной документации.': [true, false], 'Сведения о проектной документации повторного использования.': [false, false], 'Сведения об экономически эффективной проектной документации повторного использования.': [true, false], 'Сведения о представленных для проведения экспертизы результатах инженерных изысканий.': [true, false], 'Сведения о документации по планировке территории, на основании которых была осуществлена подготовка проектной документации.': [false, false]}, false, false]}, {'Какие сведения включаются в подраздел реестра, касающийся сведений, содержащихся в документах, представленных для проведения экспертизы проектной документации?': [{'Сведения о документации по планировке территории, на основании которых была осуществлена подготовка проектной документации.': [false, false], 'Сведения о природных и иных условиях территории, на которой планируется осуществлять строительство (климатический район и подрайон, ветровой район, снеговой район, интенсивность сейсмических воздействий, инженерно-геологические условия).': [true, false], 'Функциональное назначение объекта капитального строительства, применительно к которому подготовлена проектная документация, и его основные проектируемые технико-экономические показатели (в соответствии с проектной документацией).': [true, false], 'Наименование и адрес (местоположение) объекта капитального строительства, применительно к которому подготовлена проектная документация.': [true, false], 'Cметная стоимость объекта капитального строительства (при наличии в проектной документации раздела "смета на строительство объектов капитального строительства") и сведения о проверке достоверности ее определения.': [true, false], 'Сведения о застройщике (техническом заказчике), обеспечившем подготовку проектной документации.': [true, false]}, false, false]}, {'Какие сведения включаются в подраздел реестра, касающийся сведений, содержащихся в документах, представленных для проведения экспертизы результатов инженерных изысканий?': [{'Сведения о местоположении района (площадки, трассы) проведения изысканий (субъект Российской Федерации, муниципальный район).': [true, false], 'Виды работ по инженерным изысканиям.': [true, false], 'Дата подготовки отчета по результатам инженерных изысканий.': [true, false], 'Сведения о застройщике (техническом заказчике), обеспечившем проведение инженерных изысканий.': [true, false], 'Реквизиты (номер, дата выдачи) положительного заключения экспертизы в отношении применяемой типовой проектной документации (в случае, если для проведения экспертизы результатов инженерных изысканий требуется представление такого заключения).': [false, false], 'Сведения об индивидуальных предпринимателях и (или) юридических лицах, подготовивших технический отчет по результатам инженерных изысканий.': [true, false]}, false, false]}, {'Какие сведения реестра относятся к сведениям, доступ к которым обеспечивается всем заинтересованным лицам на бесплатной основе в информационно-телекоммуникационной сети "Интернет" в форме открытых данных?': [{'Сведения об индивидуальных предпринимателях и (или) юридических лицах, подготовивших проектную документацию.': [true, false], 'Сведения о застройщике (техническом заказчике), обеспечившем подготовку проектной документации.': [true, false], 'Сведения об экспертной организации.': [true, false], 'Наименование и адрес (местоположение) объекта капитального строительства, применительно к которому подготовлена проектная документация.': [true, false], 'Сведения об объекте экспертизы (проектная документация и результаты инженерных изысканий).': [true, false], 'Сведения об источнике финансирования.': [false, false]}, false, false]}, {'Какие сведения реестра относятся к сведениям, доступ к которым обеспечивается всем заинтересованным лицам на бесплатной основе в информационно-телекоммуникационной сети "Интернет" в форме открытых данных?': [{'Номер заключения экспертизы.': [true, false], 'Дата заключения экспертизы.': [true, false], 'Результат проведенной экспертизы (положительное или отрицательное заключение экспертизы).': [true, false], 'Сведения об экспертной организации.': [true, false], 'Сведения о застройщике (техническом заказчике), обеспечившем подготовку проектной документации.': [true, false], 'Сведения об источнике финансирования.': [false, false]}, false, false]}, {'При условии соответствия каким из перечисленных требований, эксперт может быть переаттестован на право подготовки заключений экспертизы проектной документации и (или) результатов инженерных изысканий?': [{'Эксперт должен постоянно проживать в Российской Федерации.': [true, false], 'Эксперт не должен иметь непогашенную или неснятую судимость за совершение умышленного преступления.': [true, false], 'Эксперт должен обладать необходимыми знаниями в области законодательства Российской Федерации о градостроительной деятельности.': [true, false], 'Эксперт должен обладать необходимыми знаниями в области законодательства Российской Федерации о техническом регулировании (включая требования к обеспечению безопасной эксплуатации объектов капитального строительства) в части, касающейся соответственно выполнения инженерных изысканий в целях проектирования, строительства и эксплуатации этих объектов либо осуществления их проектирования, строительства и эксплуатации.': [true, false], 'Эксперт не реже одного раза в 3 года проходит повышение квалификации в организации, ведущей образовательную деятельность, требования к которой установлены федеральным органом исполнительной власти, осуществляющим функции по выработке и реализации государственной политики и нормативно-правовому регулированию в сфере строительства, архитектуры, градостроительства.': [true, false], 'Эксперт должен иметь стаж работы на соответствующих должностях в органах либо организациях, проводящих экспертизу проектной документации и (или) результатов инженерных изысканий, не менее чем 3 года.': [false, false]}, false, false]}, {'Что не входит в понятие «континентальный шельф Российской Федерации»?': [{'Морское дно подводных районов, находящиеся за пределами территориального моря Российской Федерации на всем протяжении естественного продолжения ее сухопутной территории до внешней границы подводной окраины материка.': [false, false], 'Континентальный шельф включает в себя недра подводных районов, находящиеся за пределами территориального моря Российской Федерации на всем протяжении естественного продолжения ее сухопутной территории до внешней границы подводной окраины материка.': [false, false], 'Континентальный шельф включает в себя все острова Российской Федерации, за исключением скал, которые не пригодны для поддержания жизни человека.': [true, false], 'Континентальный шельф включает в себя недра подводных районов, в том числе, находящиеся за пределами территориального моря Российской Федерации на всем протяжении естественного продолжения ее сухопутной территории до внешней границы подводной окраины материка.': [true, false], 'Континентальный шельф включает в себя морское дно, в том числе подводных районов, находящиеся за пределами территориального моря Российской Федерации на всем протяжении естественного продолжения ее сухопутной территории до внешней границы подводной окраины материка.': [true, false], 'Континентальный шельф включает в себя все острова Российской Федерации.': [false, false]}, false, false]}, {'На каком основании квалификационный аттестат эксперта может быть аннулирован?': [{'В случае изменения действующего законодательства, до прохождения экспертом переаттестации.': [false, false], 'Неучастие эксперта в экспертной деятельности в течение трех последовательных календарных лет.': [false, false], 'В случае вступления в законную силу решения уполномоченных органов о привлечении лица, которому выдан квалификационный аттестат, к ответственности за правонарушения в сфере его профессиональной деятельности.': [true, false], 'В случае установления факта участия в подготовке проектной документации и (или) выполнении инженерных изысканий эксперта лично или его близких родственников (родители, дети, усыновители, усыновленные, родные братья, родные сестры, дедушка, бабушка, внуки), супруга.': [true, false], 'В случае установления факта представления для прохождения аттестации документов, содержащих недостоверные сведения.': [true, false], 'В случае установления факта участия эксперта в экспертизе объекта, расположенного на территории субъекта Российской Федерации, в котором эксперт не зарегистрирован.': [false, false]}, false, false]}, {'На какие виды подразделяются объекты капитального строительства в зависимости от функционального назначения и характерных признаков?': [{'Объекты производственного назначения (здания, строения, сооружения производственного назначения).': [true, false], 'Строящиеся и реконструируемые объекты.': [false, false], 'Объекты воздушного и водного транспорта.': [false, false], 'Объекты непроизводственного назначения (здания, строения, сооружения жилищного фонда, социально-культурного и коммунально-бытового назначения, а также иные объекты капитального строительства непроизводственного назначения).': [true, false], 'Линейные объекты (трубопроводы, автомобильные и железные дороги, линии электропередачи и др.).': [true, false], 'Объекты стратегического значения.': [false, false]}, false, false]}, {'Из скольких разделов состоит проектная документация на объекты капитального строительства производственного назначения, непроизводственного назначения и линейные объекты?': [{'Проектная документация на объекты капитального строительства производственного назначения состоит из 13 разделов.': [false, false], 'Проектная документация на объекты капитального строительства производственного назначения состоит из 12 разделов.': [true, false], 'Проектная документация на объекты капитального строительства непроизводственного назначения состоит из 12 разделов.': [true, false], 'Проектная документация на объекты капитального строительства непроизводственного назначения состоит из 13 разделов.': [false, false], 'Проектная документация на линейные объекты капитального строительства состоит из 11 разделов.': [false, false], 'Проектная документация на линейные объекты капитального строительства состоит из 10 разделов.': [true, false]}, false, false]}, {'Какие разделы должны содержаться в составе проектной документации на объекты капитального строительства производственного и непроизводственного назначения?': [{'Раздел "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений".': [true, false], 'Раздел "Перечень мероприятий по охране окружающей среды".': [true, false], 'Раздел "Система водоснабжения".': [false, false], 'Раздел "Проект полосы отвода".': [false, false], 'Раздел "Архитектурные решения".': [true, false], 'Раздел "Технологические решения".': [false, false]}, false, false]}, {'Какие разделы должны содержаться в составе проектной документации на объекты капитального строительства производственного и непроизводственного назначения?': [{'Раздел "Конструктивные и объемно-планировочные решения".': [true, false], 'Раздел "Схема планировочной организации земельного участка".': [true, false], 'Раздел "Отопление, вентиляция и кондиционирование воздуха, тепловые сети".': [false, false], 'Раздел "Мероприятия по обеспечению доступа инвалидов".': [true, false], 'Раздел "Проект организации работ по сносу или демонтажу объектов капитального строительства".': [true, false], 'Раздел "Сети связи".': [false, false]}, false, false]}, {'Какие разделы должны содержаться в составе проектной документации на объекты капитального строительства производственного и непроизводственного назначения?': [{'Раздел "Архитектурные решения".': [true, false], 'Раздел "Технологические решения".': [false, false], 'Раздел "Конструктивные и объемно-планировочные решения".': [true, false], 'Раздел "Проект организации строительства".': [true, false], 'Раздел "Инженерно- технические решения".': [false, false], 'Раздел "Проект полосы отвода".': [false, false]}, false, false]}, {'Какие разделы должны содержаться в составе проектной документации на объекты капитального строительства производственного и непроизводственного назначения?': [{'Раздел "Пояснительная записка".': [true, false], 'Раздел "Система электроснабжения".': [false, false], 'Раздел "Система водоотведения".': [false, false], 'Раздел "Проект организации строительства".': [true, false], 'Раздел "Система газоснабжения".': [false, false], 'Раздел "Мероприятия по обеспечению пожарной безопасности".': [true, false]}, false, false]}, {'Какие разделы должны содержаться в составе проектной документации на линейные объекты капитального строительства?': [{'Раздел "Проект полосы отвода".': [true, false], 'Раздел "Здания, строения и сооружения, входящие в инфраструктуру линейного объекта".': [true, false], 'Раздел "Проект организации работ по сносу (демонтажу) линейного объекта".': [true, false], 'Раздел "Схема планировочной организации земельного участка".': [false, false], 'Раздел "Проект организации строительства".': [true, false], 'Раздел "Технологические решения".': [false, false]}, false, false]}, {'Какие разделы должны содержаться в составе проектной документации на линейные объекты капитального строительства?': [{'Раздел "Мероприятия по охране окружающей среды".': [true, false], 'Раздел "Мероприятия по обеспечению доступа инвалидов".': [false, false], 'Раздел "Мероприятия по обеспечению пожарной безопасности".': [true, false], 'Раздел "Проект организации строительства".': [true, false], 'Раздел "Проект организации работ по сносу или демонтажу объектов капитального строительства".': [false, false], 'Раздел "Проект планировочной организации земельного участка".': [false, false]}, false, false]}, {'По каким признакам идентифицируются здания и сооружения?': [{'По назначению.': [true, false], 'По принадлежности к опасным производственным объектам.': [true, false], 'По наличию помещений с постоянным пребыванием людей.': [true, false], 'По обеспечению доступа инвалидов.': [false, false], 'По конструктивным и объемно-планировочным решениям.': [false, false], 'По пожарной и взрывопожарной опасности.': [true, false]}, false, false]}, {'По каким признакам идентифицируются здания и сооружения?': [{'По принадлежности к уникальным и технически сложным объектам.': [false, false], 'По принадлежности к объектам транспортной инфраструктуры и к другим объектам, функционально-технологические особенности которых влияют на их безопасность.': [true, false], 'По уровню ответственности.': [true, false], 'По радиационной и химической безопасности.': [false, false], 'По пожарной и взрывопожарной опасности.': [true, false], 'По возможности опасных природных процессов и явлений, и техногенных воздействий на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [true, false]}, false, false]}, {'По каким уровням ответственности идентифицируются здания и сооружения?': [{'Простой.': [false, false], 'Повышенный.': [true, false], 'Нормальный.': [true, false], 'Опасный.': [false, false], 'Пониженный.': [true, false], 'Особо опасный.': [false, false]}, false, false]}, {'Где и кем указываются идентификационные признаки зданий и сооружений?': [{'Указываются застройщиком (заказчиком) в договорной документации по выполнению инженерных изысканий для строительства здания или сооружения.': [false, false], 'Указываются застройщиком (заказчиком) в задании на выполнение инженерных изысканий для строительства здания или сооружения.': [true, false], 'Указываются собственником здания или сооружения в исполнительной документации.': [false, false], 'Указываются застройщиком (заказчиком) в договорной документации на проектирование здания или сооружения.': [false, false], 'Указываются лицом, осуществляющим подготовку проектной документации в текстовых материалах в составе проектной документации, передаваемой по окончании строительства на хранение собственнику здания или сооружения.': [true, false], 'Указываются застройщиком (заказчиком) в задании на проектирование.': [true, false]}, false, false]}, {'В каких случаях подготовка проектной документации и строительство здания или сооружения осуществляются в соответствии со специальными техническими условиями?': [{'Если подобное требование содержится в задании на проектирование.': [false, false], 'Если для подготовки проектной документации требуется отступление от требований, установленных национальными стандартами и сводами правил.': [true, false], 'Если подготовку проектной документации экономически целесообразнее проводить в соответствии с требованиями специальных технических условий. ': [false, false], 'Если для подготовки проектной документации недостаточно требований к надежности и безопасности, установленных стандартами и сводами правил.': [true, false], 'Если подготовка проектной документации осуществляется для уникальных зданий и сооружений.': [false, false], 'Если требования не установлены.': [true, false]}, false, false]}, {'В какой форме осуществляется обязательная оценка соответствия зданий и сооружений, а также связанных со зданиями и с сооружениями процессов проектирования (включая изыскания), строительства, монтажа, наладки и утилизации (сноса)?': [{'В форме авторского надзора.': [false, false], 'В форме текущего контроля.': [false, false], 'В форме строительного контроля.': [true, false], 'В форме государственного строительного надзора.': [true, false], 'В форме ввода объекта в эксплуатацию.': [true, false], 'В форме эксплуатационного контроля.': [false, false]}, false, false]}, {'В какой форме осуществляется обязательная оценка соответствия зданий и сооружений, а также связанных со зданиями и с сооружениями процессов эксплуатации требованиям, установленным в проектной документации?': [{'В форме авторского надзора.': [false, false], 'В форме текущего контроля.': [false, false], 'В форме строительного контроля.': [false, false], 'В форме эксплуатационного контроля.': [true, false], 'В форме государственного контроля (надзора).': [true, false], 'В форме технического надзора.': [false, false]}, false, false]}, {'Какие виды специальных технических условий разрабатываются?': [{'Разрабатываются технические требования, в результате применения которых на обязательной основе обеспечивается соблюдение требований Федерального закона от 30.12.2009 № 384-ФЗ.': [true, false], 'Разрабатываются технические требования к безопасности уникальных объектов капитального строительства, в результате применения которых на обязательной основе обеспечивается соблюдение требований Федерального закона от 30.12.2009 № 384-ФЗ.': [false, false], 'Разрабатываются технические требования к промышленной безопасности опасных производственных объектов капитального строительства, в результате применения которых на обязательной основе обеспечивается соблюдение требований Федерального закона от 30.12.2009 № 384-ФЗ.': [true, false], 'Разрабатываются технические требования к безопасности технически сложных объектов капитального строительства, в результате применения которых на обязательной основе обеспечивается соблюдение требований Федерального закона от 30.12.2009 № 384-ФЗ.': [false, false], 'Разрабатываются технические требования к сейсмической безопасности объектов капитального строительства на территориях сейсмичностью более 9 баллов, в результате применения которых на обязательной основе обеспечивается соблюдение требований Федерального закона от 30.12.2009 № 384-ФЗ.': [true, false], 'Разрабатываются технические требования, в результате применения которых на обязательной основе обеспечивается соблюдение требований Федерального закона от 22 июля 2008 года N 123-ФЗ "Технический регламент о требованиях пожарной безопасности".': [false, false]}, false, false]}, {'Что должно содержаться в специальных технических условиях?': [{'Детальное обоснование необходимости разработки СТУ и недостающие нормативные требования для конкретного объекта капитального строительства, излагаемые в соответствии со структурой сводов правил в соответствующей сфере деятельности.': [true, false], 'Перечень вынужденных отступлений от требований, установленных национальными стандартами и сводами правил, содержащий мероприятия, компенсирующие эти отступления.': [true, false], 'Сведения о заинтересованных лицах, проектной организации и (или) разработчике СТУ.': [true, false], 'Описание объекта капитального строительства в целом и его важнейших элементов с изложением объемно-планировочных и конструктивных решений с приложением схемы организации земельного участка и чертежей архитектурно-планировочных решений.': [true, false], 'Основание для строительства (правоустанавливающие документы на земельный участок).': [true, false], 'Данные, включающие наименование и место расположения объекта капитального строительства (адрес объекта капитального строительства, кадастровый номер земельного участка).': [true, false]}, false, false]}, {'Каков срок рассмотрения документации и принятия решения о согласовании проекта специальных технических условий либо об отказе в согласовании проекта специальных технических условий?': [{'Срок рассмотрения документации и принятия решения составляет тридцать календарных дней со дня поступления документации в Министерство строительства и жилищно-коммунального хозяйства Российской Федерации.': [true, false], 'Срок рассмотрения документации и принятия решения составляет сорок пять календарных дней со дня поступления документации в Министерство строительства и жилищно-коммунального хозяйства Российской Федерации.': [false, false], 'Срок рассмотрения документации и принятия решения составляет шестьдесят календарных дней со дня поступления документации в Министерство строительства и жилищно-коммунального хозяйства Российской Федерации.': [false, false], 'В случае направления межведомственного запроса по решению Министра строительства и жилищно-коммунального хозяйства Российской Федерации, срок рассмотрения документации и принятие решения может быть продлен до девяноста календарных дней.': [false, false], 'В случае направления межведомственного запроса по решению Министра строительства и жилищно-коммунального хозяйства Российской Федерации, срок рассмотрения документации и принятие решения может быть продлен до шестидесяти календарных дней.': [true, false], 'В случае направления межведомственного запроса по решению Министра строительства и жилищно-коммунального хозяйства Российской Федерации, срок рассмотрения документации и принятие решения может быть продлен до ста тридцати календарных дней.': [false, false]}, false, false]}, {'Какие из перечисленных объектов использования атомной энергии должны быть идентифицированы как объекты повышенного уровня ответственности?': [{'Атомные станции.': [true, false], 'Суда и другие плавсредства с ядерными реакторами.': [true, false], 'Сооружения и комплексы с промышленными, экспериментальными и исследовательскими ядерными реакторами.': [true, false], 'Радиоактивные отходы.': [true, false], 'Стационарные объекты и сооружения, не относящиеся к ядерным установкам, радиационным источникам и предназначенные для хранения ядерных материалов и радиоактивных веществ, хранения или захоронения радиоактивных отходов.': [true, false], 'Пункты хранения ядерных материалов и радиоактивных веществ.': [true, false]}, false, false]}, {'Какие из перечисленных объектов использования атомной энергии должны быть идентифицированы как объекты повышенного уровня ответственности?': [{'Космические и летательные аппараты с ядерными реакторами.': [true, false], 'Сооружения, комплексы, полигоны, установки и устройства с ядерными зарядами для использования в мирных целях.': [true, false], 'Не относящиеся к ядерным установкам комплексы, установки, аппараты, оборудование и изделия, в которых содержатся радиоактивные вещества или генерируется ионизирующее излучение.': [true, false], 'Материалы, содержащие или способные воспроизвести делящиеся (расщепляющиеся) ядерные вещества.': [true, false], 'Сооружения и комплексы с промышленными, экспериментальными и исследовательскими критическими и подкритическими ядерными стендами.': [true, false], 'Транспортные и транспортабельные средства с ядерными реакторами.': [true, false]}, false, false]}, {'Какие объекты авиационной инфраструктуры должны быть идентифицированы как объекты повышенного уровня ответственности?': [{'Аэродромы.': [true, false], 'Центр управления полетами летательных аппаратов.': [true, false], 'Перроны аэродромов с искусственным покрытием с длиной взлетно-посадочной полосы 1200 метров и более.': [false, false], 'Пункт приема, хранения и обработки информации в области авиационной деятельности.': [true, false], 'Объекты хранения авиационной техники.': [true, false], 'Используемые при осуществлении авиационной деятельности сооружения.': [true, false]}, false, false]}, {'Какие объекты инфраструктуры железнодорожного транспорта общего пользования должны быть идентифицированы как объекты повышенного уровня ответственности?': [{'Железнодорожный подвижной состав.': [false, false], 'Железнодорожные пути общего пользования и другие сооружения.': [true, false], 'Железнодорожные станции.': [true, false], 'Мостовые переходы с опорами высотой от 50 до 100 метров.': [true, false], 'Тоннели длиной более 500 метров.': [true, false], 'Железнодорожные пути необщего пользования.': [false, false]}, false, false]}, {'Что понимается под определениями «декларирование соответствия» и «подтверждение соответствия»?': [{'Декларирование соответствия - документ, удостоверяющий соответствие выпускаемой в обращение продукции требованиям технических регламентов.': [false, false], 'Декларирование соответствия - форма подтверждения соответствия продукции требованиям технических регламентов.': [true, false], 'Декларирование соответствия - обозначение, служащее для информирования приобретателей, в том числе потребителей, о соответствии выпускаемой в обращение продукции требованиям технических регламентов.': [false, false], 'Подтверждение соответствия - прямое или косвенное определение соблюдения требований, предъявляемых к объекту.': [false, false], 'Подтверждение соответствия - установление тождественности характеристик продукции её существующим признакам.': [false, false], 'Подтверждение соответствия - документальное удостоверение соответствия продукции или иных объектов, процессов проектирования (включая изыскания), производства, строительства, монтажа, наладки, эксплуатации, хранения, перевозки, реализации и утилизации, выполнения работ или оказания услуг требованиям технических регламентов, документам по стандартизации или условиям договоров.': [true, false]}, false, false]}, {'Что понимается под определениями «декларация о соответствии» и «сертификат соответствия»?': [{'Декларация о соответствии - документ, удостоверяющий соответствие выпускаемой в обращение продукции требованиям технических регламентов.': [true, false], 'Сертификат соответствия - документ, подтверждающий соответствие объекта требованиям национальных стандартов и условиям договоров.': [false, false], 'Декларация о соответствии - обозначение, служащее для информирования приобретателей, в том числе потребителей, о соответствии объекта сертификации требованиям системы добровольной сертификации.': [false, false], 'Сертификат соответствия - документ, удостоверяющий соответствие объекта требованиям технических регламентов, документам по стандартизации или условиям договоров.': [true, false], 'Декларация о соответствии - установление тождественности характеристик продукции её существующим признакам.': [false, false], 'Сертификат соответствия - документ, служащий для информирования приобретателей о соответствии объекта сертификации требованиям системы добровольной сертификации или национальному стандарту.': [false, false]}, false, false]}, {'Что понимается под определениями «сертификация» и «система сертификации»?': [{'Сертификация - форма осуществляемого органом по сертификации подтверждения соответствия объектов требованиям технических регламентов, документам по стандартизации или условиям договоров.': [true, false], 'Сертификация - прямое или косвенное определение соблюдения требований, предъявляемых к объекту.': [false, false], 'Сертификация - установление тождественности характеристик продукции её существующим признакам.': [false, false], 'Система сертификации - форма подтверждения соответствия продукции требованиям технических регламентов.': [false, false], 'Система сертификации - совокупность правил выполнения работ по сертификации, ее участников и правил функционирования системы сертификации в целом.': [true, false], 'Система сертификации - проверка выполнения юридическим лицом или индивидуальным предпринимателем требований технических регламентов к продукции или к продукции и связанным с требованиями к продукции процессам проектирования (включая изыскания), производства, строительства, монтажа, наладки, эксплуатации, хранения, перевозки, реализации и утилизации и принятие мер по результатам проверки.': [false, false]}, false, false]}, {'Что понимается под определениями «знак обращения на рынке» и «знак соответствия»?': [{'Знак обращения на рынке - обозначение, служащее для информирования приобретателей, в том числе потребителей, о соответствии выпускаемой в обращение продукции требованиям технических регламентов.': [true, false], 'Знак обращения на рынке - документальное удостоверение соответствия продукции или иных объектов, процессов проектирования (включая изыскания), производства, строительства, монтажа, наладки, эксплуатации, хранения, перевозки, реализации и утилизации, выполнения работ или оказания услуг требованиям технических регламентов, документам по стандартизации или условиям договоров.': [false, false], 'Знак обращения на рынке - обозначение продукции, которая ранее не находилась в обращении на территории Российской Федерации либо которая ранее выпускалась в обращение и свойства или характеристики которой были впоследствии изменены.': [false, false], 'Знак соответствия - документ, удостоверяющий соответствие объекта требованиям технических регламентов, документам по стандартизации или условиям договоров.': [false, false], 'Знак соответствия - обозначение, служащее для информирования приобретателей, в том числе потребителей, о соответствии объекта сертификации требованиям системы добровольной сертификации.': [true, false], 'Знак соответствия - обозначение, удостоверяющие соответствие выпускаемой в обращение продукции требованиям технических регламентов.': [false, false]}, false, false]}, {'В каких формах осуществляется обязательное подтверждение соответствия?': [{'В форме добровольной сертификации.': [false, false], 'В форме принятия декларации о соответствии.': [true, false], 'За счёт внесения в единый перечень продукции, подлежащей обязательной сертификации.': [false, false], 'В форме обязательной сертификации.': [true, false], 'В форме стандартизации.': [false, false], 'В форме контроля (надзора) за соблюдением требований технических регламентов.': [false, false]}, false, false]}, {'Какие функции выполняет орган по сертификации?': [{'Осуществляет подтверждение соответствия объектов добровольного подтверждения соответствия.': [true, false], 'Ведет единый реестр зарегистрированных систем добровольной сертификации, содержащий сведения о юридических лицах и (или) об индивидуальных предпринимателях, создавших системы добровольной сертификации.': [false, false], 'Выдает сертификаты соответствия на объекты, прошедшие добровольную сертификацию.': [true, false], 'Предоставляет заявителям право на применение знака соответствия, если применение знака соответствия предусмотрено соответствующей системой добровольной сертификации.': [true, false], 'Утверждает документы по стандартизации.': [false, false], 'Приостанавливает или прекращает действие выданных им сертификатов соответствия.': [true, false]}, false, false]}, {'Какие документы могут включаться в перечень документов по стандартизации, в результате применения которых на добровольной основе обеспечивается соблюдение требований принятого технического регламента?': [{'Национальные стандарты.': [true, false], 'Стандарты иностранных государств при условии регистрации указанных стандартов в Федеральном информационном фонде технических регламентов и стандартов.': [true, false], 'Международные стандарты.': [true, false], 'Региональные стандарты.': [true, false], 'Отраслевые стандарты.': [false, false], 'Стандарты предприятий.': [false, false]}, false, false]}, {'Какие документы могут включаться в перечень документов по стандартизации, в результате применения которых на добровольной основе обеспечивается соблюдение требований принятого технического регламента?': [{'Специальные технические условия.': [false, false], 'Региональные своды правил.': [true, false], 'Отраслевые своды правил.': [false, false], 'Национальные стандарты.': [true, false], 'Своды правил иностранных государств при условии регистрации указанных сводов правил в Федеральном информационном фонде технических регламентов и стандартов.': [true, false], 'Методические рекомендации.': [false, false]}, false, false]}, {'Что относится к обязательным техническим требованиям, обеспечивающим достижение показателей, характеризующих выполнение требований энергетической эффективности, вводимых с момента установления требований энергетической эффективности?': [{'Требования к отдельным элементам, конструкциям зданий, строений, сооружений и к их свойствам, позволяющие исключить нерациональный расход энергетических ресурсов как в процессе строительства, реконструкции, капитального ремонта зданий, строений, сооружений, так и в процессе их эксплуатации.': [true, false], 'Показатели, характеризующие удельную величину расхода энергетических ресурсов в здании, строении, сооружении.': [true, false], 'Требования к влияющим на энергетическую эффективность зданий, строений, сооружений архитектурным, функционально-технологическим, конструктивным и инженерно-техническим решениям.': [true, false], 'Требования к используемым в зданиях, строениях, сооружениях устройствам и технологиям, позволяющие исключить нерациональный расход энергетических ресурсов как в процессе строительства, реконструкции, капитального ремонта зданий, строений, сооружений, так и в процессе их эксплуатации.': [true, false], 'Требования по интеграции в энергетический баланс зданий, строений, сооружений нетрадиционных источников энергии и вторичных энергоресурсов.': [false, false], 'Требования к включаемым в проектную документацию и применяемым при строительстве, реконструкции, капитальном ремонте зданий, строений, сооружений технологиям и материалам, позволяющие исключить нерациональный расход энергетических ресурсов как в процессе строительства, реконструкции, капитального ремонта зданий, строений, сооружений, так и в процессе их эксплуатации.': [true, false]}, false, false]}, {'Что является объектом технического регулирования?': [{'Технологические процессы, осуществляемые в зданиях и сооружениях в соответствии с их функциональным назначением, а также располагаемое в них технологическое оборудование.': [false, false], 'Здания и сооружения любого назначения (в том числе входящие в их состав сети инженерно-технического обеспечения и системы инженерно-технического обеспечения).': [true, false], 'Временные здания и сооружения, не являющиеся объектами капитального строительства.': [false, false], 'Требования охраны труда в строительстве, а также при эксплуатации и ликвидации зданий и сооружений.': [false, false], 'Связанные со зданиями и с сооружениями процессы проектирования (включая изыскания), строительства, монтажа, наладки, эксплуатации и утилизации (сноса).': [true, false], 'Строительные материалы и изделия для изготовления, возведения и (или) монтажа строительных конструкций и систем инженерного обеспечения зданий и сооружений.': [false, false]}, false, false]}, {'Применительно к каким объектам осуществляется архитектурно-строительное проектирование?': [{'Частям объектов капитального строительства.': [true, false], 'Строящимся объектам капитального строительства.': [true, false], 'Демонтируемым объектам капитального строительства.': [false, false], 'Проектируемым объектам капитального строительства.': [false, false], 'Реконструируемым объектам капитального строительства.': [true, false], 'Переустраиваемым объектам капитального строительства.': [false, false]}, false, false]}, {'В каких границах осуществляется архитектурно-строительное проектирование?': [{'В границах проектирования, установленных заданием на проектирование.': [false, false], 'В границах элемента планировочной структуры, установленных документацией по планировке территории.': [false, false], 'В границах зоны размещения объектов капительного строительства, установленных документацией по планировке территории.': [false, false], 'В границах земельного участка, принадлежащего застройщику.': [true, false], 'В границах населенных пунктов.': [false, false], 'В границах земельного участка, принадлежащего правообладателю, которому органы местного самоуправления передали такой земельный участок в случаях, установленных бюджетным законодательством Российской Федерации.': [true, false]}, false, false]}, {'Что включает в себя проектная документация?': [{'Материалы в текстовой форме.': [true, false], 'Основные комплекты рабочих чертежей.': [false, false], 'Материалы в графической форме.': [true, false], 'Материалы в форме информационной модели.': [true, false], 'Спецификации оборудования, изделий и материалов.': [false, false], 'Материалы, определяющие архитектурные, конструктивные, функционально-технологические решения.': [true, false]}, false, false]}, {'Какие решения должны определять материалы в составе проектной документации?': [{'Функционально-технологические.': [true, false], 'Обоснованные.': [false, false], 'Математические.': [false, false], 'Инженерно-технические. ': [true, false], 'Конструктивные.': [true, false], 'Организационные.': [false, false]}, false, false]}, {'В каких случаях осуществление подготовки проектной документации не требуется?': [{'Для производственных объектов площадью менее 1500 м.кв. и количеством этажей не более, чем 2.': [false, false], 'При реконструкции объектов индивидуального жилищного строительства.': [true, false], 'При капитальном ремонте.': [false, false], 'При строительстве объектов индивидуального жилищного строительства.': [true, false], 'При техническом перевооружении в составе реконструкции производственного объекта.': [false, false], 'Для канатных дорог.': [false, false]}, false, false]}, {'Какие объекты следует относить к объектам индивидуального жилищного строительства?': [{'Отдельно стоящий блокированный жилой дом с количеством этажей два, предназначенный для проживания не более двух семей.': [false, false], 'Отдельно стоящий жилой дом с количеством этажей три, предназначенный для проживания одной семьи.': [true, false], 'Отдельно стоящий блокированный жилой дом с количеством этажей два и мансардным этажом, предназначенный для проживания не более двух семей.': [false, false], 'Отдельно стоящий жилой дом с количеством надземных этажей три и подвальным этажом, предназначенный для проживания одной семьи.': [true, false], 'Отдельно стоящий жилой дом с количеством надземных этажей два, подвальным и мансардным этажами, предназначенный для проживания двух семей.': [false, false], 'Отдельно стоящий жилой дом с количеством этажей два, предназначенный для проживания одной семьи.': [true, false]}, false, false]}, {'Работы по каким договорам о подготовке проектной документации могут выполняться индивидуальными предпринимателями или юридическими лицами, не являющимися членами саморегулируемых организаций в области архитектурно-строительного проектирования?': [{'Заключенными с застройщиком.': [false, false], 'Заключенными с лицом, осуществляющем строительство.': [true, false], 'Заключенными с лицом, осуществляющим подготовку проектной документации.': [true, false], 'Заключенными с лицом, осуществляющим строительный контроль.': [true, false], 'Заключенными с региональным оператором.': [false, false], 'Заключенными с лицом, ответственным за эксплуатацию здания.': [false, false]}, false, false]}, {'В каких случаях при подготовке проектной документации не требуется членство в саморегулируемых организациях в области архитектурно-строительного проектирования?': [{'Для коммерческих организаций, в уставных капиталах которых юридических лиц, созданных публично-правовыми образованиями , составляет пятьдесят процентов в случае заключения такими коммерческими организациями договоров подряда на подготовку проектной документации с местными органами исполнительной власти.': [false, false], 'Если договор о подготовке проектной документации заключен с региональным оператором.': [false, false], 'Для коммерческих организаций, в уставных капиталах которых доля государственных и муниципальных унитарных предприятий, государственных и муниципальных автономных учреждений составляет более пятидесяти процентов, в случае заключения такими коммерческими организациями договоров подряда на подготовку проектной документации с федеральными органами исполнительной власти.': [true, false], 'Для коммерческих организаций, в уставных капиталах которых доля государственных и муниципальных унитарных предприятий, государственных и муниципальных автономных учреждений составляет пятьдесят и более процентов, в случае заключения такими коммерческими организациями договоров подряда на подготовку проектной документации с федеральными органами исполнительной власти.': [false, false], 'Для муниципальных унитарных предприятий в случае заключения ими договоров подряда на подготовку проектной документации с федеральными органами исполнительной власти.': [true, false], 'Если договор о подготовке проектной документации заключен с лицом, осуществляющим строительство.': [true, false]}, false, false]}, {'Кто может являться лицом, осуществляющим подготовку проектной документации?': [{'Застройщик (при условии, что он является членом саморегулируемой организации в области архитектурно-строительного проектирования).': [true, false], 'Технический заказчик.': [false, false], 'Региональный оператор.': [false, false], 'Лицо, ответственное за эксплуатацию здания, сооружения.': [false, false], 'Индивидуальный предприниматель, заключивший договор подряда на подготовку проектной документации.': [true, false], 'Юридическое лицо, заключившие договор подряда на подготовку проектной документации.': [true, false]}, false, false]}, {'Какие материалы должны быть предоставлены лицу, осуществляющему подготовку проектной документации на основании договора подряда, застройщиком?': [{'Градостроительный план земельного участка для нелинейного объекта.': [true, false], 'Результаты инженерных изысканий (в случае, если они отсутствуют, договором подряда на подготовку проектной документации должно быть предусмотрено задание на выполнение инженерных изысканий).': [true, false], 'Календарный график работ.': [false, false], 'Контролируемые климатические параметры района строительства.': [false, false], 'Технические условия (в случае, если функционирование проектируемого объекта капитального строительства невозможно обеспечить без подключения такого объекта к сетям инженерно-технического обеспечения).': [true, false], 'Перечень технологического оборудования с указанием нагрузок и условий присоединения к сетям инженерно-технического обеспечения.': [false, false]}, false, false]}, {'Какие материалы могут не предоставляться техническим заказчиком лицу, осуществляющему подготовку проектной документации на основании договора подряда?': [{'Градостроительный план земельного участка для нелинейного объекта.': [false, false], 'Результаты инженерных изысканий в случае, если договором подряда на подготовку проектной документации предусмотрено задание на выполнение инженерных изысканий.': [true, false], 'Проект планировки территории для объектов жилищного строительства.': [true, false], 'Проектная документация повторного применения.': [true, false], 'Кадастровый план земельного участка.': [true, false], 'Технические условия (в случае, если функционирование проектируемого объекта капитального строительства невозможно обеспечить без подключения (технологического присоединения) такого объекта к сетям инженерно-технического обеспечения).': [false, false]}, false, false]}, {'Кем и на какой срок устанавливается действие предоставленных технических условий?': [{'Местными органами исполнительной власти не менее, чем на три года.': [false, false], 'Федеральными органами исполнительной власти не менее, чем на три года по федеральным целевым программам.': [false, false], 'Организациями, осуществляющими эксплуатацию сетей инженерно-технического обеспечения не менее, чем на три года.': [true, false], 'Организациями, осуществляющими эксплуатацию сетей инженерно-технического обеспечения не менее, чем на пять лет при комплексном освоении земельных участков в целях жилищного строительства.': [true, false], 'Лицом, осуществляющим инженерные изыскания в составе инженерных изысканий не менее, чем на три года.': [false, false], 'Правообладателем земельного участка на срок не более, чем на пять лет.': [false, false]}, false, false]}, {'В каких случаях прекращаются обязательства организации, предоставившей технические условия, предусматривающие максимальную нагрузку, сроки подключения объектов капитального строительства к сетям инженерно-технического обеспечения и срок действия технических условий?': [{'Если в течение шести месяцев с момента предоставления правообладателю земельного участка технических условий, он не определит необходимую ему для подключения к сетям инженерно-технического обеспечения нагрузку.': [false, false], 'Если в течение одного года с момента предоставления правообладателю земельного участка технических условий, он не определит необходимую ему для подключения к сетям инженерно-технического обеспечения нагрузку. ': [true, false], 'Если при комплексном освоении земельного участка в целях жилищного строительства в течение шести месяцев с момента предоставления правообладателю земельного участка технических условий он не подаст заявку подключении по таким техническим условиям.': [false, false], 'Если при комплексном освоении земельного участка в целях жилищного строительства в течение одного года с момента предоставления правообладателю земельного участка технических условий он не подаст заявку подключении по таким техническим условиям.': [false, false], 'Если при комплексном освоении земельного участка в целях жилищного строительства в течение двух лет с момента предоставления правообладателю земельного участка технических условий он не подаст заявку подключении по таким техническим условиям.': [false, false], 'Если при комплексном освоении земельного участка в целях жилищного строительства в течение трех лет с момента предоставления правообладателю земельного участка технических условий он не подаст заявку подключении по таким техническим условиям.': [true, false]}, false, false]}, {'В соответствии с какими данными организация, осуществляющая эксплуатацию сетей инженерно-технического обеспечения, обязана обеспечить правообладателю земельного участка в установленные сроки подключение объекта к сетям инженерно-технического обеспечения?': [{'В соответствии с проектной документацией.': [false, false], 'В соответствии с техническими условиями.': [true, false], 'В соответствии с градостроительным планом земельного участка.': [false, false], 'В соответствие с информацией о плате за подключение.': [true, false], 'В соответствии с документацией по планировке территории.': [false, false], 'В соответствии с договором.': [false, false]}, false, false]}, {'На кого возложена обязанность предоставления заинтересованным лицам технических условий подключения (технологического присоединения) к сетям инженерно-технического обеспечения до дня принятия решения о предоставлении земельного участка, находящегося в государственной или муниципальной собственности?': [{'На исполнительный орган государственной власти, уполномоченный на распоряжение земельными участками, находящимися в государственной собственности.': [true, false], 'На организации, осуществляющие эксплуатацию сетей инженерно-технического обеспечения.': [false, false], 'На лица, являющиеся собственниками сетей инженерно-технического обеспечения.': [false, false], 'На федеральный орган исполнительной власти применительно к объектам капитального строительства, планируемых к строительству на территории двух и более субъектов федерации.': [false, false], 'На регионального оператора, осуществляющего деятельность по капитальному ремонту объектов жилого назначения.': [false, false], 'На орган местного самоуправления, уполномоченный на распоряжение земельными участками, находящимися в муниципальной собственности.': [true, false]}, false, false]}, {'На основании каких исходных данных осуществляется подготовка проектной документации?': [{'Задание застройщика или технического заказчик