ечения.': [true, false], 'Декларация пожарной безопасности.': [false, false], 'Проект организации строительства объектов капитального строительства.': [true, false], 'Сведения о нормативной периодичности выполнения работ по капитальному ремонту объекта капитального строительства, необходимых для обеспечения безопасной эксплуатации такого объекта, а также сведения об объеме и составе указанных работ.': [true, false], 'Генеральный план.': [false, false], 'Проект организации дорожного движения.': [false, false]}, false, false]}, {'Какие разделы включаются в состав проектной документации нелинейных опасных производственных объектов капитального строительства?': [{'Разделы, содержащие инженерно-технические решения.': [true, false], 'Разделы, содержащие перечень мероприятий, направленные на обеспечение соблюдения требований к обеспечению надежности и безопасности электроэнергетических систем и объектов электроэнергетики.': [true, false], 'Разделы, содержащие перечень мероприятий, направленные на обеспечение соблюдения требований энергетической эффективности.': [true, false], 'Перечень мероприятий по обеспечению механической, пожарной и иной безопасности.': [true, false], 'Декларация пожарной безопасности.': [false, false], 'Разделы, содержащие функционально-технологические решения.': [true, false]}, false, false]}, {'Для каких объектов в состав проектной документации включается раздел «перечень мероприятий по обеспечению доступа инвалидов»?': [{'К объектам транспорта.': [true, false], 'К объектам религиозного назначения.': [true, false], 'К объектам жилищного фонда.': [true, false], 'К объектам образования.': [true, false], 'К объектам культуры.': [true, false], 'К объектам делового назначения.': [true, false]}, false, false]}, {'В каких случаях в состав проектной документации включается раздел "проект организации работ по сносу или демонтажу объектов капитального строительства"?': [{'При необходимости демонтажа частей объектов капитального строительства для реконструкции других объектов капитального строительства.': [true, false], 'При необходимости сноса или демонтажа временных зданий и сооружений, расположенных на земельном участке, предоставленном для строительства другого объекта капитального строительства.': [false, false], 'При необходимости сноса объектов капитального строительства для строительства других объектов капитального строительства.': [true, false], 'При необходимости сноса аварийных объектов капитального строительства вследствие чрезвычайных ситуаций.': [false, false], 'При демонтаже подъемных машин и механизмов.': [false, false], 'При сносе объектов капитального строительства вследствие сейсмических воздействий.': [false, false]}, false, false]}, {'В каких случаях в состав проектной документации включается раздел "смета"?': [{'На строительство и реконструкцию объектов капитального строительства, финансируемые с привлечением средств бюджетов бюджетной системы Российской Федерации.': [true, false], 'На проведение работ по сохранению объектов культурного наследия, финансируемые с привлечением средств бюджетов бюджетной системы Российской Федерации.': [true, false], 'На проведение природоохранных мероприятий, финансируемых с привлечением средств юридических лиц, доля в уставных капиталах которых Российской Федерации более 50 процентов.': [false, false], 'На проведения работ по благоустройству территорий, финансируемые с привлечением средств бюджетов бюджетной системы Российской Федерации.': [false, false], 'На проведение работ по перепланировке и переустройству помещений многоквартирных жилых домов, финансируемых с привлечением средств юридических лиц, созданных субъектами Российской Федерации.': [false, false], 'На капитальный ремонт объектов капитального строительства, финансируемые с привлечением средств регионального оператора.': [true, false]}, false, false]}, {'По чьей инициативе подготовка проектной документации может осуществляться применительно к отдельным этапам строительства, реконструкции объектов капитального строительства?': [{'Застройщика.': [true, false], 'Технического заказчика.': [true, false], 'Лица, осуществляющего эксплуатацию объекта капитального строительства.': [false, false], 'Лица, осуществляющего подготовку проектной документации.': [false, false], 'Лица, осуществляющего строительство.': [false, false], 'Регионального оператора.': [false, false]}, false, false]}, {'В каких случаях подготовка проектной документации может осуществляться применительно к отдельным этапам?': [{'При строительстве.': [true, false], 'При реконструкции.': [true, false], 'При капитальном ремонте.': [false, false], 'При перепланировке.': [false, false], 'При переустройстве.': [false, false], 'При выводе из эксплуатации.': [false, false]}, false, false]}, {'Кем определяется состав разделов проектной документации при проведении капитального ремонта объектов капитального строительства?': [{'Застройщиком.': [true, false], 'Техническим заказчиком.': [true, false], 'Лицом, осуществляющим эксплуатацию объекта капитального строительства.': [false, false], 'Лицом, осуществляющим подготовку проектной документации.': [false, false], 'Лицом, осуществляющего строительство.': [false, false], 'Региональным оператором.': [false, false]}, false, false]}, {'К какой проектной документации Правительство Российской Федерации устанавливает требования по составу и содержанию?': [{'Применительно к объектам благоустройства.': [false, false], 'Применительно к отдельным этапам реконструкции.': [true, false], 'Применительно к проектной документации, представляемой на экспертизу проектной документации.': [true, false], 'Применительно к проектной документации, представляемой в органы государственного строительного надзора.': [true, false], 'Применительно к особо опасным и технически сложным объектам.': [false, false], 'Применительно к перепланировке и переустройству помещений многоквартирных жилых домов.': [false, false]}, false, false]}, {'К какой проектной документации Правительство Российской Федерации не устанавливает требования по составу и содержанию?': [{'Применительно к объектам благоустройства.': [true, false], 'Применительно к отдельным этапам реконструкции.': [false, false], 'Применительно к проектной документации, представляемой на экспертизу проектной документации.': [false, false], 'Применительно к проектной документации, представляемой в органы государственного строительного надзора.': [false, false], 'Применительно к водным объектам.': [true, false], 'Применительно к перепланировке и переустройству помещений многоквартирных жилых домов.': [true, false]}, false, false]}, {'Какие мероприятия должна содержать проектная документация опасных производственных объектов?': [{'Перечень мероприятий по гражданской обороне.': [true, false], 'Перечень мероприятий по противодействию терроризму.': [true, false], 'Перечень мероприятий по защите от грызунов (по дератизации).': [false, false], 'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера.': [true, false], 'Перечень мероприятий по обеспечению доступа инвалидов.': [false, false], 'Перечень мероприятий по удалению избытков грунта.': [false, false]}, false, false]}, {'Какие мероприятия должна содержать проектная документация уникальных объектов?': [{'Перечень мероприятий по гражданской обороне.': [true, false], 'Перечень мероприятий по противодействию терроризму.': [true, false], 'Перечень мероприятий по недопущению сверхнормативных осадок.': [false, false], 'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера.': [true, false], 'Технико-экономическое обоснование.': [false, false], 'Перечень мероприятий по рекультивации земель.': [false, false]}, false, false]}, {'Кем утверждается проектная документация при наличии положительного заключения экспертизы проектной документации?': [{'Застройщиком.': [true, false], 'Техническим заказчиком.': [true, false], 'Лицом, осуществляющим эксплуатацию объекта капитального строительства.': [false, false], 'Лицом, осуществляющим подготовку проектной документации.': [false, false], 'Лицом, осуществляющим строительство.': [false, false], 'Региональным оператором.': [false, false]}, false, false]}, {'Кем осуществляется согласование проектной документации на проведение работ по сохранению объекта культурного наследия?': [{'Федеральным органом охраны объектов культурного наследия - в отношении отдельных объектов культурного наследия федерального значения, перечень которых утверждается Правительством Российской Федерации.': [true, false], 'Федеральным органом исполнительной власти, осуществляющим функции по выработке и реализации государственной политики и нормативно-правовому регулированию в сфере строительства, архитектуры, градостроительства.': [false, false], 'Региональным органом охраны объектов культурного наследия - в отношении объектов культурного наследия федерального значения (за исключением отдельных объектов культурного наследия федерального значения, перечень которых утверждается Правительством Российской Федерации), объектов культурного наследия регионального значения, выявленных объектов культурного наследия.': [true, false], 'Муниципальным органом охраны объектов культурного наследия - в отношении объектов культурного наследия местного (муниципального) значения.': [true, false], 'Органом исполнительной власти субъекта Российской Федерации, уполномоченными на проведение государственной экспертизы проектной документации.': [false, false], 'Юридическими лицами, аккредитованными на право проведения негосударственной экспертизы.': [false, false]}, false, false]}, {'Какие федеральные органы исполнительной власти вправе уточнять отдельные требования к содержанию разделов в отношении проектной документации на объекты военной инфраструктуры и объекты безопасности?': [{'Федеральная служба исполнения наказаний.': [false, false], 'Министерство по делам гражданской обороны, чрезвычайным ситуациям и ликвидации последствий стихийных бедствий.': [false, false], 'Федеральное агентство правительственной связи и информации при Президенте Российской Федерации.': [false, false], 'Федеральная служба безопасности Российской Федерации.': [true, false], 'Федеральная служба по экологическому, технологическому и атомному надзору.': [false, false], 'Министерство обороны Российской Федерации.': [true, false]}, false, false]}, {'На какие виды подразделяются объекты капитального строительства в зависимости от функционального назначения и характерных признаков?': [{'Гидротехнические объекты.': [false, false], 'Объекты производственного назначения.': [true, false], 'Объекты обороны.': [false, false], 'Линейные объекты.': [true, false], 'Объекты непроизводственного назначения.': [true, false], 'Уникальные.': [false, false]}, false, false]}, {'Что содержит текстовая часть проектной документации?': [{'Описание принятых технических и иных решений.': [true, false], 'Расчетные обоснования.': [false, false], 'Ссылки на нормативные и (или) технические документы, используемые при подготовке проектной документации.': [true, false], 'Результаты расчетов.': [true, false], 'Спецификации материалов и оборудования.': [false, false], 'Выдержки из нормативных и (или) технических документы, обосновывающие принятые технические решения.': [false, false]}, false, false]}, {'В каком виде выполняется графическая часть проектной документации?': [{'Другие документы в графической форме.': [true, false], 'Видеоматериалов.': [false, false], 'Чертежей.': [true, false], 'Схем.': [true, false], 'Планов.': [true, false], 'Информационных моделей.': [true, false]}, false, false]}, {'В целях реализации каких решений, содержащихся в проектной документации, разрабатывается рабочая документация?': [{'Технологические решения.': [true, false], 'Организационные решения.': [false, false], 'Экономические решения.': [false, false], 'Технические решения.': [true, false], 'Архитектурные решения.': [true, false], 'Управленческие решения.': [false, false]}, false, false]}, {'В каких случаях подготовке проектной документации должна предшествовать разработка специальных технических условий?': [{'Если недостаточно требований по надежности и безопасности, установленных нормативными техническими документами.': [true, false], 'Для особо опасных и технически сложных объектов.': [false, false], 'Для уникальных объектов.': [false, false], 'Если нормативными техническими документами не установлены требования по надежности и безопасности.': [true, false], 'Если федеральными законами не установлены требования по надежности и безопасности.': [false, false], 'Для объектов повышенного уровня ответственности.': [false, false]}, false, false]}, {'Что входит в состав исходных данных, подлежащих включению в раздел «пояснительная записка» для объектов производственного и непроизводственного назначения?': [{'Кадастровый план земельного участка.': [false, false], 'Технические условия.': [true, false], 'Документация по планировке территории.': [false, false], 'Разрешение на отклонения от предельных параметров разрешенного строительства объектов капитального строительства.': [true, false], 'Задание на проектирование.': [true, false], 'Утвержденный и зарегистрированный в установленном порядке градостроительный план земельного участка, предоставленного для размещения объекта капитального строительства.': [true, false]}, false, false]}, {'Какие сведения о потребности объекта капитального строительства в инженерно-техническом обеспечении должны быть приведены в разделе «пояснительная записка» для объектов производственного и непроизводственного назначения?': [{'В емкости присоединяемой сети связи.': [false, false], 'В топливе.': [true, false], 'В воде.': [true, false], 'Расчетный объем дождевых стоков.': [false, false], 'В газе.': [true, false], 'В электрической энергии.': [true, false]}, false, false]}, {'Какие сведения для объектов производственного назначения должны быть приведены в разделе «пояснительная записка» проектной документации?': [{'Сведения о значимости объекта капитального строительства для поселений.': [false, false], 'Сведения о сырьевой базе, потребности производства в воде, топливно-энергетических ресурсах.': [true, false], 'Сведения о комплексном использовании сырья, вторичных энергоресурсов, отходов производства.': [true, false], 'Сведения о численности работников и их профессионально-квалификационном составе.': [false, false], 'Сведения о значимости объекта капитального строительства для муниципальных образований.': [false, false], 'Данные о проектной мощности объекта капитального строительства.': [true, false]}, false, false]}, {'Какие зоны имеют особые условия использования территорий? ': [{'Особые условия использования территорий имеют охранные зоны.': [true, false], 'Особые условия использования территорий имеют санитарно-защитные зоны.': [true, false], 'Особые условия использования территорий имеют водоохранные зоны.': [true, false], 'Особые условия использования территорий имеют курортные зоны.': [false, false], 'Особые условия использования территорий имеют рыбоохранные зоны Ладожского озера и озера Байкал.': [false, false], 'Особые условия использования территорий имеют зоны санитарной охраны источников питьевого и хозяйственно-бытового водоснабжения.': [true, false]}, false, false]}, {'Что входит в понятие "реконструкция объектов капитального строительства (за исключением линейных объектов)"?': [{'В понятие "реконструкция объектов капитального строительства" входит надстройка здания.': [true, false], 'В понятие "реконструкция объектов капитального строительства" входит перестройка здания.': [true, false], 'В понятие "реконструкция объектов капитального строительства" входит расширение объектов капитального строительства.': [true, false], 'В понятие "реконструкция объектов капитального строительства" входит изменение параметров объектов капитального строительства.': [true, false], 'В понятие "реконструкция объектов капитального строительства" входит замена отдельных элементов строительных конструкций.': [false, false], 'В понятие "реконструкция объектов капитального строительства" входит восстановление несущих строительных конструкций.': [true, false]}, false, false]}, {'В каких целях проводятся инженерные изыскания?': [{'В целях получения материалов о природных условиях территории, на которой будут осуществляться строительство, реконструкция объектов капитального строительства.': [true, false], 'В целях изучения факторов техногенного воздействия на окружающую среду.': [true, false], 'В целях подготовки данных по обоснованию материалов, необходимых для территориального планирования.': [false, false], 'С целью получения материалов, необходимых для обоснования компоновки зданий, строений, сооружений, принятия конструктивных и объемно-планировочных решений.': [true, false], 'В целях подготовки данных по обоснованию материалов, необходимых для территориального зонирования. ': [false, false], 'В целях получения материалов, необходимых для проведения расчетов оснований, фундаментов и конструкций зданий, строений, сооружений.': [true, false]}, false, false]}, {'Какие функции обеспечивает застройщик?': [{'Застройщик обеспечивает снос объектов капитального строительства. ': [true, false], 'Застройщик обеспечивает работу по реконструкции объектов капитального строительства.': [true, false], 'Застройщик обеспечивает работу по проведению капитального ремонта объектов капитального строительства.': [true, false], 'Застройщик обеспечивает выполнение инженерных изысканий.': [true, false], 'Застройщик обеспечивает подготовку оформление земельных отношений.': [false, false], 'Застройщик обеспечивает подготовку проектной документации для строительства.': [true, false]}, false, false]}, {'Какие функции выполняет технический заказчик - юридическое лицо, которое уполномочено застройщиком?': [{'Технический заказчик подписывает документы, необходимые для получения разрешения на ввод объекта капитального строительства в эксплуатацию.': [true, false], 'Технический заказчик заключает договоры о выполнении инженерных изысканий подготовке проектной документации, о строительстве, реконструкции, капитальном ремонте. ': [true, false], 'Технический заказчик подготавливает задания на выполнение указанных видов работ, предоставляет лицам, выполняющим инженерные изыскания и (или) осуществляющим подготовку проектной документации, строительство, реконструкцию, капитальный ремонт объектов капитального строительства, материалы и документы, необходимые для выполнения указанных видов работ.': [true, false], 'Технический заказчик утверждает отчеты об инженерных изысканиях.': [false, false], 'Технический заказчик утверждает проектную документацию.': [true, false], 'Технический заказчик оформляет земельно-имущественные отношения.': [false, false]}, false, false]}, {'Что такое элементы благоустройства?': [{'Элементами благоустройства являются различные виды декоративные, технические, планировочные, конструктивные устройства и элементы озеленения.': [true, false], 'Элементами благоустройства являются различные виды оборудования и оформления, в том числе фасадов зданий, строений, сооружений.': [true, false], 'Элементами благоустройства являются малые архитектурные формы, некапитальные нестационарные строения и сооружения.': [true, false], 'Элементами благоустройства являются информационные щиты и указатели.': [true, false], 'Элементами благоустройства являются тротуары и пешеходные дорожки, открытые плоскостные автостоянки.': [false, false], 'Элементами благоустройства являются придомовые территории в границах красных линий застройки.': [false, false]}, false, false]}, {'Какие полномочия относятся к компетенции органов государственной власти субъектов Российской Федерации в области градостроительной деятельности? ': [{'К компетенции органов государственной власти субъектов Российской Федерации в области градостроительной деятельности относится подготовка и утверждение документов территориального планирования двух и более субъектов Российской Федерации, документов территориального планирования субъекта Российской Федерации.': [true, false], 'К компетенции органов государственной власти субъектов Российской Федерации в области градостроительной деятельности относится утверждение документации по планировке территории в случаях, предусмотренных градостроительным Кодексом. ': [true, false], 'К компетенции органов государственной власти субъектов Российской Федерации в области градостроительной деятельности относится утверждение региональных нормативов градостроительного проектирования.': [true, false], 'К компетенции органов государственной власти субъектов Российской Федерации в области градостроительной деятельности относится осуществление регионального государственного строительного надзора в случаях, предусмотренных градостроительным Кодексом.': [true, false], 'К компетенции органов государственной власти субъектов Российской Федерации в области градостроительной деятельности относится согласование проектов схем территориального планирования муниципальных районов, проектов генеральных планов поселений, городских округов, проектов правил землепользования и застройки в случаях, предусмотренных градостроительным Кодексом.': [true, false], 'К компетенции органов государственной власти субъектов Российской Федерации в области градостроительной деятельности относится осуществление мониторинга разработки и утверждения программ комплексного развития систем коммунальной инфраструктуры поселений, городских округов, программ комплексного развития транспортной инфраструктуры поселений, городских округов, программ комплексного развития социальной инфраструктуры поселений, городских округов.': [true, false]}, false, false]}, {'Какие виды инженерных изысканий относятся к специальным?': [{'К специальным инженерным изысканиям относятся геотехнические исследования.': [true, false], 'К специальным инженерным изысканиям относятся обследования состояния грунтов основания зданий и сооружений.': [true, false], 'К специальным инженерным изысканиям относятся поиск и разведка подземных вод для целей водоснабжения.': [true, false], 'К специальным инженерным изысканиям относятся локальный мониторинг компонентов окружающей среды.': [true, false], 'К специальным инженерным изысканиям относятся разведка грунтовых строительных материалов.': [true, false], 'К специальным инженерным изысканиям относятся локальные обследования загрязнения воздуха, грунтов и грунтовых вод.': [false, false]}, false, false]}, {'Что передается исполнителю проектных работ, если подготовка проектной документации осуществляется индивидуальным предпринимателем или юридическим лицом на основании договора подряда?': [{'Задание на проектирование.': [false, false], 'Градостроительный план земельного участка. ': [true, false], 'В случае подготовки проектной документации линейного объекта – проект планировки территории и проект межевания территории.': [true, false], 'Технические условия. ': [true, false], 'Результаты инженерных изысканий.': [true, false], 'Выписка из реестра членов саморегулируемой организации.': [false, false]}, false, false]}, {'Кем утверждается проектная документация?': [{'Проектная документация утверждается застройщиком.': [true, false], 'Проектная документация утверждается техническим заказчиком.': [true, false], 'Проектная документация утверждается лицом, ответственным за эксплуатацию здания, сооружения.': [true, false], 'Проектная документация утверждается региональным оператором.': [true, false], 'Проектная документация утверждается руководителем органа местного самоуправления.': [false, false], 'Проектная документация утверждается собственником земельного участка.': [false, false]}, false, false]}, {'Проектная документация каких видов объектов должна содержать перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера, мероприятий по противодействию терроризму?': [{'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера должны содержаться в проектной документации для объектов использования атомной энергии.': [true, false], 'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера должны содержаться в проектной документации опасных производственных объектов, определяемых в соответствии с законодательством Российской Федерации.': [true, false], 'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера должны содержаться в проектной документации особо опасных, технически сложных, уникальных объектов.': [true, false], 'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера должны содержаться в проектной документации для объектов общественного назначения, зрелищных, спортивных, торговых комплексов и сооружений.': [false, false], 'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера должны содержаться в проектной документации для зданий органов власти Российской Федерации, органов власти субъектов Российской Федерации и местного самоуправления. ': [false, false], 'Перечень мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера должны содержаться в проектной документации для объектов обороны и безопасности.': [true, false]}, false, false]}, {'В отношении каких объектов капитального строительства экспертиза проектной документации и результатов инженерных изысканий не проводится?': [{'Экспертиза не проводится для отдельно стоящих жилых домов с количеством этажей не более чем три, предназначенные для проживания одной семьи (объекты индивидуального жилищного строительства), размещенные на землях особо охраняемых природных территорий.': [false, false], 'Экспертиза не проводится для отдельно стоящих жилых домов с количеством этажей не более чем три, предназначенные для проживания одной семьи (объекты индивидуального жилищного строительства).': [true, false], 'Экспертиза не проводится для жилых домов с количеством этажей не более чем три, состоящие из нескольких блоков, количество которых не превышает десять и каждый из которых предназначен для проживания одной семьи, имеет общую стену (общие стены) без проемов с соседним блоком или соседними блоками, расположен на отдельном земельном участке и имеет выход на территорию общего пользования (жилые дома блокированной застройки), в случае, если строительство или реконструкция таких жилых домов осуществляется с привлечением средств бюджетов бюджетной системы Российской Федерации.': [false, false], 'Экспертиза не проводится для многоквартирных домов с количеством этажей не более чем три, состоящие из одной или нескольких блок-секций, количество которых не превышает четыре, в каждой из которых находятся несколько квартир и помещения общего пользования и каждая из которых имеет отдельный подъезд с выходом на территорию общего пользования, в случае, если строительство или реконструкция таких многоквартирных домов осуществляется без привлечения средств бюджетов бюджетной системы Российской Федерации.': [false, false], 'Экспертиза не проводится для отдельно стоящих объектов капитального строительства с количеством этажей не более чем два, общая площадь которых составляет не более чем 1500 квадратных метров и которые не предназначены для проживания граждан и осуществления производственной деятельности, за исключением объектов, которые в соответствии со статьей 48.1 настоящего Кодекса являются особо опасными, технически сложными или уникальными объектами.': [true, false], 'Экспертиза не проводится для отдельно стоящих объектов капитального строительства с количеством этажей не более чем два, общая площадь которых составляет не более чем 1500 квадратных метров, которые предназначены для осуществления производственной деятельности и для которых не требуется установление санитарно-защитных зон или для которых в пределах границ земельных участков, на которых расположены такие объекты, установлены санитарно-защитные зоны или требуется установление таких зон, за исключением объектов, которые в соответствии со статьей 48.1 настоящего Кодекса являются особо опасными, технически сложными или уникальными объектами.': [true, false]}, false, false]}, {'Каким нормативным правовым актом осуществляется регулирование в области инженерных изысканий, в том числе в области инженерно-геологических изысканий?': [{'Градостроительный кодекс Российской Федерации" от 29.12.2004 N 190-ФЗ.': [true, false], 'Гражданский кодекс Российской Федерации (часть первая)" от 30.11.1994 N 51-ФЗ.': [true, false], 'Постановление Правительства Российской Федерации от 05.03.2007 N 145 "О порядке Организации и проведения государственной экспертизы проектной документации и результатов инженерных изысканий". ': [true, false], 'Постановление Правительства Российской Федерации от 31.03.2012 N 272 "Об утверждении Положения об организации и проведении негосударственной экспертизы проектной документации и (или) результатов инженерных изысканий".': [true, false], 'Приказ Минстроя России от 08.06.2018 N 341/пр "Об утверждении требований, к составу, содержанию и порядку оформления заключения государственной экспертизы проектной документации и результатов инженерных изысканий".': [true, false], 'Приказ Минстроя России от 29.06.2017 N 941/пр "Об утверждении перечня Направлений деятельности экспертов, по которым претенденты имеют намерение получить право подготовки заключений экспертизы документации и (или) результатов инженерных изысканий". ': [false, false]}, false, false]}, {'Какими нормативными правовыми актами следует руководствоваться в части организационных процедур при проведении негосударственной экспертизы проектной документации и (или) инженерных изысканий?': [{'Градостроительным кодексом Российской Федерации.': [true, false], 'Постановлением Правительства Российской Федерации от 05.03.2007 N 145 "О порядке Организации и проведения государственной экспертизы проектной документации и результатов инженерных изысканий".': [true, false], 'Постановлением Правительства Российской Федерации от 31.03.2012 N 272 "Об утверждении Положения об организации и проведении негосударственной экспертизы проектной документации и (или) результатов инженерных изысканий".': [true, false], 'Приказом Минстроя России от 08.06.2018 N 341/пр "Об утверждении Требований к составу, содержанию и порядку оформления заключения государственной экспертизы проектной документации и (или) результатов инженерных изысканий".': [true, false], 'Постановлением Правительства Российской Федерации от 31.03.2012 N 271 "О порядке аттестации на право подготовки заключений экспертизы проектной документации и (или) результатов инженерных изысканий".': [false, false], 'Постановлением Правительства Российской Федерации от 16.04.2012 N 327 "О порядке ведения государственного реестра юридических лиц, аккредитованных на право проведения негосударственной экспертизы проектной документации и результатов инженерных изысканий".': [false, false]}, false, false]}, {'При каких условиях юридическое лицо может быть аккредитовано на право проведения негосударственной экспертизы проектной документации и (или) негосударственной экспертизы результатов инженерных изысканий?': [{'Наличие по месту основной работы соответственно не менее чем пять работников, имеющих аттестацию на право подготовки заключений экспертизы проектной документации, или не менее чем четыре работника, имеющих аттестацию на право подготовки заключений экспертизы результатов инженерных изысканий в соответствии с приказом Минстроя России от 29.06.2017№ 941 "Об утверждения перечня направлений деятельности экспертов, по которым претенденты имеют намерение получить право подготовки заключений экспертизы проектной документации и (или) результатов инженерных изысканий".': [false, false], 'Наличие по месту основной работы соответственно не менее чем пять работников, имеющих аттестацию на право подготовки заключений экспертизы проектной документации, или не менее чем пять работников, имеющих аттестацию на право подготовки заключений экспертизы результатов инженерных изысканий.': [true, false], 'Наличие у юридического лица сайта в сети "Интернет".': [true, false], 'Наличие регламента проведения негосударственной экспертизы проектной документации, утвержденного приказом руководителя юридического лица и размещенного на сайте такого юридического лица в сети "Интернет". ': [true, false], 'Наличие справки об отсутствии судимости у работников, имеющих аттестацию на право подготовки заключений экспертизы проектной документации или заключений экспертизы результатов инженерных изысканий. ': [false, false], 'Наличие гражданства Российской Федерации у руководителя юридического лица.': [false, false]}, false, false]}, {' Что должны обеспечивать минимальные требования технического регламента с учетом степени риска причинения вреда? ': [{'Обеспечивать безопасность излучений.': [true, false], 'Обеспечивать взрывобезопасность, механическую и пожарную безопасность.': [true, false], 'Обеспечивать безопасность продукции (технических устройств, применяемых на опасном производственном объекте).': [true, false], 'Обеспечивать термическую безопасность, химическую безопасность, электрическую безопасность.': [true, false], 'Обеспечивать радиационную безопасность населения.': [true, false], 'Обеспечивать единство и точность измерений. ': [false, false]}, false, false]}, {'Какие направления деятельности входят в состав понятия "градостроительная деятельность"?': [{'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде благоустройства территорий. ': [true, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде градостроительного зонирования, планировки территории. ': [true, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде инженерных изысканий.': [false, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде архитектурно-строительного проектирования. ': [true, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде экспертизы проектной документации и результатов инженерных изысканий.': [false, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде эксплуатации зданий, сооружений. ': [true, false]}, false, false]}, {'Какие виды зон устанавливаются путем осуществления градостроительной деятельности?': [{'Путем осуществления градостроительной деятельности устанавливаются функциональные зоны.': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются санитарно-защитные зоны. ': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются территориальные зоны.': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются защитные зоны объектов культурного наследия. ': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются водоохранные зоны. ': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются зоны санитарной охраны источников питьевого и хозяйственно-бытового водоснабжения. ': [true, false]}, false, false]}, {'Какие зоны отнесены по статусу к зонам с особыми условиями использования территорий? ': [{'К зонам с особыми условиями использования территорий отнесены зоны затопления. ': [true, false], 'К зонам с особыми условиями использования территорий отнесены зоны развития опасных геологических процессов. ': [false, false], 'К зонам с особыми условиями использования территорий отнесены зоны охраны объектов культурного наследия.': [true, false], 'К зонам с особыми условиями использования территорий отнесены зоны экологического бедствия. ': [false, false], 'К зонам с особыми условиями использования территорий отнесена приаэродромная территория. ': [true, false], 'К зонам с особыми условиями использования территорий отнесены зоны подтопления. ': [true, false]}, false, false]}, {'Что отнесено к виду элементов планировочной структуры? ': [{'К виду элементов планировочной структуры отнесен квартал.': [true, false], 'К виду элементов планировочной структуры отнесен город.': [false, false], 'К виду элементов планировочной структуры отнесен район.': [true, false], 'К виду элементов планировочной структуры отнесена улично-дорожная сеть.': [true, false], 'К виду элементов планировочной структуры отнесена территория транспортно-пересадочного узла.': [true, false], 'К виду элементов планировочной структуры отнесена территория, занятая линейным объектом и (или) предназначенная для размещения линейного объекта, за исключением улично-дорожной сети.': [true, false]}, false, false]}, {'Что отнесено к основным принципам законодательства о градостроительной деятельности? ': [{'К основным принципам законодательства о градостроительной деятельности отнесено обеспечение инвалидам условий для беспрепятственного доступа к объектам социального и иного назначения. ': [true, false], 'К основным принципам законодательства о градостроительной деятельности отнесено обеспечение комплексного и устойчивого развития территории на основе территориального планирования, градостроительного зонирования и планировки территории. ': [true, false], 'К основным принципам законодательства о градостроительной деятельности отнесено обеспечение комфортной городской среды. ': [false, false], 'К основным принципам законодательства о градостроительной деятельности отнесено осуществление строительства на основе документов территориального планирования, правил землепользования и застройки и документации по планировке территории. ': [true, false], 'К основным принципам законодательства о градостроительной деятельности отнесено обеспечение единства требований к порядку осуществления взаимодействия субъектов градостроительных отношений, указанных в ст. 5 Градостроительного кодекса Российской Федерации. ': [true, false], 'К основным принципам законодательства о градостроительной деятельности отнесено обеспечение комплексного и устойчивого развития территории на основе территориального планирования и градостроительного зонирования. ': [false, false]}, false, false]}, {'В каких случаях сметная стоимость строительства определяется с обязательным применением сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, и сметных цен строительных ресурсов? ': [{'Сметная стоимость строительства определяется с обязательным применением сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, и сметных цен строительных ресурсов в случае строительства, финансируемого с привлечением средств бюджетов бюджетной системы Российской Федерации. ': [true, false], 'Сметная стоимость строительства определяется с обязательным применением сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, и сметных цен строительных ресурсов в случае строительства, финансируемого с привлечением кредитных средств. ': [true, false], 'Сметная стоимость строительства определяется с обязательным применением сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, и сметных цен строительных ресурсов в случае капитального ремонта многоквартирного дома (общего имущества в многоквартирном доме), осуществляемого полностью или частично за счет средств регионального оператора, товарищества собственников жилья, жилищного, жилищно-строительного кооператива или иного специализированного потребительского кооператива либо средств собственников помещений в многоквартирном доме. ': [true, false], 'Сметная стоимость строительства определяется с обязательным применением сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, и сметных цен строительных ресурсов в случае строительства, финансируемого с привлечением средств юридических лиц, доля в уставных (складочных) капиталах которых Российской Федерации, субъектов Российской Федерации, муниципальных образований составляет более 50 процентов. ': [true, false], 'Сметная стоимость строительства определяется с обязательным применением сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, и сметных цен строительных ресурсов в случае средств юридических лиц, созданных Российской Федерацией, субъектами Российской Федерации, муниципальными образованиями, юридических лиц, доля в уставных (складочных) капиталах которых Российской Федерации, субъектов Российской Федерации, муниципальных образований составляет не более 50 процентов. ': [false, false], 'Сметная стоимость строительства определяется с применением сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, и сметных цен строительных ресурсов, если это предусмотрено федеральным законом или договором. ': [true, false]}, false, false]}, {'Что отображается на картах, которые содержат генеральный план городского округа? ': [{'На картах, которые содержит генеральный план городского округа отображаются границы населенных пунктов (в том числе границы образуемых населенных пунктов), входящих в состав городского округа. ': [true, false], 'На картах генерального плана городского округа отображаются границы и описание функциональных зон с указанием планируемых для размещения в них объектов федерального значения, объектов регионального значения, объектов местного значения (за исключением линейных объектов) и местоположения линейных объектов федерального значения, линейных объектов регионального значения, линейных объектов местного значения. ': [true, false], 'На картах, которые содержит генеральный план городского округа отображаются зоны с особыми условиями использования территорий. ': [false, false], 'На картах, которые содержит генеральный план городского округа отображаются планируемые для размещения автомобильные дороги местного значения.': [true, false], 'На картах, которые содержит генеральный план городского округа отображаются объекты местного значения городского округа в области электро-, тепло-, газо- и водоснабжения населения, водоотведения. ': [true, false], 'На картах, которые содержит генеральный план городского округа отображаются планируемые для размещения объекты физической культуры и массового спорта, образования, здравоохранения, обработки, утилизации, обезвреживания, размещения твердых коммунальных отходов. ': [true, false]}, false, false]}, {'Что относится к видам разрешенного использования земельных участков и объектов капитального строительства?': [{'К установленным законодательством видам разрешенного использования земельных участков и объектов капитального строительства относятся дополнительные виды разрешенного использования. ': [false, false], 'К установленным законодательством видам разрешенного использования земельных участков и объектов капитального строительства относятся основные виды разрешенного использования.': [true, false], 'К установленным законодательством видам разрешенного использования земельных участков и объектов капитального строительства относятся вспомогательные виды разрешенного использования. ': [true, false], 'К установленным законодательством видам разрешенного использования земельных участков и объектов капитального строительства относятся ограниченные виды разрешенного использования.': [false, false], 'К установленным законодательством видам разрешенного использования земельных участков и объектов капитального строительства относятся эффективные виды разрешенного использования.': [false, false], 'К установленным законодательством видам разрешенного использования земельных участков и объектов капитального строительства относятся условно разрешенные виды использования. ': [true, false]}, false, false]}, {'Что является условиями договора о комплексном развитии территории по инициативе правообладателей? ': [{'Условиями договора о комплексном развитии территории по инициативе правообладателей являются обязательства сторон договора обеспечить осуществление мероприятий по освоению территории, включая ввод в эксплуатацию объектов капитального строительства, в соответствии с графиками осуществления каждого мероприятия в предусмотренные указанными графиками сроки.': [true, false], 'Условиями договора о комплексном развитии территории по инициативе правообладателей являются разграничение обязательств сторон по осуществлению мероприятий, необходимых для осуществления деятельности по комплексному и устойчивому развитию территории, обязательств по изменению видов разрешенного использования земельных участков, образованию земельных участков, установлению сервитутов, обеспечению осуществления государственной регистрации прав на земельные участки и (или) расположенные на них объекты недвижимого имущества. ': [true, false], 'Условием договора о комплексном развитии территории по инициативе правообладателей является обязательство органа местного самоуправления внести изменения и дополнения в документы территориального планирования.': [false, false], 'Условиями договора о комплексном развитии территории по инициативе правообладателей являются обязательства уполномоченного органа местного самоуправления обеспечить строительство объектов коммунальной, транспортной, социальной инфраструктур, необходимых для функционирования объектов капитального строительства жилого, производственного, общественно-делового и иного назначения и обеспечения жизнедеятельности граждан, или обязательства правообладателей обеспечить размещение таких объектов за счет собственных средств в соответствии с опережающими графиками проектирования, строительства, реконструкции указанных объектов коммунальной, транспортной, социальной инфраструктур. ': [true, false], 'Условием договора о комплексном развитии территории по инициативе правообладателей является цена права на заключение договора.': [false, false], 'Условиями договора о комплексном развитии территории по инициативе правообладателей являются виды льгот (при наличии), предоставляемых правообладателям в соответствии с Градостроительным кодексом Российской Федерации, нормативными правовыми актами субъектов Российской Федерации, муниципальными правовыми актами, а также порядок и условия их предоставления, прекращения. ': [true, false]}, false, false]}, {'Какие решения определяет проектная документация для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта? ': [{'Проектная документация определяет социальные решения для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта.': [false, false], 'Проектная документация определяет экономические решения для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта.': [false, false], 'Проектная документация определяет функционально-технологические решения для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта.': [true, false], 'Проектная документация определяет архитектурные решения для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта.': [true, false], 'Проектная документация определяет конструктивные решения для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта.': [true, false], 'Проектная документация определяет инженерно-технические решения для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта.': [true, false]}, false, false]}, {'В соответствии с чем осуществляется подготовка проектной документации? ': [{'Подготовка проектной документации осуществляется в соответствии с разрешением на отклонение от предельных параметров разрешенного строительства, реконструкции объектов капитального строительства.': [true, false], 'Подготовка проектной документации осуществляется в соответствии с требованиями технических регламентов.': [true, false], 'Подготовка проектной документации осуществляется в соответствии с техническими условиями.': [true, false], 'Подготовка проектной документации осуществляется в соответствии с правоустанавливающими документами на земельный участок.': [false, false], 'Подготовка проектной документации осуществляется в соответствии с заданием технического заказчика (при подготовке проектной документации на основании договора подряда на подготовку проектной документации).': [true, false], 'Подготовка проектной документации осуществляется в соответствии с результатами инженерных изысканий.': [true, false]}, false, false]}, {'Сколько разделов включено в состав проектной документации линейных объектов? ': [{'В состав проектной документации линейных объектов, включено десять разделов. ': [true, false], 'В состав проектной документации линейных объектов, включено 11 разделов.': [false, false], 'В состав проектной документации линейных объектов, включено 10 разделов.': [true, false], 'В состав проектной документации линейных объектов, включено 13 разделов.': [false, false], 'В состав проектной документации линейных объектов, включено одиннадцать разделов.': [false, false], 'В состав проектной документации линейных объектов, включено тринадцать разделов.': [false, false]}, false, false]}, {'На какие виды подразделяются объекты капитального строительства в зависимости от функционального назначения и характерных признаков? ': [{'В числе объектов капитального строительства в зависимости от функционального назначения и характерных признаков выделяются линейные объекты.': [true, false], 'В числе объектов капитального строительства в зависимости от функционального назначения и характерных признаков выделяются объекты площадного характера.': [false, false], 'В числе объектов капитального строительства в зависимости от функционального назначения и характерных признаков выделяются объекты непроизводственного назначения.': [true, false], 'В числе объектов капитального строительства в зависимости от функционального назначения и характерных признаков выделяются объекты рекреационного назначения.': [false, false], 'В числе объектов капитального строительства в зависимости от функционального назначения и характерных признаков выделяются объекты производственного назначения.': [true, false], 'В числе объектов капитального строительства в зависимости от функционального назначения и характерных признаков выделяются объекты транспортного назначения.': [false, false]}, false, false]}, {'Что должен содержать в графической части раздел проектной документации "Конструктивные и объемно-планировочные решения"? ': [{'Раздел проектной документации "Конструктивные и объемно-планировочные решения" должен содержать в графической части план с отражением сведений о топографических, инженерно-геологических, гидрогеологических, метеорологических и климатических условиях земельного участка, предоставленного для размещения объекта капитального строительства. ': [false, false], 'Раздел проектной документации "Конструктивные и объемно-планировочные решения" должен содержать в графической части чертежи фрагментов планов и разрезов, требующих детального изображения. ': [true, false], 'Раздел проектной документации "Конструктивные и объемно-планировочные решения" должен содержать в графической части чертежи характерных разрезов зданий и сооружений с изображением несущих и ограждающих конструкций, указанием относительных высотных отметок уровней конструкций, полов, низа балок, ферм, покрытий с описанием конструкций кровель и других элементов конструкций. ': [true, false], 'Раздел проектной документации "Конструктивные и объемно-планировочные решения" должен содержать в графической части планы перекрытий, покрытий, кровли. ': [true, false], 'Раздел проектной документации "Конструктивные и объемно-планировочные решения" должен содержать в графической части план и сечения фундаментов. ': [true, false], 'Раздел проектной документации "Конструктивные и объемно-планировочные решения" должен содержать в графической части план отделки основных помещений. ': [false, false]}, false, false]}, {'Какие подразделы должен содержать раздел проектной документации "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений"? ': [{'Раздел "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений" должен содержать подраздел "Система энергоснабжения". ': [false, false], 'Раздел "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений" должен содержать подраздел "Отопление, вентиляция и кондиционирование воздуха, тепловые сети". ': [true, false], 'Раздел "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений" должен содержать подраздел "Система мусороудаления". ': [false, false], 'Раздел "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений" должен содержать подраздел "Система газоснабжения". ': [true, false], 'Раздел "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений" должен содержать подраздел "Сети связи". ': [true, false], 'Раздел "Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений" должен содержать подразделы "Система водоснабжения" и "Cистема водоотведения". ': [true, false]}, false, false]}, {'Что должен содержать в графической части раздел проектной документации "Перечень мероприятий по охране окружающей среды"? ': [{'Раздел проектной документации "Перечень мероприятий по охране окружающей среды" должен содержать в графической части ситуационный план (карту-схему) района строительства с указанием на нем границ земельного участка, предоставленного для размещения объекта капитального строительства, границ санитарно-защитной зоны, селитебной территории, рекреационных зон, водоохранных зон, зон охраны источников питьевого водоснабжения, мест обитания животных и растений, занесенных в Красную книгу Российской Федерации и красные книги субъектов Российской Федерации, а также мест нахождения расчетных точек. ': [true, false], 'Раздел проектной документации "Перечень мероприятий по охране окружающей среды" должен содержать в графической части ситуационный план (карту-схему) района строительства с указанием границ зон развития опасных геологических процессов.': [false, false], 'Раздел проектной документации "Перечень мероприятий по охране окружающей среды" должен содержать в графической части ситуационный план (карту-схему) района строительства с указанием границ земельного участка, предоставленного для размещения объекта капитального строительства, расположения источников выбросов в атмосферу загрязняющих веществ и устройств по очистке этих выбросов. ': [true, false], 'Раздел проектной документации "Перечень мероприятий по охране окружающей среды" должен содержать в графической части карты-схемы и сводные таблицы с результатами расчетов загрязнения атмосферы при неблагоприятных погодных условиях и выбросов по веществам и комбинациям веществ с суммирующимися вредными воздействиями - для объектов производственного назначения. ': [true, false], 'Раздел проектной документации "Перечень мероприятий по охране окружающей среды" должен содержать в графической части ситуационный план (карту-схему) района с указанием границ земельного участка, предоставленного для размещения объекта капитального строительства, с указанием контрольных пунктов, постов, скважин и иных объектов, обеспечивающих отбор проб воды из поверхностных водных объектов, а также подземных вод, - для объектов производственного назначения. ': [true, false], 'Раздел проектной документации "Перечень мероприятий по охране окружающей среды" должен содержать в графической части ситуационный план (карту-схему) района с указанием точек мониторинговых наблюдений.': [false, false]}, false, false]}, {'Что должен содержать в графической части раздел проектной документации "Мероприятия по обеспечению пожарной безопасности"? ': [{'Ситуационный план (карту-схему) района с указанием границ земельного участка, предоставленного для размещения объекта капитального строительства, с указанием скважин и иных объектов, обеспечивающих отбор воды из поверхностных водных объектов, а также подземных вод.': [false, false], 'Раздел проектной документации "Мероприятия по обеспечению пожарной безопасности" должен содержать в графической части план эвакуации маломобильных групп населения. ': [false, false], 'Ситуационный план организации земельного участка, предоставленного для размещения объекта капитального строительства, с указанием въезда (выезда) на территорию и путей подъезда к объектам пожарной техники, мест размещения и емкости пожарных резервуаров (при их наличии), схем прокладки наружного противопожарного водопровода, мест размещения пожарных гидрантов и мест размещения насосных станций.': [true, false], 'Раздел проектной документации "Мероприятия по обеспечению пожарной безопасности" должен содержать в графической части ситуационный план с указанием мест размещения пожарных депо.': [false, false], 'Схемы эвакуации людей и материальных средств из зданий (сооружений) и с прилегающей к зданиям (сооружениям) территории в случае возникновения пожара.': [true, false], 'Структурные схемы технических систем (средств) противопожарной защиты (автоматических установок пожаротушения, автоматической пожарной сигнализации, внутреннего противопожарного водопровода).': [true, false]}, false, false]}, {'Что должен содержать раздел проектной документации "Мероприятия по обеспечению 