требований оснащенности зданий, строений и сооружений приборами учета используемых энергетических ресурсов" должен содержать методику оценки энергетической эффективности.': [false, false], 'Раздел проектной документации "Мероприятия по обеспечению соблюдения требований энергетической эффективности и требований оснащенности зданий, строений и сооружений приборами учета используемых энергетических ресурсов" должен содержать перечень мероприятий по использованию возобновляемых энергетических ресурсов.': [false, false]}, false, false]}, {'Что должен содержать раздел проектной документации "Смета на строительство объектов капитального строительства" (текстовая часть в составе пояснительной записки к сметной документации)? ': [{'Раздел проектной документации "Смета на строительство объектов капитального строительства" (текстовая часть в составе пояснительной записки к сметной документации) должен содержать перечень утвержденных сметных нормативов, сведения о которых включены в федеральный реестр сметных нормативов, принятых для составления сметной документации на строительство. ': [true, false], 'Раздел проектной документации "Смета на строительство объектов капитального строительства" (текстовая часть в составе пояснительной записки к сметной документации) должен содержать обоснование предполагаемой (предельной) стоимости строительства на основе документально подтвержденных сведений о проектах-аналогах (при наличии таких проектов) при отсутствии укрупненных нормативов цены строительства для объектов, аналогичных по назначению, проектной мощности, природным и иным условиям территории, на которой планируется осуществлять строительство. ': [true, false], 'Раздел проектной документации "Смета на строительство объектов капитального строительства" (текстовая часть в составе пояснительной записки к сметной документации) должен содержать наименование проектной организации (при наличии). ': [false, false], 'Раздел проектной документации "Смета на строительство объектов капитального строительства" (текстовая часть в составе пояснительной записки к сметной документации) должен содержать наименование подрядной организации (при наличии). ': [true, false], 'Раздел проектной документации "Смета на строительство объектов капитального строительства" (текстовая часть в составе пояснительной записки к сметной документации) должен содержать обоснование особенностей определения сметной стоимости строительных работ для объекта капитального строительства.': [true, false], 'Раздел проектной документации "Смета на строительство объектов капитального строительства" (текстовая часть в составе пояснительной записки к сметной документации) должен содержать обоснование особенностей определения сметной стоимости строительных работ, осуществляемых в зимний период времени.': [false, false]}, false, false]}, {'Что должен содержать раздел проектной документации «иная документация в случаях, предусмотренных федеральными законами Российской Федерации»?': [{'Раздел проектной документации "Иная документация в случаях, предусмотренных федеральными законами" должен содержать декларацию безопасности гидротехнических сооружений, разрабатываемую на стадии проектирования. ': [true, false], 'Раздел проектной документации "Иная документация в случаях, предусмотренных федеральными законами" должен содержать проект производства работ.': [false, false], 'Раздел проектной документации "Иная документация в случаях, предусмотренных федеральными законами" должен содержать иную документацию, установленную законодательными актами Российской Федерации. ': [true, false], 'Раздел проектной документации "Иная документация в случаях, предусмотренных федеральными законами" должен содержать перечень мероприятий по гражданской обороне, мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера для объектов использования атомной энергии (в том числе ядерных установок, пунктов хранения ядерных материалов и радиоактивных веществ), опасных производственных объектов, определяемых таковыми в соответствии с законодательством Российской Федерации, особо опасных, технически сложных, уникальных объектов, объектов обороны и безопасности. ': [true, false], 'Раздел проектной документации "Иная документация в случаях, предусмотренных федеральными законами" должен содержать декларацию промышленной безопасности опасных производственных объектов, разрабатываемую на стадии проектирования. ': [true, false], 'Раздел проектной документации "Иная документация в случаях, предусмотренных федеральными законами" должен содержать методику оценки эффективности капитальных вложений': [false, false]}, false, false]}, {'Какие объекты отнесены к особо опасным и технически сложным объектам? ': [{'К особо опасным и технически сложным объектам относятся магистральные линии электропередачи напряжением 220 киловольт.': [false, false], 'К особо опасным и технически сложным объектам относятся объекты инфраструктуры железнодорожного транспорта общего пользования. ': [true, false], 'К особо опасным и технически сложным объектам относятся объекты использования атомной энергии. ': [true, false], 'К особо опасным и технически сложным объектам относятся объекты инфраструктуры внеуличного транспорта. ': [true, false], 'К особо опасным и технически сложным объектам относятся объекты инфраструктуры морского порта, предназначенные для стоянок и обслуживания маломерных, спортивных парусных и прогулочных судов.': [false, false], 'К особо опасным и технически сложным объектам относятся объекты инфраструктуры воздушного транспорта. ': [true, false]}, false, false]}, {'Какие объекты отнесены к особо опасным и технически сложным объектам? ': [{'Подвесные канатные дороги.': [true, false], 'Сооружения связи, являющиеся особо опасными, технически сложными в соответствии с законодательством Российской Федерации в области связи.': [true, false], 'Линии электропередачи и иные объекты электросетевого хозяйства напряжением 330 киловольт и более.': [true, false], 'Гидротехнические сооружения первого и второго классов.': [true, false], 'Тепловые электростанции мощностью от 100 мегаватт и выше.': [false, false], 'Объекты космической инфраструктуры.': [true, false]}, false, false]}, {'Что является предметом экспертизы проектной документации и результатов инженерных изысканий? ': [{'Предметом экспертизы проектной документации являются оценка соответствия проектной документации результатам инженерных изысканий. ': [true, false], 'Предметом экспертизы проектной документации являются оценка соответствия проектной документации требованиям градостроительного плана земельного участка.': [false, false], 'Предметом экспертизы результатов инженерных изысканий являются оценка соответствия требованиям технических регламентов. ': [true, false], 'Предметом экспертизы проектной документации являются оценка соответствия проектной документации требованиям технических регламентов, в том числе санитарно-эпидемиологическим, экологическим требованиям, требованиям государственной охраны объектов культурного наследия, требованиям пожарной, промышленной, ядерной, радиационной и иной безопасности. ': [false, false], 'Предметом экспертизы проектной документации являются оценка соответствия проектной документации требованиям нормативных технических документов, а также правилам землепользования и застройки. ': [false, false], 'Предметом экспертизы результатов инженерных изысканий являются оценка соответствия требованиям нормативных технических документов. ': [false, false]}, false, false]}, {'Что является основанием для отказа в принятии проектной документации и (или) результатов инженерных изысканий, направленных на экспертизу? ': [{'Направление не подлежащих государственной экспертизе проектной документации и (или) результатов инженерных изысканий. ': [false, false], 'Направление на экспертизу не всех документов, предусмотренных Правительством Российской Федерации в соответствии с частью 11 статьи 49 Градостроительного кодекса Российской Федерации.': [true, false], 'Отсутствие в составе проектной документации разделов, которые подлежат включению в состав такой документации в соответствии с требованиями, установленными Правительством Российской Федерации. ': [true, false], 'Несоответствие результатов инженерных изысканий составу и форме, установленным в соответствии с частью 6 статьи 47 Градостроительного кодекса Российской Федерации. ': [true, false], 'Отсутствие результатов инженерных изысканий, указанных в части 6 статьи 47 Градостроительного кодекса Российской Федерации, или отсутствие положительного заключения экспертизы результатов инженерных изысканий (в случае, если результаты инженерных изысканий были направлены на экспертизу до направления на экспертизу проектной документации). ': [true, false], 'Выполнение инженерных изысканий, результаты которых направлены на экспертизу, лицом, которое не соответствует требованиям, указанным в частях 2 и 3 статьи 47 Градостроительного кодекса Российской Федерации. ': [true, false]}, false, false]}, {'При условии соблюдения каким требованиям физическое лицо может быть аттестовано на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий?': [{'Физическое лицо может быть аттестовано на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий при условии наличия высшего образования соответствующего профиля.': [true, false], 'Физическое лицо может быть аттестовано на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий при условии отсутствия непогашенной или неснятой судимости за совершение умышленного преступления.': [true, false], 'Физическое лицо может быть аттестовано на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий при условии наличия стажа работы в сфере подготовки проектной документации и (или) выполнения инженерных изысканий по соответствующему направлению деятельности не менее чем пять лет или стажа работы на соответствующих должностях в органах либо организациях, проводящих экспертизу проектной документации и (или) экспертизу результатов инженерных изысканий, не менее чем три года.': [true, false], 'Физическое лицо может быть аттестовано на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий при условии наличия научной степени или научного звания. ': [false, false], 'Физическое лицо может быть аттестовано на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий при условии постоянного проживания в Российской Федерации.': [true, false], 'Физическое лицо может быть аттестовано на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий при условии обладания необходимыми знаниями в области законодательства Российской Федерации о градостроительной деятельности, законодательства Российской Федерации о техническом регулировании (в том числе требований к обеспечению безопасной эксплуатации объектов капитального строительства) в части, касающейся соответственно выполнения инженерных изысканий в целях проектирования, строительства и эксплуатации этих объектов, проектирования, строительства и эксплуатации этих объектов.': [true, false]}, false, false]}, {'На каком основании квалификационный аттестат эксперта может быть аннулирован?': [{'Основанием для аннулирования квалификационного аттестата на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий является вступление в законную силу решения уполномоченных органов о привлечении лица, которому выдан квалификационный аттестат, к ответственности за правонарушения в сфере его профессиональной деятельности. ': [true, false], 'Основанием для аннулирования квалификационного аттестата на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий является установление факта участия эксперта в экспертизе при наличии личной заинтересованности в ее результатах, в том числе если в подготовке проектной документации и (или) выполнении инженерных изысканий участвовали двоюродные братья или сестры эксперта.': [false, false], 'Основанием для аннулирования квалификационного аттестата на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий является установление факта участия эксперта в экспертизе при наличии личной заинтересованности в ее результатах, в том числе если в подготовке проектной документации и (или) выполнении инженерных изысканий участвовали эксперт лично или его близкие родственники (родители, дети, усыновители, усыновленные, родные братья и родные сестры, дедушка, бабушка, внуки), супруг. ': [true, false], 'Основанием для аннулирования квалификационного аттестата на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий является выезд за границу на постоянное место жительства.': [false, false], 'Основанием для аннулирования квалификационного аттестата на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий является вступление в законную силу решения уполномоченных органов о привлечении лица, которому выдан квалификационный аттестат, к ответственности за правонарушения, не касающиеся сферы его профессиональной деятельности.': [false, false], 'Основанием для аннулирования квалификационного аттестата на право подготовки заключений экспертизы проектной документации и (или) экспертизы результатов инженерных изысканий является установление факта представления для прохождения аттестации документов, содержащих недостоверные сведения. ': [true, false]}, false, false]}, {'Соответствие проектной документации каким требованиям подтверждает разрешение на строительство?': [{'Разрешение на строительство подтверждает соответствие проектной документации требованиям, установленным градостроительным регламентом (за исключением случая, если на земельный участок не распространяется действие градостроительного регламента или для земельного участка не устанавливается градостроительный регламент), при осуществлении строительства, реконструкции объекта капитального строительства, не являющегося линейным объектом. ': [true, false], 'Разрешение на строительство подтверждает соответствие проектной документации требованиям, установленным проектом планировки территории и проектом межевания территории (за исключением случаев, если в соответствии с Градостроительным кодексом Российской Федерации подготовка проекта планировки территории и проекта межевания территории не требуется), при осуществлении строительства, реконструкции объекта капитального строительства, не являющегося линейным объектом. ': [true, false], 'Разрешение на строительство подтверждает соответствие проектной документации требованиям, установленным градостроительным планом земельного участка при осуществлении строительства, реконструкции объекта капитального строительства, не являющегося линейным объектом.': [false, false], 'Разрешение на строительство подтверждает допустимость размещения объекта капитального строительства на земельном участке в соответствии с разрешенным использованием такого земельного участка и ограничениями, установленными в соответствии с земельным и иным законодательством Российской Федерации. ': [true, false], 'Разрешение на строительство подтверждает соответствие проектной документации требованиям, установленным требованиям, установленным проектом планировки территории и проектом межевания территории, при осуществлении строительства, реконструкции линейного объекта. ': [true, false], 'Разрешение на строительство подтверждает соответствие проектной документации установленным в соответствии с частью 7 статьи 36 Градостроительного кодекса Российской Федерации требованиям к назначению, параметрам и размещению объекта капитального строительства на указанном земельном участке в случае, если на земельный участок не распространяется действие градостроительного регламента или для земельного участка не устанавливается градостроительный регламент. ': [true, false]}, false, false]}, {'Что включено в состав основных целей саморегулируемых организаций в области инженерных изысканий, архитектурно-строительного проектирования, строительства, реконструкции, капитального ремонта объектов капитального строительства?': [{'Предупреждение причинения вреда жизни или здоровью физических лиц, имуществу физических или юридических лиц вследствие недостатков работ, которые оказывают влияние на безопасность объектов капитального строительства и выполняются членами саморегулируемых организаций. ': [true, false], 'Повышение качества работ в области негосударственной экспертизы.': [false, false], 'Повышение качества выполнения инженерных изысканий, осуществления архитектурно-строительного проектирования, строительства, реконструкции объектов капитального строительства. ': [true, false], 'Обеспечение исполнения членами саморегулируемых организаций обязательств по договорам подряда на выполнение инженерных изысканий, на подготовку проектной документации, договорам строительного подряда, заключенным с использованием конкурентных способов определения поставщиков (подрядчиков, исполнителей) в соответствии с законодательством Российской Федерации о контрактной системе в сфере закупок товаров, работ, услуг для обеспечения государственных и муниципальных нужд, законодательством Российской Федерации о закупках товаров, работ, услуг отдельными видами юридических лиц.': [true, false], 'Повышение качества выполнения капитального ремонта и сноса объектов капитального строительства. ': [true, false], 'Повышение качества работ в области государственной экспертизы.': [false, false]}, false, false]}, {'Что должен содержать в своем составе раздел проекта "Пояснительная записка"?': [{'Сведения о функциональном назначении объекта капитального строительства, состав и характеристику производства, номенклатуру выпускаемой продукции (работ, услуг).': [true, false], 'Сведения о потребности объекта капитального строительства в топливе, газе, воде и электрической энергии.': [true, false], 'Данные о проектной мощности объекта капитального строительства - для объектов производственного назначения.': [true, false], 'Сведения об использовании возобновляемых источников энергии и вторичных энергетических ресурсов.': [true, false], 'Характеристику земельного участка, предоставленного для размещения объекта капитального строительства.': [false, false], 'Графическую часть.': [false, false]}, false, false]}, {'Что входит в понятие «этап строительства»?': [{'Строительство объекта капитального строительства из числа объектов капитального строительства, планируемых к строительству, реконструкции на одном земельном участке, если такой объект может быть введен в эксплуатацию и эксплуатироваться автономно (то есть независимо от строительства или реконструкции иных объектов капитального строительства на этом земельном участке).': [true, false], 'Реконструкция объекта капитального строительства из числа объектов капитального строительства, планируемых к строительству, реконструкции на одном земельном участке, если такой объект может быть введен в эксплуатацию и эксплуатироваться автономно (то есть независимо от строительства или реконструкции иных объектов капитального строительства на этом земельном участке).': [true, false], 'Реконструкция объекта капитального строительства из числа объектов капитального строительства, планируемых к реконструкции на одном земельном участке.': [false, false], 'Строительство или реконструкция объекта капитального строительства из числа объектов капитального строительства, планируемых к строительству, реконструкции на одном земельном участке.': [false, false], 'Строительство части объекта капитального строительства, которая может быть введена в эксплуатацию и эксплуатироваться автономно (то есть независимо от строительства или реконструкции иных частей этого объекта капитального строительства).': [true, false], 'Реконструкция части объекта капитального строительства, которая может быть введена в эксплуатацию и эксплуатироваться автономно (то есть независимо от строительства или реконструкции иных частей этого объекта капитального строительства).': [true, false]}, false, false]}, {'Что такое «проектная документация»?': [{'Проектная документация содержит сведения в отношении объекта капитального строительства, описание принятых технических и иных решений, пояснения, ссылки на нормативные и (или) технические документы, используемые при подготовке проектной документации и результаты расчетов, обосновывающие принятые решения.': [true, false], 'Проектная документация представляет собой документацию, отображающую принятые технические и иные решения и выполняется в виде чертежей, схем, планов и других документов в графической форме. ': [true, false], 'Проектная документация представляет собой документацию, определяющую архитектурные, функционально-технологические, конструктивные и инженерно-технические решения для обеспечения строительства, реконструкции объектов капитального строительства, их частей, капитального ремонта.': [true, false], 'Проектная документация содержит сведения, определяющие совокупность количественных показателей материалов, изделий, конструкций и оборудования, затрат труда работников в строительстве, времени эксплуатации машин и механизмов, установленных на принятую единицу измерения, и иных затрат.': [false, false], 'Проектная документация представляет собой документацию, содержащую спецификации оборудования и материалов.': [false, false], 'Проектная документация представляет собой документацию, содержащую материалы в форме информационной модели.': [true, false]}, false, false]}, {'Что такое опасные природные процессы и явления?': [{'Землетрясения, сели.': [true, false], 'Эрозия почвы и иные подобные процессы и явления, оказывающие негативные или разрушительные воздействия на здания и сооружения.': [true, false], 'Оползни, лавины.': [true, false], 'Негативные воздействия на здания и сооружения.': [false, false], 'Подтопление территории.': [true, false], 'Ураганы, смерчи.': [true, false]}, false, false]}, {'Что такое сложные природные условия?': [{'Наличие специфических по составу и состоянию грунтов на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [true, false], 'Наличие риска возникновения (развития) опасных природных процессов и явлений на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [true, false], 'Наличие некачественных по составу и состоянию грунтов на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [false, false], 'Наличие недопустимого риска возникновения (развития) опасных природных процессов и явлений на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [false, false], 'Наличие технологических воздействий на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [false, false], 'Наличие техногенных воздействий на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [true, false]}, false, false]}, {'Что такое модернизация здания?': [{'Частный случай реконструкции предусматривающий изменение и обновление его морально устаревшего инженерного оборудования.': [true, false], 'Реконструкция части здания, предусматривающая изменение и обновление его морально устаревшего инженерного оборудования.': [false, false], 'Капитальный ремонт здания, предусматривающий изменение и обновление его морально устаревшего инженерного оборудования.': [false, false], 'Капитальный ремонт здания, предусматривающий изменение и обновление объемно-планировочного и архитектурного решений существующего здания старой постройки.': [false, false], 'Реконструкция части здания, предусматривающая изменение и обновление объемно-планировочного и архитектурного решений существующего здания старой постройки.': [false, false], 'Частный случай реконструкции, предусматривающий изменение и обновление объемно-планировочного и архитектурного решений существующего здания старой постройки.': [true, false]}, false, false]}, {'Что такое сеть инженерно-технического обеспечения объекта капитального строительства?': [{'Сеть инженерно-технического обеспечения - одна из систем здания или сооружения, предназначенная для выполнения функций водоснабжения, канализации, отопления, вентиляции, кондиционирования воздуха, газоснабжения, электроснабжения, связи, информатизации, диспетчеризации, мусороудаления, вертикального транспорта (лифты, эскалаторы) или функций обеспечения безопасности.': [false, false], 'Сеть инженерно-технического обеспечения - совокупность трубопроводов, предназначенных для инженерно-технического обеспечения зданий и сооружений.': [true, false], 'Сеть инженерно-технического обеспечения - совокупность кабельных линий, предназначенных для инженерно-технического обеспечения зданий и сооружений.': [false, false], 'Сеть инженерно-технического обеспечения - совокупность коммуникаций и других сооружений, предназначенных для инженерно-технического обеспечения зданий и сооружений.': [true, false], 'Сеть инженерно-технического обеспечения - одна из сетей здания или сооружения, предназначенная для выполнения функций энергоснабжения, технического снабжения и обеспечения безопасности.': [false, false], 'Сеть инженерно-технического обеспечения - совокупность трубопроводов, кабелей (в том числе кабелей связи), предназначенных для инженерно-технического обеспечения зданий и сооружений.': [false, false]}, false, false]}, {'Какие здания относятся к зданиям и сооружениям нормального уровня ответственности?': [{'Все здания и сооружения за исключением зданий и сооружений повышенного уровня ответственности.': [true, false], 'Все здания и сооружения за исключением зданий и сооружений отнесенных к особо опасным, технически сложным объектам.': [false, false], 'Все здания и сооружения за исключением зданий и сооружений временного (сезонного) назначения, а также зданий и сооружений вспомогательного использования.': [false, false], 'Линейные объекты трубопроводного транспорта, железные дороги, метрополитены.': [false, false], 'Жилые здания, производственные здания, общественные здания.': [false, false], 'Все здания и сооружения за исключением зданий и сооружений пониженного уровня ответственности.': [true, false]}, false, false]}, {'Какие признаки установлены для идентификации зданий и сооружений по безопасности?': [{'Назначение.': [true, false], 'Адрес объекта или его координаты.': [false, false], 'Принадлежность к объектам транспортной инфраструктуры и к другим объектам, функционально-технологические особенности которых влияют на их безопасность.': [true, false], 'Принадлежность к особо опасным, технически сложным или уникальным объектам.': [false, false], 'Принадлежность к опасным производственным объектам.': [true, false], 'Наличие помещений с постоянным пребыванием людей.': [true, false]}, false, false]}, {'Кем устанавливается и где указывается необходимость разработки проектной документации на объект капитального строительства применительно к отдельным этапам строительства? ': [{'Необходимость разработки проектной документации на объект капитального строительства применительно к отдельным этапам строительства устанавливается застройщиком.': [false, false], 'Необходимость разработки проектной документации на объект капитального строительства применительно к отдельным этапам строительства устанавливается заказчиком.': [true, false], 'Необходимость разработки проектной документации на объект капитального строительства применительно к отдельным этапам строительства устанавливается проектной организацией.': [false, false], 'Необходимость разработки проектной документации на объект капитального строительства применительно к отдельным этапам строительства указывается в справке на получение банковского кредита.': [false, false], 'Необходимость разработки проектной документации на объект капитального строительства применительно к отдельным этапам строительства указывается в задании на проектирование.': [true, false], 'Необходимость разработки проектной документации на объект капитального строительства применительно к отдельным этапам строительства указывается в заявке на проведение экспертизы проектной документации.': [false, false]}, false, false]}, {'Какие объекты относятся к особо опасным и технически сложным объектам капитального строительства? ': [{'Объекты использования атомной энергии (в том числе ядерные установки, пункты хранения ядерных материалов и радиоактивных веществ, пункты хранения радиоактивных отходов).': [true, false], 'Линии электропередачи и иные объекты электросетевого хозяйства напряжением 220 киловольт и более.': [false, false], 'Сооружения связи, проектной документацией которых предусмотрены такие характеристики, как заглубление подземной части (полностью или частично) ниже планировочной отметки земли от пяти до десяти метров.': [true, false], 'Метрополитены.': [true, false], 'Объекты инфраструктуры железнодорожного транспорта общего пользования.': [true, false], 'Сооружения связи, проектной документацией которых предусмотрены такие характеристики, как высота от пятидесяти до ста метров.': [false, false]}, false, false]}, {'Какие объекты относятся к особо опасным и технически сложным объектам капитального строительства?': [{'Объекты инфраструктуры воздушного транспорта.': [true, false], 'Морские порты, за исключением объектов инфраструктуры морского порта, предназначенных для стоянок и обслуживания маломерных, спортивных парусных и прогулочных судов.': [false, false], 'Гидротехнические сооружения первого и второго классов, устанавливаемые в соответствии с законодательством о безопасности гидротехнических сооружений.': [true, false], 'Тепловые электростанции мощностью 100 мегаватт и выше.': [false, false], 'Подвесные канатные дороги.': [true, false], 'Линии электропередачи и иные объекты электросетевого хозяйства напряжением 330 киловольт и более.': [true, false]}, false, false]}, {'Какие признаки классифицируют объекты капитального строительства как уникальные? ': [{'Высота более чем 100 метров или заглубление подземной части (полностью или частично) ниже планировочной отметки земли более чем на 15 метров.': [true, false], 'Высота более чем 75 метров или заглубление подземной части (полностью или частично) ниже планировочной отметки земли более чем на 8 метров.': [false, false], 'Наличие не менее одной из перечисленных характеристик, указанных в п. 2 ст 48.1 ГрК РФ.': [true, false], 'Пролеты более чем 100 метров, наличие консоли более чем 20 метров.': [true, false], 'Наличие не менее двух из перечисленных характеристик, указанных п. 2 ст 48.1 ГрК РФ.': [false, false], 'Пролеты более чем 75 метров, наличие консоли более чем 15 метров.': [false, false]}, false, false]}, {'В отношении каких объектов органы государственной экспертизы, подведомственные федеральному органу исполнительной власти, наделены полномочиями по организации и проведению государственной экспертизы проектной документации?': [{'Объектов, строительство, реконструкцию которых предполагается осуществлять на территориях двух субъектов Российской Федерации.': [true, false], 'Объектов, строительство, реконструкцию которых предполагается осуществлять на территориях трех субъектов Российской Федерации.': [true, false], 'Автомобильных дорог регионального значения.': [false, false], 'Объектов, сведения о которых составляют государственную тайну.': [true, false], 'Особо опасных, технически сложных и уникальных объектов.': [true, false], 'Автомобильных дорог федерального значения.': [true, false]}, false, false]}, {'На каком основании квалификационный аттестат эксперта может быть аннулирован?': [{'Установление факта участия эксперта в экспертизе при наличии личной заинтересованности в ее результатах.': [true, false], 'Если в подготовке проектной документации и (или) выполнении инженерных изысканий участвовал эксперт лично.': [true, false], 'Если в подготовке проектной документации и (или) выполнении инженерных изысканий участвовали близкие родственники эксперта (родители, дети, усыновители, усыновленные, родные братья и родные сестры, дедушка, бабушка, внуки), супруг.': [true, false], 'Установление факта представления для прохождения аттестации документов, содержащих недостоверные сведения.': [true, false], 'Если в подготовке проектной документации и (или) выполнении инженерных изысканий участвовали близкие родственники эксперта (родители, дети, усыновители, усыновленные, родные и двоюродные братья и родные сестры, дедушка, бабушка, внуки, правнуки), супруг.': [false, false], 'Вступление в законную силу решения уполномоченных органов о привлечении лица, которому выдан квалификационный аттестат, к ответственности за правонарушения в сфере его профессиональной деятельности.': [true, false]}, false, false]}, {'Что является предметом экспертизы проектной документации?': [{'Оценка соответствия проектной документации требованиям технических регламентов.': [true, false], 'Оценка соответствия проектной документации санитарно-эпидемиологическим требованиям.': [true, false], 'Оценка соответствия проектной документации требованиям в области охраны окружающей среды.': [true, false], 'Оценка соответствия требований к обеспечению надежности и безопасности электроэнергетических систем и объектов электроэнергетики.': [true, false], 'Оценка соответствия результатов инженерных изысканий требованиям проектной документации.': [false, false], 'Оценка соответствия проектной документации результатам инженерных изысканий.': [true, false]}, false, false]}, {'Что является предметом экспертизы проектной документации?': [{'Проверка достоверности определения сметной стоимости строительства объектов капитального строительства в определенных случаях.': [true, false], 'Оценка соответствия проектной документации требованиям к безопасному использованию атомной энергии.': [true, false], 'Оценка соответствия требований антитеррористической защищенности объекта.': [true, false], 'Оценка соответствия заданию застройщика или технического заказчика на проектирование.': [true, false], 'Оценка соответствия проектной документации требованиям в области охраны окружающей среды, в отношении которой проводится государственная экологическая экспертиза.': [false, false], 'Оценка соответствия проектной документации результатам инженерных изысканий.': [true, false]}, false, false]}, {'В каком случае экспертиза результатов инженерных изысканий не проводится?': [{'В случае, если изыскания проводились для объектов индивидуального жилищного строительства.': [true, false], 'В случае, если изыскания проводились для объектов строительства с использованием проектной документации повторного использования.': [false, false], 'В случае, если имеется положительное заключение экспертизы на результаты инженерных изысканий для проектной документации на строительство объекта капитального строительства, расположенного на соседнем земельном участке.': [false, false], 'В случае, если инженерные изыскания выполнялись для линейного объекта.': [false, false], 'В случае, если для строительства, реконструкции не требуется получение разрешения на строительство.': [true, false], 'В случае, если не требовалось разрешение на проведение инженерных изысканий.': [false, false]}, false, false]}, {'Какие установлены минимально необходимые требования для аккредитации юридического лица на право проведения негосударственной экспертизы проектной документации и результатов инженерных изысканий?': [{'Наличие документов, подтверждающих аттестацию физических лиц на право подготовки заключений экспертизы проектной документации и результатов инженерных изысканий (копии паспорта, трудовых книжек, ИНН физ. лица заверенные установленным порядком).': [false, false], 'Наличие по месту основной работы не менее чем пять работников, имеющих аттестацию на право подготовки заключений экспертизы проектной документации.': [true, false], 'Наличие по месту основной работы не менее чем пять работников, имеющих аттестацию на право подготовки заключений экспертизы результатов инженерных изысканий.': [true, false], 'Наличие у юридического лица сайта в сети "Интернет".': [true, false], 'Подтверждение наличия у юридического лица документов об образовании юридического лица (Устав, ИНН, приказ о назначении руководителя).': [false, false], 'Наличие регламента проведения негосударственной экспертизы проектной документации, утвержденного приказом руководителя юридического лица и размещенного на сайте такого юридического лица в сети "Интернет".': [true, false]}, false, false]}, {'Каким условиям должен соответствовать эксперт для переаттестации его на право подготовки заключений экспертизы проектной документации и (или) результатов инженерных изысканий?': [{'Имеет высшее образование по профилю, соответствующему заявленному направлению деятельности эксперта.': [false, false], 'Постоянно проживает в Российской Федерации.': [true, false], 'Имеет стаж работы в сфере подготовки проектной документации и (или) выполнения инженерных изысканий по заявленному направлению деятельности эксперта не менее чем 5 лет или стаж работы на соответствующих должностях в органах либо организациях, проводящих экспертизу проектной документации и (или) результатов инженерных изысканий, не менее чем 3 года': [false, false], 'Не имеет непогашенную или неснятую судимость за совершение умышленного преступления.': [true, false], 'Обладает необходимыми знаниями в области законодательства Российской Федерации о градостроительной деятельности и законодательства Российской Федерации о техническом регулировании (включая требования к обеспечению безопасной эксплуатации объектов капитального строительства) в части, касающейся соответственно выполнения инженерных изысканий в целях проектирования, строительства и эксплуатации этих объектов либо осуществления их проектирования, строительства и эксплуатации.': [true, false], 'Не реже одного раза в 3 года проходит повышение квалификации в организации, ведущей образовательную деятельность, требования к которой установлены Министерством строительства.': [true, false]}, false, false]}, {'Что содержит текстовая часть проектной документации?': [{'Сведения в отношении объекта капитального строительства.': [true, false], 'Описание принятых технических и иных решений.': [true, false], 'Пояснения.': [true, false], 'Ссылки на нормативные и (или) технические документы, используемые при подготовке проектной документации.': [true, false], 'Принятые технические и иные решения в виде чертежей, схем, планов и других документов.': [false, false], 'Результаты расчетов, обосновывающие принятые решения.': [true, false]}, false, false]}, {'Что входит в понятие "объект капитального строительства"?': [{'Здание.': [true, false], 'Строение.': [true, false], 'Объекты, строительство которых завершено (объекты завершенного строительства).': [false, false], 'Малые архитектурные формы.': [false, false], 'Объекты, строительство которых не завершено (объекты незавершенного строительства).': [true, false], 'Сооружение.': [true, false]}, false, false]}, {'Что входит в понятие "реконструкция объекта капитального строительства"?': [{'Надстройка.': [true, false], 'Замена и (или) восстановление несущих строительных конструкций объекта капитального строительства, за исключением замены отдельных элементов таких конструкций на аналогичные или иные улучшающие показатели таких конструкций элементы и (или) восстановления указанных элементов': [true, false], 'Перестройка.': [true, false], 'Замена отдельных элементов несущих строительных конструкций на аналогичные или иные улучшающие показатели таких конструкций элементы и (или) восстановление указанных элементов.': [false, false], 'Расширение объекта капитального строительства.': [true, false], 'Изменение параметров объекта капитального строительства, его частей (высоты, количества этажей, площади, объема).': [true, false]}, false, false]}, {'Какие здания и сооружения относятся к зданиям и сооружениям пониженного уровня ответственности? ': [{'Здания и сооружения временного (сезонного) назначения.': [true, false], 'Здания и сооружения, за исключением зданий и сооружений повышенного уровня ответственности.': [false, false], 'Одноквартирные или сблокированные жилые дома.': [false, false], 'Здания и сооружения вспомогательного использования, связанные с осуществлением строительства или реконструкции здания или сооружения либо расположенные на земельных участках, предоставленных для индивидуального жилищного строительства.': [true, false], 'Здания и сооружения для содержания животных.': [false, false], 'Расположенные на земельных участках, предоставленных для индивидуального жилищного строительства.': [true, false]}, false, false]}, {'Что подлежит экспертной оценке при проведении повторной экспертизы проектной документации?': [{'Часть проектной документации и (или) результатов инженерных изысканий, в которую не вносились изменения.': [false, false], 'Часть проектной документации и (или) результатов инженерных изысканий, в которую были внесены изменения.': [true, false], 'Графическая часть проектной документации и (или) результаты инженерных изысканий, в которые были внесены изменения.': [false, false], 'Совместимость внесенных изменений с проектной документацией и (или) результатами инженерных изысканий, в отношении которых была ранее проведена государственная экспертиза.': [true, false], 'В случае если после проведения первичной (предыдущей повторной) государственной экспертизы в законодательство Российской Федерации внесены изменения, которые могут повлиять на результаты государственной экспертизы, экспертной оценке могут быть подвергнуты представленные проектная документация и (или) результаты инженерных изысканий в полном объеме. ': [false, false], 'В случае если после проведения первичной (предыдущей повторной) государственной экспертизы в законодательство Российской Федерации внесены изменения, которые могут повлиять на результаты государственной экспертизы, экспертной оценке могут быть подвергнуты представленные проектная документация и (или) результаты инженерных изысканий в полном объеме. Данное условие не применяется в случае проведения повторной государственной экспертизы проектной документации и (или) результатов инженерных изысканий, в которые внесены изменения в отношении участков, и (или) конструктивных элементов, и (или) дорожных сооружений линейных объектов транспортной инфраструктуры.': [false, false]}, false, false]}, {'Какие нормативные правовые акты регламентируют организацию и проведение экспертизы проектной документации и результатов инженерных изысканий?': [{'Статья 49 Градостроительного кодекса Российской Федерации от 29 декабря 2004г. № 190-ФЗ.': [true, false], 'Постановление Правительства Российской Федерации "О порядке организации и проведения государственной экспертизы проектной документации и результатов инженерных изысканий" от 05 марта 2007 № 145.': [true, false], 'Постановление Правительства Российской Федерации "Об утверждении Положения об организации и проведении негосударственной экспертизы проектной документации и (или) Результатов инженерных изысканий" от 31 марта 2012 г. № 272.': [true, false], 'Постановление Правительства Российской Федерации "О составе разделов проектной документации и требования к их содержанию" от 16 февраля 2008 года № 87.': [false, false], 'Гражданский кодекс Российской Федерации от 30 ноября 1994 г. №51-ФЗ.': [false, false], 'Федеральный закон "Технический регламент о безопасности зданий и сооружений" от 30.12.2009 № 384-ФЗ.': [false, false]}, false, false]}, {'На какие виды подразделяются объекты капитального строительства в зависимости от функционального назначения и характерных признаков?': [{'Объекты производственного назначения. ': [true, false], 'Объекты непроизводственного назначения.': [true, false], 'Нелинейные объекты.': [false, false], 'Линейные объекты.': [true, false], 'Объекты транспортной инфраструктуры.': [false, false], 'Особо опасные и технически сложные объекты.': [false, false]}, false, false]}, {'Кем и как утверждается экспертное заключение, подготовленное в форме электронного документа?': [{'Заключение, подготовленное в форме электронного документа, утверждается путем подписания его усиленной квалифицированной электронной подписью руководителя организации по проведению экспертизы.': [true, false], 'Заключение, подготовленное в форме электронного документа, утверждается путем подписания его усиленными квалифицированными электронными подписями экспертов.': [false, false], 'Заключение, подготовленное в форме электронного документа, утверждается путем подписания его усиленной квалифицированной электронной подписью должностного лица, уполномоченного руководителем.': [true, false], 'Заключение, подготовленное в форме электронного документа, утверждается путем подписания его простой квалифицированной электронной подписью должностного лица, уполномоченного руководителем.': [false, false], 'Заключение, подготовленное в форме электронного документа, утверждается путем подписания его простой квалифицированной электронной подписью руководителя организации по проведению экспертизы.': [false, false], 'Заключение, подготовленное в форме электронного документа, утверждается путем подписания его простыми квалифицированными электронными подписями экспертов.': [false, false]}, false, false]}, {'Кто принимает обоснованное решение о влиянии изменений на конструктивную надежность и безопасность объекта?': [{'Лицо, осуществляющее подготовку проектной документации.': [false, false], 'Застройщик по представлению лица, осуществляющего подготовку проектной документации.': [true, false], 'Органы государственного строительного надзора.': [false, false], 'Технический заказчик по представлению лица, осуществляющего подготовку проектной документации.': [true, false], 'Лицо, осуществляющее функции строительного контроля.': [false, false], 'Органы местного самоуправления.': [false, false]}, false, false]}, {'Каким признакам соответствует квалифицированная электронная подпись? ': [{'Получена в результате криптографического преобразования информации с использованием ключа электронной подписи.': [true, false], 'Ключ проверки электронной подписи указан в квалифицированном сертификате.': [true, false], 'Позволяет определить лицо, подписавшее электронный документ.': [true, false], 'Ключ проверки электронной подписи доступен по запросу в удостоверяющий центр.': [false, false], 'Позволяет обнаружить факт внесения изменений в электронный документ после момента его подписания.': [true, false], 'Для создания и проверки электронной подписи используются средства электронной подписи, имеющие подтверждение соответствия требованиям, установленным в соответствии с Федеральным законом от 06.04.2011 г. №63-ФЗ "Об электронной подписи".': [true, false]}, false, false]}, {'Проектная документация каких объектов должна содержать перечень мероприятий по гражданской обороне, мероприятий по предупреждению чрезвычайных ситуаций природного и техногенного характера?': [{'Опасных производственных объектов, определяемых таковыми в соответствии с законодательством Российской Федерации.': [true, false], 'Объектов капитального строительства, строительство которых финансируется за счет средств федерального бюджета.': [false, false], 'Особо опасных, технически сложных объектов.': [true, false], 'Объектов обороны и безопасности.': [true, false], 'Уникальных объектов.': [true, false], 'Линий электропередач 220 кВ и выше.': [false, false]}, false, false]}, {'Какие разделы проектной документации не разрабатываются для линейных объектов?': [{'Пояснительная записка.': [false, false], 'Схема планировочной организации земельного участка.': [true, false], 'Мероприятия по обеспечению пожарной безопасности.': [false, false], 'Мероприятия по охране окружающей среды.': [false, false], 'Смета на строительство.': [false, false], 'Мероприятия по обеспечению доступа инвалидов.': [true, false]}, false, false]}, {'Какие разделы проектной документации могут не разрабатываться, если объект финансируется без привлечения бюджетных средств?': [{'Система электроснабжения.': [false, false], 'Проект организации строительства.': [true, false], 'Мероприятия по обеспечению доступа инвалидов.': [false, false], 'Смета на строительство объектов капитального строительства.': [true, false], 'Мероприятия по обеспечению пожарной безопасности.': [false, false], 'Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений.': [false, false]}, false, false]}, {'Какие разделы проектной документации должны разрабатываться, если объект финансируется с привлечением бюджетных средств?': [{'Мероприятия по обеспечению пожарной безопасности.': [true, false], 'Проект организации строительства.': [true, false], 'Мероприятия по обеспечению доступа инвалидов.': [true, false], 'Схема планировочной организации земельного участка.': [true, false], 'Сведения об инженерном оборудовании, о сетях инженерно-технического обеспечения, перечень инженерно-технических мероприятий, содержание технологических решений.': [true, false], 'Смета на строительство объектов капитального строительства.': [true, false]}, false, false]}, {'По каким сторонам плана здания и сооружения наносят обозначение координационных осей?': [{'Как правило, по левой стороне.': [true, false], 'Как правило, по правой стороне.': [false, false], 'Как правило, по нижней стороне.': [true, false], 'Как правило, по верхней стороне.': [false, false], 'Как правило, по внутренней стороне.': [false, false], 'Как правило, по наружной стороне.': [false, false]}, false, false]}, {'Что такое "строительная конструкция"?': [{'Часть здания, выполняющая определенные несущие, ограждающие функции.': [true, false], 'Часть здания, выполняющая определенные эстетические функции.': [true, false], 'Часть сооружения, выполняющая определенные несущие, ограждающие функции.': [true, false], 'Часть сборной или монолитной конструкции, выполняющая определенные несущие, ограждающие и (или) эстетические функции.': [false, false], 'Составная часть сборной или монолитной конструкции.': [false, false], 'Часть сооружения, выполняющая определенные эстетические функции.': [true, false]}, false, false]}, {'На какие виды подразделяются объекты капитального строительства в зависимости от функционального назначения и характерных признаков?': [{'Объекты производственного назначения.': [true, false], 'Объекты непроизводственного назначения.': [true, false], 'Нелинейные объекты.': [false, false], 'Линейные объекты.': [true, false], 'Особо опасные объекты.': [false, false], 'Особо опасные, технически сложные и уникальные объекты.': [false, false]}, false, false]}, {'Каким образом как правило вносят изменения в проектную документацию, ранее переданную заказчику?': [{'Заменой (перевыпуском) тома - при его полной переработке.': [true, false], 'Зачеркиванием.': [false, false], 'Выпуском дополнительных томов.': [true, false], 'Закрашиванием белым цветом': [false, false], 'Подчисткой (смывкой).': [false, false], 'Заменой, добавлением или исключением отдельных листов тома.': [true, false]}, false, false]}, {'Чьими подписями оформляют титульные листы проектной документации?': [{'Разработчика проекта (проектировщика).': [false, false], 'Руководителя или главного инженера организации.': [true, false], 'Нормоконтролера.': [false, false], 'Лица, ответственного за подготовку проектной документации, например, главного инженера (архитектора) проекта.': [true, false], 'Застройщика.': [false, false], 'Технического заказчика.': [false, false]}, false, false]}, {'Какую информацию необходимо размещать на сайте юридического лица, аккредитованного на право проведения негосударственной экспертизы проектной документации и (или) результатов инженерных изысканий?': [{'Наименование, адрес (место нахождения) юридического лица.': [true, false], 'Номер контактного телефона и адрес электронной почты юридического лица.': [false, false], 'Состав органов управления юридического лица.': [true, false], 'Фамилии, имена, отчества работников юридического лица, аттестованных на право подготовки заключений экспертизы проектной документации и (или) результатов инженерных изысканий, с указанием направлений деятельности': [true, false], 'Номер контактного телефона юридического лица.': [true, false], 'Регламент проведения негосударственной экспертизы проектной документации и (или) негосударственной экспертизы результатов инженерных изысканий.': [true, false]}, false, false]}, {'Какие дополнительные документы имеет право затребовать организация по проведению государственной или негосударственной экспертизы?': [{'Расчеты технологических решений.': [true, false], 'Теплотехнические расчеты.': [false, false], 'Расчеты конструктивных решений.': [true, false], 'Расчеты инсоляции.': [false, false], 'Материалы инженерных изысканий.': [true, false], 'Расчеты естественного и (или) искусственного освещения.': [false, false]}, false, false]}, {'Как и в скольких экземплярах производится выдача заключения государственной экспертизы проектной документации?': [{'Положительное заключение государственной экспертизы на бумажном носителе выдается в 3 экземплярах.': [false, false], 'Выдача заключения государственной экспертизы осуществляется в электронной форме.': [true, false], 'В случае если документы для проведения государственной экспертизы представлены на бумажном носителе, выдача заключения государственной экспертизы осуществляется только на руки заявителю.': [false, false], 'В случае если проектная документация и (или) результаты инженерных изысканий содержат сведения, составляющие государственную тайну, выдача заключения государственной экспертизы осуществляется на бумажном носителе на руки заявителю или путем направления заказного письма, если иное не установлено законодательством Российской Федерации о государственной тайне.': [true, false], 'Положительное заключение государственной экспертизы на бумажном носителе выдается в 2 экземплярах.': [false, false], 'Положительное заключение государственной экспертизы на бумажном носителе выдается в 4 экземплярах.': [true, false]}, false, false]}, {'Какие документы помещаются в дело государственной экспертизы?': [{'Заявления о проведении государственной экспертизы (первичной и повторной).': [true, false], 'Копия договора.': [true, false], 'Проектная документация.': [false, false], 'Документы, содержащие выводы, сделанные привлеченными на договорной основе к проведению экспертизы организациями и (или) специалистами.': [true, false], 'Заключения государственной экспертизы (первичные и повторные).': [true, false], 'Иные связанные с проведением государственной экспертизы документы (копии документов), определенные законодательством Российской Федерации и организацией по проведению государственной экспертизы.': [true, false]}, false, false]}, {'Какой орган осуществляет ведение единого государственного реестра заключений экспертизы проектной документации объектов капитального строительства?': [{'Федеральная служба по экологическому, технологическому и атомному надзору.': [false, false], 'Определенное Федеральной службой по экологическому, технологическому и атомному надзору подведомственное ему государственное (бюджетное или автономное) учреждение.': [false, false], 'Определенное Министерством строительства и жилищно-коммунального хозяйства Российской Федерации подведомственное ему государственное (бюджетное или автономное) учреждение.': [true, false], 'Определенное Министерством экономического развития Российской Федерации подведомственное ему государственное (бюджетное или автономное) учреждение.': [false, false], 'Министерство строительства и жилищно-коммунального хозяйства Российской Федерации.': [true, false], 'Министерство экономического развития Российской Федерации.': [false, false]}, false, false]}, {'Какие требования в случае возникновения пожара должны соблюдаться?': [{'Сохранение устойчивости здания или сооружения, а также прочности несущих строительных конструкций в течение времени, необходимого для эвакуации людей и выполнения других действий, направленных на сокращение ущерба от пожара.': [true, false], 'Ограничение образования и распространения опасных факторов пожара в пределах очага пожара.': [true, false], 'Нераспространение пожара на соседние здания и сооружения.': [true, false], 'Эвакуация людей (с учетом особенностей инвалидов и других групп населения с ограниченными возможностями передвижения) в безопасную зону до нанесения вреда их жизни и здоровью вследствие воздействия опасных факторов пожара.': [true, false], 'Возможность доступа личного состава подразделений пожарной охраны и доставки средств пожаротушения в любое помещение здания или сооружения.': [true, false], 'Возможность проведения мероприятий по спасению людей и сокращению наносимого пожаром ущерба имуществу физических или юридических лиц, государственному или муниципальному имуществу, окружающей среде, жизни и здоровью животных и растений.': [true, false]}, false, false]}, {'Здание или сооружение должно быть спроектировано и построено таким образом, чтобы в процессе эксплуатации здания или сооружения обеспечивались безопасные условия для проживания и пребывания человека в зданиях и сооружениях по каким следующим показателям?': [{'Качество воздуха в производственных, жилых и иных помещениях зданий и сооружений и в рабочих зонах производственных зданий и сооружений.': [true, false], 'Качество воды, используемой в качестве питьевой и для хозяйственно-бытовых нужд.': [true, false], 'Инсоляция и солнцезащита помещений жилых, общественных и производственных зданий.': [true, false], 'Естественное и искусственное освещение помещений.': [true, false], 'Защита от шума в помещениях жилых и общественных зданий и в рабочих зонах производственных зданий и сооружений.': [true, false], 'Микроклимат помещений.': [true, false]}, false, false]}, {'Что должно быть предусмотрено в случаях, предусмотренных в задании на проектирование здания или сооружения, в проектной документации для обеспечения безопасности зданий и сооружений, строительство и эксплуатация которых планируются в сложных природных условиях?': [{'Меры, направленные на защиту людей, здания или сооружения, территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения, от воздействия опасных природных процессов и явлений и техногенных воздействий, а также меры, направленные на предупреждение и (или) уменьшение последствий воздействия опасных природных процессов и явлений и техногенных воздействий.': [true, false], 'Конструктивные меры, уменьшающие чувствительность строительных конструкций и основания к воздействию опасных природных процессов и явлений и техногенным воздействиям.': [true, false], 'Меры по улучшению свойств грунтов основания.': [true, false], 'Ведение строительных работ способами, не приводящими к проявлению новых и (или) интенсификации действующих опасных природных процессов и явлений.': [true, false], 'Пространственная работа строительных конструкций.': [false, false], 'Геометрическая и физическая нелинейность.': [false, false]}, false, false]}, {'Какие меры должны быть предусмотрены в проектной документации здания и сооружения с помещениями с пребыванием людей?': [{'Ограничению проникновения в помещения пыли, влаги, вредных и неприятно пахнущих веществ из атмосферного воздуха.': [true, false], 'Обеспечению воздухообмена, достаточного для своевременного удаления вредных веществ из воздуха и поддержания химического состава воздуха в пропорциях, благоприятных для жизнедеятельности человека.': [true, false], 'Предотвращению проникновения в помещения с постоянным пребыванием людей вредных и неприятно пахнущих веществ из трубопроводов систем и устройств канализации, отопления, вентиляции, кондиционирования, из воздуховодов и технологических трубопроводов, а также выхлопных газов из встроенных автомобильных стоянок.': [true, false], 'Предотвращению проникновения почвенных газов (радона, метана) в помещения, если в процессе инженерных изысканий обнаружено их наличие на территории, на которой будут осуществляться строительство и эксплуатация здания или сооружения.': [true, false], 'Геометрическая и физическая нелинейность.': [false, false], 'Пластические и реологические свойства материалов и грунтов.': [false, false]}, false, false]}, {'Что должно быть предусмотрено в проектной документации зданий и сооружений?': [{'Устройства для предупреждения случайного движения подвижных элементов оборудования здания или сооружения (в том числе при отказе устройств автоматического торможения), которое может привести к наступлению несчастных случаев и нанесению травм людям.': [true, false], 'Конструкция окон, обеспечивающая их безопасную эксплуатацию, в том числе мытье и очистку наружных поверхностей.': [true, false], 'Устройства для предупреждения случайного выпадения людей из оконных проемов (в случаях, когда низ проема ниже высоты центра тяжести большинства взрослых людей).': [true, false], 'Достаточное освещение путей перемещения людей и транспортных средств.': [true, false], 'Размещение хорошо различимых предупреждающих знаков на прозрачных полотнах дверей и перегородках.': [true, false], 'Потерю устойчивости положения.': [false, false]}, false, false]}, {'Какие в проектной документации должны быть предусмотрены меры по предотвращению наступления несчастных случаев и нанесения травм людям в результате взрывов?': [{'Соблюдение правил безопасности устройства систем отопления, горячего водоснабжения, газоиспользующего оборудования, дымоходов, дымовых труб, резервуаров и трубопроводов для воспламеняющихся жидкостей и газов.': [true, false], 'Соблюдение правил безопасной установки теплогенераторов и установок для сжиженных газов.': [true, false], 'Регулирование температуры нагревания и давления в системах горячего водоснабжения и отопления.': [true, false], 'Предотвращение чрезмерного накопления взрывоопасных веществ в воздухе помещений, в том числе путем использования приборов газового контроля.': [true, false], 'Сопротивление теплопередаче ограждающих строительных конструкций здания или сооружения.': [false, false], 'Разность температуры на внутренней поверхности ограждающих строительных конструкций и температуры воздуха внутри здания или сооружения во время отопительного периода.': [false, false]}, false, false]}, {'Какая информация должна содержаться в целях предупреждения действий, вводящих в заблуждение приобретателей, в проектной документации здания или сооружения?': [{'Идентификационные признаки здания или сооружения.': [true, false], 'Срок эксплуатации здания или сооружения и их частей.': [true, false], 'Показатели энергетической эффективности здания или сооружения.': [true, false], 'Степень огнестойкости здания или сооружения.': [true, false], 'Температура воздуха внутри здания или сооружения.': [false, false], 'Результирующая температура.': [false, false]}, false, false]}, {'Что представляет собой техническое регулирование в области пожарной безопасности?': [{'Установление в нормативных правовых актах Российской Федерации и нормативных документах по пожарной безопасности требований пожарной безопасности к продукции, процессам проектирования, производства, эксплуатации, хранения, транспортирования, реализации и утилизации.': [true, false], 'Правовое регулирование отношений в области применения и использования требований пожарной безопасности.': [true, false], 'Правовое регулирование отношений в области оценки соответствия.': [true, false], 'Принадлежность к объектам транспортной инфраструктуры и к другим объектам, функционально-технологические особенности которых влияют на их безопасность.': [false, false], 'Возможность опасных природных процессов и явлений и техногенных воздействий на территории, на которой будут осуществляться строительство, реконструкция и эксплуатация здания или сооружения.': [false, false], 'Принадлежность к опасным производственным объектам.': [false, false]}, false, false]}, {'Что относится к опасным факторам пожара, воздействующим на людей и имущество?': [{'Пламя.': [true, false], 'Тепловой поток.': [true, false], 'Повышенная температура окружающей среды.': [true, false], 'Искры.': [true, false], 'Повреждение инженерных коммуникаций.': [false, false], 'Радиоактивные и токсичные вещества и материалы, попавшие в окружающую среду из разрушенных технологических установок, оборудования, агрегатов, изделий и иного имущества.': [false, false]}, false, false]}, {'Что относится к опасным факторам пожара, воздействующим на людей и имущество?': [{'Воздействие огнетушащих веществ.': [false, false], 'Вынос высокого напряжения на токопроводящие части технологических установок, оборудования, агрегатов, изделий и иного имущества.': [false, false], 'Осколки, части разрушившихся зданий, сооружений, транспортных средств, технологических установок, оборудования, агрегатов, изделий и иного имущества.': [false, false], 'Повышенная концентрация токсичных продуктов горения и термического разложения.': [true, false], 'Пониженная концентрация кислорода.': [true, false], 'Снижение видимости в дыму.': [true, false]}, false, false]}, {'На какие группы подразделяются вещества и материалы по горючести?': [{'Горючие - вещества и материалы, неспособные гореть в воздухе. Горючие вещества могут быть пожаровзрывоопасными (например, окислители или вещества, выделяющие горючие продукты при взаимодействии с водой, кислородом воздуха или друг с другом).': [false, false], 'Легковоспламеняемые - вещества и материалы, способные гореть в воздухе при воздействии источника зажигания, но неспособные самостоятельно гореть после его удаления.': [false, false], 'Негорючие - вещества и материалы, способные самовозгораться, а также возгораться под воздействием источника зажигания и самостоятельно гореть после его удаления.': [false, false], 'Негорючие - вещества и материалы, неспособные гореть в воздухе. Негорючие вещества могут быть пожаровзрывоопасными (например, окислители или вещества, выделяющие горючие продукты при взаимодействии с водой, кислородом воздуха или друг с другом).': [true, false], 'Трудногорючие - вещества и материалы, способные гореть в воздухе при воздействии источника зажигания, но неспособные самостоятельно гореть после его удаления.': [true, false], 'Горючие - вещества и материалы, способные самовозгораться, а также возгораться под воздействием источника зажигания и самостоятельно гореть после его удаления.': [true, false]}, false, false]}, {'Какими свойствами характеризуется пожарная опасность строительных, текстильных и кожевенных материалов?': [{'Горючесть.': [true, false], 'Воспламеняемость.': [true, false], 'Способность распространения пламени по поверхности.': [true, false], 'Дымообразующая способность.': [true, false], 'Токсичность продуктов горения.': [true, false], 'Ударный шум.': [false, false]}, false, false]}, {'На основе каких принципов осуществляется подтверждение соответствия?': [{'Доступности информации о порядке осуществления подтверждения соответствия заинтересованным лицам.': [true, false], 'Недопустимости применения обязательного подтверждения соответствия к объектам, в отношении которых не установлены требования технических регламентов.': [true, false], 'Установления перечня форм и схем обязательного подтверждения соответствия в отношении определенных видов продукции в соответствующем техническом регламенте.': [true, false], 'Уменьшения сроков осуществления обязательного подтверждения соответствия и затрат заявителя.': [true, false], 'Недопустимости принуждения к осуществлению добровольного подтверждения соответствия, в том числе в определенной системе добровольной сертификации.': [true, false], 'Защиты имущественных интересов заявителей, соблюдения коммерческой тайны в отношении сведений, полученных при осуществлении подтверждения соответствия.': [true, false]}, false, false]}, {'Что должна содержать декларация о соответствии?': [{'Наименование и местонахождение заявителя.': [true, false], 'Наименование и местонахождение изготовителя.': [true, false], 'Информацию об объекте подтверждения соответствия, позволяющую идентифицировать этот объект.': [true, false], 'Наименование технического регламента, на соответствие требованиям которого подтверждается продукция.': [true, false], 'Указание на схему декларирования соответствия.': [true, false], 'Заявление заявителя о безопасности продукции при ее использовании в соответствии с целевым назначением и принятии заявителем мер по обеспечению соответствия продукции требованиям технических регламентов.': [true, false]}, false, false]}, {'Что входит в обязанности должностных лиц, осуществляющих федеральный государственный метрологический надзор?': [{'Знакомиться с результатами проверки и указывать в акте проверки о своем ознакомлении с результатами проверки, согласии или несогласии с ними, а также с отдельными действиями должностных лиц органа федерального государственного метрологического надзора.': [false, false], 'Обжаловать действия (бездействие) должностных лиц органа федерального государственного метрологического надзора, повлекшие за собой нарушение прав юридического лица, индивидуального предпринимателя при проведении проверки, в административном и (или) судебном порядке в соответствии с законодательством Российской Федерации.': [false, false], 'Проверять соответствие используемых единиц величин единицам величин, допущенным к применению в Российской Федерации.': [true, false], 'Проверять наличие и соблюдение аттестованных методик (методов) измерений.': [true, false], 'Своевременно и в полной мере исполнять полномочия по предупреждению, выявлению и пресечению нарушений обязательных требований.': [true, false], 'Соблюдать государственную, коммерческую, служебную и иную охраняемую законом тайну.': [true, false]}, false, false]}, {'Что обязано сделать должностное лицо, осуществляющее федеральный государственный метрологический надзор, при выявлении нарушений?': [{'Запрещать выпуск из производства, ввоз на территорию Российской Федерации и продажу предназначенных для применения в сфере государственного регулирования обеспечения единства измерений стандартных образцов и средств измерений неутвержденных типов или предназначенных для применения в сфере государственного регулирования обеспечения единства измерений стандартных образцов и средств измерений, не соответствующих обязательным требованиям (за исключением выпуска из производства и ввоза на территорию Российской Федерации стандартных образцов или средств измерений, предназначенных для проведения испытаний стандартных образцов или средств измерений в целях утверждения типа).': [true, false], 'Запрещать применение эталонов единиц величин, не удовлетворяющих установленным обязательным требованиям, стандартных образцов и средств измерений неутвержденных типов, а также неповеренных средств измерений или средств измерений, не соответствующих установленным обязательным требованиям, при выполнении измерений, относящихся к сфере государственного регулирования обеспечения единства измерений.': [true, false], 'Наносить на средства измерений и эталоны единиц величин знак непригодности в случаях, когда средство измерений и эталон единиц величин не соответствуют обязательным требованиям.': [true, false], 'Давать обязательные к исполнению предписания и устанавливать сроки устранения нарушений, установленных законодательством Российской Федерации обязательных требований.': [true, false], 'Составлять протоколы об административных правонарушениях в пределах полномочий, предусмотренных статьей 23.52 Кодекса Российской Федерации об административных правонарушениях.': [true, false], 'Принимать меры по контролю за устранением выявленных нарушений, их предупреждению, предотвращению возможного причинения вреда жизни, здоровью граждан, вреда животным, растениям, окружающей среде, обеспечению безопасности государства, предупреждению возникновения чрезвычайных ситуаций техногенного характера в соответствии со сроками, установленными соответствующими предписаниями, а также меры, направленные на привлечение должностных и юридических лиц к административной ответственности за неисполнение предписаний.': [true, false]}, false, false]}, {'Что принимают за единицу учета документации?': [{'Для проектной документации - текстовые и графические документы, сброшюрованные в том (папку).': [true, false], 'Для рабочей документации - графические и текстовые документы, имеющие самостоятельное обозначение.': [true, false], 'Для иной технической документации - сброшюрованный том текстовых и/или графических материалов, имеющих свое наименование, шифр или обозначение.': [true, false], 'Преобразование не должно изменять наименование документа и его вида в зависимости от способа их выполнения и характера использования (подлинник, дубликат, копия).': [false, false], 'Документы, полученные в результате взаимного преобразования, должны иметь соответствующие ссылки друг на друга.': [false, false], 'Документы одного вида и наименования, независимо от формы выполнения.': [false, false]}, false, false]}, {'Как допускается хранить подлинники документов в бумажной форме?': [{'Наличие установленных подписей и дат, в том числе подписей специалистов, осуществляющих нормоконтроль проектной и рабочей документации.': [false, false], 'Правильность заполнения основных надписей и оформления титульных листов документов по ГОСТ Р 21.1101 "Система проектной документации для строительства(СПДС). Основные требования к проектной и рабочей документации.". ': [false, false], 'Правильность заполнения информационно-удостоверяющих листов одного или нескольких электронных документов, наличие электронных цифровых подписей или иных аналогов собственноручных подписей.': [false, false], 'В порядке возрастания их инвентарных номеров. Внутри папки с одним инвентарным номером чертежи располагают и хранят в порядке возрастания номеров листов, без учета форматов.': [true, false], 'По объектам - в порядке возрастания обозначений документов (по основным комплектам рабочих чертежей (маркам) и прилагаемым к ним документам).': [true, false], 'В другом установленном организацией порядке.': [true, false]}, false, false]}, {'В каком порядке комплектуют текстовые и графические материалы в том проектной документации?': [{'Обложка - титульный лист - содержание тома - ведомость "состав проектной документации" - текстовая часть - графическая часть (чертежи и схемы).': [true, false], 'Титульный лист - обложка - содержание тома - ведомость "состав проектной документации" - текстовая часть - графическая часть (чертежи и схемы)…': [false, false], 'Обложка - Содержание тома - ведомость "состав проектной документации" - текстовая часть - графическая часть (чертежи и схемы).': [false, false], 'Обложка - титульный лист - содержание тома - ведомость "состав проектной документации" - текстовая часть…': [true, false], '...текстовая часть - графическая часть (чертежи и схемы).': [true, false], '…графическая часть (чертежи и схемы) - текстовая часть.': [false, false]}, false, false]}, {'Как указывают линейные размеры на чертежах без обозначения единиц длины?': [{'В метрах с точностью до двух знаков после запятой - на чертежах наружных сетей и коммуникаций, генерального плана и транспорта, за исключением случаев, оговоренных в соответствующих стандартах СПДС.': [true, false], 'В сантиметрах с точностью до двух знаков после запятой - на чертежах наружных сетей и коммуникаций, генерального плана и транспорта, за исключением случаев, оговоренных в соответствующих стандартах СПДС.': [false, false], 'В дюймах с точностью до двух знаков после запятой - на чертежах наружных сетей и коммуникаций, генерального плана и транспорта, за исключением случаев, оговоренных в соответствующих стандартах СПДС.': [false, false], 'В метрах с точностью до десяти знаков после запятой - на чертежах наружных сетей и коммуникаций, генерального плана и транспорта, за исключением случаев, оговоренных в соответствующих стандартах СПДС.': [false, false], 'В метрах с точностью до одного знака после запятой - на чертежах наружных сетей и коммуникаций, генерального плана и транспорта, за исключением случаев, оговоренных в соответствующих стандартах СПДС.': [false, false], 'В миллиметрах - на всех остальных видах чертежей.': [true, false]}, false, false]}, {'Чьими подписями оформляют титульные листы томов проектной документации?': [{'Руководителя или главного инженера организации.': [true, false], 'Лица, ответственного за подготовку проектной документации, например, главного инженера (архитектора) проекта.': [true, false], 'Заказчика.': [false, false], 'Представителя заказчика.': [false, false], 'Лица непосредственно осуществляющего разработку.': [false, false], 'Экспертной организации.': [false, false]}, false, false]}, {'При условии соответствия каким требованиям эксперт может быть переаттестован на право подготовки заключений экспертизы проектной документации и (или) результатов инженерных изысканий?': [{'Имеет высшее профессиональное образование по профилю, соответствующему заявленному направлению деятельности эксперта.': [false, false], 'Имеет стаж работы в сфере подготовки проектной документации и (или) выполнения инженерных изысканий по заявленному направлению деятельности эксперта не менее чем 5 лет или стаж работы на соответствующих должностях в органах либо организациях, проводящих экспертизу проектной документации и (или) результатов инженерных изысканий, не менее чем 3 года.': [false, false], 'Не постоянно проживает в Российской Федерации.': [false, false], 'Постоянно проживает в Российской Федерации.': [true, false], 'Не имеет непогашенную или неснятую судимость за совершение умышленного преступления.': [true, false], 'Обладает необходимыми знаниями в области законодательства Российской Федерации о градостроительной деятельности и законодательства Российской Федерации о техническом регулировании (включая требования к обеспечению безопасной эксплуатации объектов капитального строительства) в части, касающейся соответственно выполнения инженерных изысканий в целях проектирования, строительства и эксплуатации этих объектов либо осуществления их проектирования, строительства и эксплуатации.': [true, false]}, false, false]}, {'Какие документы для прохождения аттестации претендент представляет в Минстрой России?': [{'Заявление с указанием фамилии, имени, отчества (при наличии), идентификационного номера налогоплательщика, страхового номера индивидуального лицевого счета в системе обязательного пенсионного страхования, адреса электронной почты (при наличии), направления деятельности эксперта, по которому претендент имеет намерение получить право подготовки заключений экспертизы проектной документации и (или) результатов инженерных изысканий, из числа направлений деятельности экспертов, включенных в перечень, устанавливаемый Министерством (далее - направление деятельности), а также сведений об отсутствии непогашенной или неснятой судимости за совершение умышленного преступления.': [true, false], 'Копия паспорта либо иного основного документа, удостоверяющего личность, а также копия документа, подтверждающего постоянное проживание в Российской Федерации, если в основном документе, удостоверяющем личность, отсутствуют сведения о постоянном проживании в Российской Федерации.': [true, false], 'Копия диплома о высшем профессиональном образовании.': [true, false], 'Заверенная в установленном законодательством Российской Федерации порядке копия трудовой книжки или иных документов, подтверждающих требуемый стаж работы.': [true, false], 'Сведения о документах, на основании которых принято решение о аттестации.': [false, false], 'Копию военного билета.': [false, false]}, false, false]}, {'Какие из перечисленных объектов использования атомной энергии должны быть идентифицированы как объекты повышенного уровня ответственности?': [{'Космические и летательные аппараты с ядерными реакторами.': [true, false], 'Сооружения, комплексы, полигоны, установки и устройства с ядерными зарядами для использования в мирных целях.': [true, false], 'Не относящиеся к ядерным установкам комплексы, установки, аппараты, оборудование и изделия, в которых содержатся радиоактивные вещества или генерируется ионизирующее излучение.': [true, false], 'Материалы, содержащие или способные воспроизвести делящиеся (расщепляющиеся) ядерные вещества.': [true, false], 'Сооружения и комплексы с промышленными, экспериментальными и исследовательскими критическими и подкритическими ядерными стендами.': [true, false], 'Транспортные и транспортабельные средства с ядерными реакторами.': [true, false]}, false, false]}, {'Какие полномочия относятся к компетенции органов государственной власти Российской Федерации в области градостроительной деятельности? ': [{'К компетенции органов государственной власти Российской Федерации в области градостроительной деятельности относится подготовка и утверждение документов территориального планирования Российской Федерации.': [true, false], 'К компетенции органов государственной власти Российской Федерации в области градостроительной деятельности относится организация и проведение государственной экспертизы проектов документов территориального планирования Российской Федерации.': [false, false], 'К компетенции органов государственной власти Российской Федерации в области градостроительной деятельности относится техническое регулирование в области градостроительной деятельности.': [true, false], 'К компетенции органов государственной власти Российской Федерации в области градостроительной деятельности относится установление перечня видов работ по инженерным изысканиям, по подготовке проектной документации, по строительству, реконструкции, капитальному ремонту объектов капитального строительства, которые оказывают влияние на безопасность объектов капитального строительства.': [true, false], 'К компетенции органов государственной власти Российской Федерации в области градостроительной деятельности относится организация и проведение государственной экспертизы проектной документации объектов, строительство, реконструкцию которых предполагается осуществлять на территориях двух и более субъектов Российской Федерации, посольств, консульств и представительств Российской Федерации за рубежом, в исключительной экономической зоне Российской Федерации, на континентальном шельфе Российской Федерации, во внутренних морских водах, в территориальном море Российской Федерации, объектов обороны и безопасности, иных объектов, сведения о которых составляют государственную тайну, автомобильных дорог федерального значения, объектов культурного наследия (памятников истории и культуры) федерального значения (в случае, если при проведении работ по сохранению объекта культурного наследия федерального значения затрагиваются конструктивные и другие характеристики надежности и безопасности такого объекта), указанных в статье 48.1 настоящего Кодекса особо опасных, технически сложных и уникальных объектов, объектов, используемых для обезвреживания и (или) захоронения отходов I - V классов опасности, иных объектов, определенных Правительством Российской Федерации, а также результатов инженерных изысканий, выполняемых для подготовки проектной документации указанных в настоящем пункте объектов.': [true, false], 'К компетенции органов государственной власти Российской Федерации в области градостроительной деятельности относится подготовка и утверждение документов территориального планирования двух и более субъектов Российской Федерации, документов территориального планирования субъекта Российской Федерации.': [false, false]}, false, false]}, {'Что представляют собой результаты инженерных изысканий? ': [{'Документ о выполненных инженерных изысканиях, содержащий материалы в текстовой форме и в виде карт (схем) и отражающий сведения о задачах инженерных изысканий. ': [true, false], 'Документ о местоположении территории, на которой планируется осуществлять строительство, реконструкцию объекта капитального строительства, о видах, об объеме, о способах и о сроках проведения работ по выполнению инженерных изысканий в соответствии с программой инженерных изысканий. ': [true, false], 'Документ о качестве выполненных инженерных изысканий, о результатах комплексного изучения природных и техногенных условий указанной территории, в том числе о результатах изучения, оценки и прогноза возможных изменений природных и техногенных условий указанной территории применительно к объекту капитального строительства при осуществлении строительства, реконструкции такого объекта и после их завершения и о результатах оценки влияния строительства, реконструкции такого объекта на другие объекты капитального строительства.': [true, false], 'Документ о выявлении необходимости выполнения отдельных видов инженерных изысканий, состав, объем и метод их выполнения устанавливаются с учетом требований технических регламентов программой инженерных изысканий, разработанной на основе задания застройщика или технического заказчика, в зависимости от вида и назначения объектов капитального строительства, их конструктивных особенностей, технической сложности и потенциальной опасности, стадии архитектурно-строительного проектирования, а также от сложности топографических, инженерно-геологических, экологических, гидрологических, метеорологических и климатических условий территории, на которой будут осуществляться строительство, реконструкция объектов капитального строительства, степени изученности указанных условий.': [false, false], 'Отчет о выполненных инженерных изысканиях для проектирования и строительства объекта капитального строительства.': [false, false], 'Документ о видах инженерных изысканий, порядок их выполнения для подготовки проектной документации, строительства, реконструкции объектов капитального строительства, а также состав, форма материалов и результатов инженерных изысканий, порядок формирования и ведения государственного фонда материалов и данных инженерных изысканий с учетом потребностей информационных систем обеспечения градостроительной деятельности устанавливаются Правительством Российской Федерации.': [false, false]}, false, false]}, {'В каком виде проектная документация и результаты инженерных изысканий представляются на государственную (негосударственную) экспертизу?': [{'C 1 января 2017 г. проектная документация и (или) результаты инженерных изысканий, а также иные документы, необходимые для проведения государственной экспертизы проектной документации и (или) результатов инженерных изысканий, представляются в государственное учреждение, подведомственное Министерству строительства и жилищно-коммунального хозяйства Российской Федерации, а также в уполномоченные на проведение такой государственной экспертизы органы исполнительной власти субъектов Российской Федерации или подведомственные этим органам государственные учреждения в электронной форме.': [true, false], 'В случаях, если проектная документация направлена на повторную экспертизу по причине внесения изменения в отдельные разделы проектной и отчетных материалов инженерных изысканий документации, при этом проектная документация получила положительное заключение государственной экспертизы до 1 января 2017 года допускается представление отдельных разделов проектной документации и результатов инженерных выпущенных до 1 января 2017 года в бумажном виде, при условии верификации текстовой и графической части соответствующих разделов проектной документации.': [false, false], 'Документы представляются в форме электронных документов с использованием официального сайта экспертной организации в информационно-телекоммуникационной сети "Интернет", а в предусмотренных законодательством Российской Федерации случаях - также с использованием сервиса "личный кабинет" федеральной государственной информационной системы "Единый портал государственных и муниципальных услуг (функций)" при условии регистрации заявителя и экспертной организации в федеральной государственной информационной системе "Единая система идентификации и аутентификации в инфраструктуре, обеспечивающей информационно-технологическое взаимодействие информационных систем, используемых для предоставления государственных и муниципальных услуг в электронной форме". ': [true, false], 'В случаях, если застройщик или технический заказчик обеспечивает формирование и ведение информационной модели, результаты инженерных изысканий подготавливаются в форме, позволяющей осуществлять их использование при формировании и ведении информационной модели.': [true, false], 'Документы представляются в электронном виде, за исключением случаев, когда проектная документация и (или) результаты инженерных изысканий содержат сведения, составляющие государственную тайну. ': [true, false], 'В случае подготовки проектной документации и результатов инженерных изысканий подлежащих негосударственной экспертизе, застройщик или технический заказчик самостоятельно принимает решение в каком виде представляется документация, данное требование устанавливается в договоре с проектной и экспертной организациями в соответствии с Гражданским кодексом Российской Федерации.': [false, false]}, false, false]}, {'Какие направления деятельности входят в состав понятия "градостроительная деятельность"?': [{'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде благоустройства территорий. ': [true, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде градостроительного зонирования, планировки территории. ': [true, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде инженерных изысканий.': [false, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде архитектурно-строительного проектирования. ': [true, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде экспертизы проектной документации и результатов инженерных изысканий.': [false, false], 'В состав понятия "градостроительная деятельность" входит деятельность, осуществляемая в виде эксплуатации зданий, сооружений. ': [true, false]}, false, false]}, {'Какие виды зон устанавливаются путем осуществления градостроительной деятельности?': [{'Путем осуществления градостроительной деятельности устанавливаются функциональные зоны.': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются санитарно-защитные зоны. ': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются территориальные зоны.': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются защитные зоны объектов культурного наследия. ': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются водоохранные зоны. ': [true, false], 'Путем осуществления градостроительной деятельности устанавливаются зоны санитарной охраны источников питьевого и хозяйственно-бытового водоснабжения. ': [true, false]}, false, false]}, {'В каких случаях не проводится экспертиза проектной документации?': [{'Экспертиза не проводится в отношении проектной документации на отдельно стоящие жилые дома с количеством этажей не более чем три, предназначенные для проживания одной семьи (объекты индивидуального жилищного строительства). ': [true, false], 'Экспертиза не проводится в отношении проектной документации жилые дома с количеством этажей не более чем три, состоящие из нескольких блоков, количество которых не превышает десять и каждый из которых предназначен для проживания одной семьи, имеет общую стену (общие стены) без проемов с соседним блоком или соседними блоками, расположен на отдельном земельном участке и имеет выход на территорию общего пользования (жилые дома блокированной застройки), в случае, если строительство или реконструкция таких жилых домов осуществляется с привлечением средств бюджетов бюджетной системы Российской Федерации. ': [false, false], 'Экспертиза не проводится в отношении проектной документации на отдельно стоящие объекты капитального строительства с количеством этажей не более чем два, общая площадь которых составляет не более чем 1500 квадратных метров и которые не предназначены для проживания граждан и осуществления производственной деятельности, включая уникальные объекты. ': [false, false], 'Экспертиза не проводится в отношении проектной документации отдельно стоящие объекты капитального строительства с количеством этажей не более чем два, общая площадь которых составляет не более чем 1500 квадратных метров, которые предназначены для осуществления производственной деятельности и для которых не требуется установление санитарно-защитных зон или для которых в пределах границ земельных участков, на которых расположены такие объекты, установлены санитарно-защитные зоны или требуется установление таких зон, за исключением объектов, которые в соответствии со статьей 48.1 Градостроительного кодекса Российской Федерации являются особо опасными, технически сложными или уникальными объектами.': [true, false], 'Экспертиза не проводится в отношении проектной документации на буровые скважины, предусмотренные подготовленными, согласованными и утвержденными в соответствии с законодательством Российской Федерации о недрах техническим проектом разработки месторождений полезных ископаемых или иной проектной документацией на выполнение работ, связанных с пользованием участками недр. ': [true, false], 'Экспертиза проектной документации не проводится в случае, если для строительства или реконструкции объекта капитального строительства не требуется получение разрешения на строительство. ': [true, false]}, false, false]}, {'В каких случаях не проводится экспертиза проектной документации?': [{'Если для строительства или реконструкции не требуется получение разрешения на строительство.': [true, false], 'Если при строительстве или реконструкции объекта капитального строительства применяется модифицированная проектная документация.': [false, false], 'В отношении разделов проектной документации, подготовленных для проведения капитального ремонта объектов капитального строительства.': [true, false], 'Проведение государственной экспертизы или негосударственной экспертизы проектной документации и (или) результатов инженерных изысканий является обязательным.': [false, false], 'Застройщиком или техническим заказчиком принято решение о проведении государственной экспертизы.': [false, false], 'Объекты, строительство или реконструкцию которых предполагается осуществлять на территориях 2 и более субъектов Российской Федерации, посольств, консульств и представительств Российской Федерации за рубежом.': [false, false]}, false, false]}
      ],

    }
  },
  methods: {
    getExamScore() {
      this.resetUserInput()
      for (const index in this.allIndexes) {
        for (const [key,] of Object.entries(this.questionList[index])) {
          let wrongAnswer = 0
          for (const [key2,] of Object.entries(this.questionList[index][key][0])) {
            //console.log("this.questionList[this.cardIndex][key][0][key2]", this.questionList[this.cardIndex][key][0][key2])
            if (this.questionList[index][key][0][key2][0] == !this.questionList[index][key][0][key2][1]) {
              wrongAnswer = wrongAnswer + 1
            }
          }
          if (wrongAnswer == 0) {
            this.correctCounterExam = this.correctCounterExam + 1
          }
        }
      }
      this.examComplete = true
    },
    resetUserInput() {
      for (const [key,] of Object.entries(this.questionList[this.cardIndex])) {
        for (const [key2,] of Object.entries(this.questionList[this.cardIndex][key][0])) {
          //console.log("this.questionList[this.cardIndex][key][0][key2]", this.questionList[this.cardIndex][key][0][key2])
          this.questionList[this.cardIndex][key][0][key2][1] = false
        }
      }
    },
    downgradeCounter() {
      if (this.counter > 0) {
        this.counter = this.counter - 1
        this.resetUserInput()
      }
    },
    upgradeCounter() {
      if (this.counter < this.allIndexes.length-1) {
        this.counter = this.counter + 1
        this.resetUserInput()
      }
    },    
    resetColors() {
      this.answerCorrect.backgroundColor=''
      this.answerWrong.backgroundColor=''
    },
    restoreColors() {
      this.answerCorrect.backgroundColor='#82fd6a'
      this.answerWrong.backgroundColor='#ffaa90' 
    },    
    update() {
      this.resetColors()
      this.counter = 0
      this.correctCounter = 0
      this.examComplete = false
      this.correctCounterExam = 0
      for (let i=0; i<this.questionList.length; i++) {
        for (const [key,] of Object.entries(this.questionList[i])) {
          for (const [key2,] of Object.entries(this.questionList[i][key][0])) {
            //console.log("this.questionList[this.cardIndex][key][0][key2]", this.questionList[this.cardIndex][key][0][key2])
            this.questionList[i][key][0][key2][1] = false
          }          
          this.questionList[i][key][1] = false
          this.questionList[i][key][2] = false
        }
      }

    },
    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [array[i], array[j]] = [array[j], array[i]];
      }
      return array
    },    
    setTrue(name, name2, evt) {
      this.questionList[this.cardIndex][name][0][name2][1] = !this.questionList[this.cardIndex][name][0][name2][1]
      let el = evt.target
      if (this.questionList[this.cardIndex][name][0][name2][1] == true) {
        el.classList.add('selectedStyle')
      } else {
        el.classList.remove('selectedStyle')
      }
    },
    checkAnswer() {
      for (const [key,] of Object.entries(this.questionList[this.cardIndex])) {
        let mistake = false
        for (const [key2,] of Object.entries(this.questionList[this.cardIndex][key][0])) {
          if (this.questionList[this.cardIndex][key][0][key2][0] != this.questionList[this.cardIndex][key][0][key2][1]) {
            mistake = true
            break
          }
        }
        if (mistake == false) {
          this.questionList[this.cardIndex][key][1] = true
          this.correctCounter = this.correctCounter + 1
        }
        this.questionList[this.cardIndex][key][2] = true
      }

      this.answerCorrect['backgroundColor'] = '#82fd6a'
      this.answerWrong['backgroundColor'] = '#ffaa90'
    } 
  },
  computed: {
    questionList() {
        if ( this.docSelected == "17_1.Ценообразование и сметное нормирование.xlsx" ) {
          this.resetColors()
          return this.extimatesQuestions
        } else {
          this.resetColors()
          return this.generalQuestions 
        }
    },
    cardIndex() {
      return this.allIndexes[this.counter]
    },
    allIndexes() {
      let indexesList = []
      for (let i=0; i<this.questionList.length; i++) {
        indexesList.push(i)
      }
      if (this.typeSelected == 'Случайно') {
        return this.shuffleArray(JSON.parse(JSON.stringify(indexesList)))
      } else if (this.typeSelected == 'Экзамен') {
        let shuffledList = this.shuffleArray(JSON.parse(JSON.stringify(indexesList)))
        let list = []
        for (let i=0; i<this.examDeck; i++) {
          list.push(shuffledList[i])
        }
        return list
      } else {
        return JSON.parse(JSON.stringify(indexesList))
      }
    },

  },
  watch: {

  },
  created() {


  },  

}
</script>

